import * as React from 'react';
import { ViewStyle, Text, View, TextStyle } from 'react-native';

import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';
import { DialogButtons, Variants } from '../../kfmMoveView/models/AppContext';
import { Flyout } from 'react-native-windows';
import { commonThemeManager, ICommonTheme } from '../themes/ICommonTheme';
import { CommonButton } from './CommonButton';
import { CommonContext } from '../loader/models/CommonContext';
import { useGlobalStyles } from '../../kfmMoveView/themes/globalStyles';
/**
 * Defines types for modal.
 */
interface IDialogStyles {
  container: ViewStyle;
  modalContent: ViewStyle;
  buttonsContainer: ViewStyle;
  headerText: TextStyle;
}

/**
 * Creates Dialog styles.
 */
const useDialogStyles: ThemedStylesHook<IDialogStyles> = createThemedStylesHook<ICommonTheme, IDialogStyles>(
  commonThemeManager,
  (theme: ICommonTheme) => {
    return {
      container: {
        width: 500,
        minHeight: 200,
        backgroundColor: theme.dialog.backgroundColor,
        borderRadius: 7,
        alignSelf: 'center',
        display: 'flex',
        borderWidth: 1,
        borderColor: theme.dialog.dialogBorderColor
      },
      modalContent: {
        flex: 1,
        padding: 24
      },
      buttonsContainer: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-evenly',
        backgroundColor: theme.dialog.buttonBackgroundColor,
        flex: 0.7,
        alignItems: 'center'
      },
      headerText: {
        marginBottom: 12
      }
    };
  }
);

export interface IDialogProps {
  /**
   * Button with primary styles applied
   */
  primary: DialogButtons;
  /**
   * Dialog header as a string
   */
  header: string;
  /**
   * React Node content for the body of the dialog
   */
  body: React.ReactNode;
  /**
   * Button texts
   */
  buttonOneText: string;
  buttonTwoText: string;
  /**
   * Callback method to submit button clicks
   */
  buttonOneClick: () => void;
  buttonTwoClick: () => void;
  /**
   * Hide/Show the Confirmation Dialog
   */
  isOpen: boolean;

  /**
   * Callback when the dialog is dismissed
   */
  onDismiss: () => void;

  /**
   * Horizontal and vertical offset values for Flyout
   * Both are optional
   */
  verticalOffset?: number;
  horizontalOffset?: number;
}

/**
 * Creates Confirmation Dialog for
 * @param {Interface} IDialogProps - The button name and onPress props of a button.
 */

export const Dialog = (props: IDialogProps): JSX.Element => {
  const {
    buttonOneClick,
    buttonTwoClick,
    buttonOneText,
    buttonTwoText,
    header,
    isOpen,
    body,
    primary,
    onDismiss,
    verticalOffset = 300,
    horizontalOffset = 300
  } = props;
  const commonContext = React.useContext(CommonContext);
  const primaryButtonRef = React.createRef<View>();
  const styles = useDialogStyles();
  const globalStyles = useGlobalStyles();

  const { metadata } = commonContext;
  const isRTL = metadata?.account?.isRTL || false;
  const buttonOneTestID = primary === DialogButtons.First ? 'dialogPrimaryButton' : 'dialogSecondaryButton';
  const buttonTwoTestID = primary === DialogButtons.First ? 'dialogSecondaryButton' : 'dialogPrimaryButton';

  // The 21H1 Windows UI Automation framework is unable to see the invisible Popup window that encapuslates the
  // Flyout control, even though the Flyout is visible on screen. In order to interact with the Flyout dialog,
  // we must force the primary dialog button to be focused so that the BVT test code can retrieve it by
  // reading the static AutomationElement.FocusedElement property. The rest of the Flyout content is accessed
  // by getting the parent of the primary dialog button AutomationElement.
  React.useEffect(() => {
    if (__DEV__ && metadata?.account?.isBVTRunning && isOpen) {
      primaryButtonRef.current?.focus();
    }
  }, [isOpen, primaryButtonRef.current, metadata?.account?.isBVTRunning]);

  return (
    <Flyout
      placement='top'
      verticalOffset={verticalOffset}
      horizontalOffset={horizontalOffset}
      isOverlayEnabled
      isLightDismissEnabled={false}
      isOpen={isOpen}
      testID='flyout'
      onDismiss={onDismiss}
    >
      <View style={[styles.container, { direction: isRTL ? 'rtl' : 'ltr' }]}>
        <View style={styles.modalContent}>
          <Text
            accessibilityRole='header'
            accessibilityLabel={header}
            style={[globalStyles.title, styles.headerText]}
          >
            {header}
          </Text>
          <>{body}</>
        </View>
        <View style={styles.buttonsContainer}>
          <CommonButton
            width={239}
            buttonName={buttonOneText}
            onPress={buttonOneClick}
            variant={primary === DialogButtons.First ? Variants.Primary : Variants.Default}
            testID={buttonOneTestID}
            innerRef={primary === DialogButtons.First ? primaryButtonRef : undefined}
          />
          <CommonButton
            width={230}
            buttonName={buttonTwoText}
            onPress={buttonTwoClick}
            variant={primary === DialogButtons.Second ? Variants.Primary : Variants.Default}
            testID={buttonTwoTestID}
            innerRef={primary === DialogButtons.Second ? primaryButtonRef : undefined}
          />
        </View>
      </View>
    </Flyout>
  );
};

/**
 * Helper handler for the dismiss callback
 * Prevents dismissal of the dialog if it is dismissed by losing focus.
 *
 * This helper handler will reopen the flyout if it was dismissed because it lost focus.
 * A timeout is needed to reopen the flyout because the initial dismiss needs to be processed first.
 *
 * A component based on XAML ContentDialog would be truly modal and not require this handler.
 * Alerts are currently based on ContentDialog, but they suffer from a lack of customizability and
 * do not support multi-window scenarios.
 */
export const dialogDismissHandler = (
  openStateChanger: (isOpen: boolean) => void,
  isUserClosed: boolean
): void => {
  openStateChanger(false);
  if (!isUserClosed) {
    setTimeout(() => {
      openStateChanger(true);
    }, 10);
  }
};
