import * as React from 'react';
import { ViewStyle, View } from 'react-native';
import { Picker } from '@react-native-picker/picker';
import { commonThemeManager, ICommonTheme, useCommonStyles } from '../themes/ICommonTheme';
import { CommonContext } from '../loader/models/CommonContext';
import { Hover } from './Hover';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';

// Define props for dropdown
interface IDropdownProps {
  /**
   * Values of the dropdown items: First value of tuple is the in internal value, second value is
   * the localized string name
   */
  itemValues: Array<[string, string]>;
  /**
   * String id of selected item
   */
  selectedItem: string;
  /**
   * Callback function passed to onValueChange handler.
   */
  onClick: (props: string) => void;
  /**
   * Custom accessibiility label for this dropdown (Optional)
   */
  accessibilityLabel?: string;
}

// Defines types for Dropdown styles.
interface IDropdownStyles {
  dropdownView: ViewStyle;
  dropdownHover: ViewStyle;
}

const DROPDOWN_HEIGHT = 30;
const DROPDOWN_WIDTH = 120;

// Creates Dropdown styles.
const useDropdownStyles: ThemedStylesHook<IDropdownStyles> = createThemedStylesHook<
  ICommonTheme,
  IDropdownStyles
>(commonThemeManager, (theme: ICommonTheme) => {
  return {
    dropdownView: {
      backgroundColor: theme.dropdown.dropdownBackgroundColor
    },
    dropdownHover: {
      backgroundColor: theme.dropdown.dropdownBackgroundColorHover
    }
  };
});

/**
 * Creates dropdown box
 * @param {Interface} IDropdownProps - The items, selectedItem, and onClick
 */
export function Dropdown(props: IDropdownProps): JSX.Element {
  const { selectedItem, itemValues, onClick, accessibilityLabel } = props;
  const commonStyles = useCommonStyles();
  const styles = useDropdownStyles();
  const [selected, setSelected] = React.useState(selectedItem);
  const commonContext = React.useContext(CommonContext);

  const itemArray = itemValues.map(([internalName, localizedName], key) => {
    return <Picker.Item label={localizedName} value={internalName} key={key} />;
  });

  const onValueChange = (itemValue: string): void => {
    setSelected(itemValue);
    onClick(itemValue);
  };
  // NOTE: Using <Hover> because the <Picker> component handes its own pressed state, so using
  // CommonPressable as a wrapper doesn't make sense / adds too much unnecessary complexity.
  return (
    <Hover>
      {(isHovering: boolean) => {
        return (
          <View style={[styles.dropdownView, isHovering ? styles.dropdownHover : undefined]}>
            <Picker
              testID={'dropdownSettingPicker'}
              selectedValue={selected}
              onValueChange={onValueChange}
              itemStyle={[
                commonStyles.bodyText,
                {
                  minWidth: DROPDOWN_WIDTH * commonContext.textScale,
                  height: DROPDOWN_HEIGHT * commonContext.textScale
                }
              ]}
              // Note: This accessibility label is currently not being read by narrator.
              // Bug link: https://onedrive.visualstudio.com/DefaultCollection/Sync/_workitems/edit/1409046
              // Upstream bug link: https://github.com/react-native-picker/picker/issues/310
              accessibilityLabel={accessibilityLabel}
            >
              {itemArray}
            </Picker>
          </View>
        );
      }}
    </Hover>
  );
}
