import React from 'react';
import { StringIDWin } from '../../resources/resources';

export type Metadata = IMetadata | null;

export enum QoSTelemetryResultType {
  Success = 'Success',
  Failure = 'Failure',
  UnexpectedFailure = 'UnexpectedFailure'
}

export interface IQoSTelemetryData {
  apiName: string;
  resultType: QoSTelemetryResultType;
  resultCode?: number;
  httpCode?: number;
  duration?: number;
  error?: string;
  validationError?: string;
  correlationVector?: string;
  value?: string;
}

/**
 * CommonContext stores data relevant to all apps
 */
export interface ICommonContextData {
  metadata: Metadata;

  /**
   * Hook to obtain a localized string from a string ID
   */
  useString: (id: StringIDWin) => string;

  /**
   * Flag indicating that the common context's and the child app's data have been loaded
   */
  isAppFullyLoaded: boolean;

  failedFetch: boolean;
  textScale: number;
  /**
   * Callback for the child app to indicate that its data has been loaded or fetch failed.
   */
  onChildDataLoaded: (failedFetch: boolean) => void;

  /**
   * Allows caller to invoke the native action handler (ActionManager).
   */
  onAction: (action: string) => void;

  /**
   * Allows caller to log a message through the native logging API.
   */
  onLog: (message: string) => void;

  /**
   * Allows caller to record a QoS Telemetry Point through the native logging API.
   */
  onRecordQoSTelemetry: (data: IQoSTelemetryData) => void;
}

export const defaultCommonContextData: ICommonContextData = {
  metadata: null,
  useString: (id: StringIDWin): string => {
    return '';
  },
  isAppFullyLoaded: false,
  failedFetch: false,
  textScale: 1,
  onChildDataLoaded: (): void => {},
  onAction: (): void => {},
  onLog: (): void => {},
  onRecordQoSTelemetry: (): void => {}
};

export const CommonContext: React.Context<ICommonContextData> =
  React.createContext<ICommonContextData>(defaultCommonContextData);

/**
 * MetadataContext stores user and app metadata
 * so that RN views can render based on its contents
 */

interface IAccountMetadata {
  isRTL: boolean;
  isODB: boolean;
  isSignedIn: boolean;
  displayName: string;
  email: string;
  profilePictureLink: string;
  isDebugBuild: boolean;
  isBVTRunning: boolean;
}

/**
 * Enum matching the QuotaStateUX C++ enum found in QuotaInformation.h
 */
export enum QuotaState {
  Unknown = 0,
  Normal = 1,
  Nearing = 2,
  Critical = 3,
  Full = 4,
  OverLimit = 5
}

interface IQuotaMetadata {
  quotaState: QuotaState;
  storageUsedInBytes: number;
  storageTotalInBytes: number;
}

interface IWindowsMetadata {
  textScaleFactor: number;
}

export interface IMetadata {
  account: IAccountMetadata;
  quota: IQuotaMetadata;
  windows: IWindowsMetadata;
}
