import React from 'react';
import { View, ActivityIndicator, ViewStyle, Text } from 'react-native';
import { TextStyle } from 'react-native-windows';
import { AppContext, ILinkState, IMountedFolder } from '../models/AppContext';
import { CommonButton } from '../../common/components/CommonButton';
import { GenericSettingWrapper } from '../components/GenericSettingWrapper';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';
import { Dialog, dialogDismissHandler } from '../../common/components/Dialog';
import { DialogButtons } from '../../kfmMoveView/models/AppContext';
import { useCommonStyles } from '../../common/themes/ICommonTheme';

// additional styling for network settings
interface IMountedFolderViewStyles {
  mountedFolderButtonsView: ViewStyle;
  stopSyncButtonView: ViewStyle;
  bodyText: TextStyle;
}

// About page styling specifications
const useMountedFolderViewStyles: ThemedStylesHook<IMountedFolderViewStyles> = createThemedStylesHook<
  ITheme,
  IMountedFolderViewStyles
>(themeManager, (theme: ITheme) => {
  return {
    mountedFolderButtonsView: {
      flexDirection: 'row',
      flexWrap: 'nowrap'
    },
    stopSyncButtonView: {
      marginLeft: 15
    },
    bodyText: {
      marginTop: '2%',
      fontSize: 12,
      color: theme.bodyTextColor
    }
  };
});

enum StopSyncButtonState {
  HIDDEN,
  STOP,
  UNLINK
}

interface IMountedFolderViewProps extends IMountedFolder {
  stopSyncButtonState: StopSyncButtonState;
  unlinkFolderActionParameter: string;
  index: number;
}

const MountedFolderView = (props: IMountedFolderViewProps): JSX.Element => {
  const {
    scopeId,
    libraryType,
    folderName,
    folderDescription,
    stopSyncButtonState,
    stopSyncConfirmHeader,
    unlinkConfirmBody,
    stopSyncConfirmBody,
    unlinkFolderActionParameter,
    index
  } = props;
  const styles = useMountedFolderViewStyles();
  const commonStyles = useCommonStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);

  const [isDialogOpen, setIsDialogOpen] = React.useState<boolean>(false);
  const [isDialogUserClosed, setIsDialogUserClosed] = React.useState<boolean>(false);

  const [stopScopeId, setStopScopeId] = React.useState<string>('');

  const onPressChooseFoldersButton = (): void => {
    appContext.onChooseFolders(scopeId, libraryType);
  };

  // Only render the stop-sync button if it is not hidden.
  let stopSyncButton = null;
  if (stopSyncButtonState !== StopSyncButtonState.HIDDEN) {
    const isUnlinkConfirm = stopSyncButtonState === StopSyncButtonState.UNLINK;
    const stopSyncConfirmBodyString = isUnlinkConfirm ? unlinkConfirmBody : stopSyncConfirmBody;
    const stopSyncConfirmButtonText = isUnlinkConfirm
      ? useString(StringIDWin.IDS_SETTINGS_UNLINK_CONFIRM_DIALOG_OK)
      : useString(StringIDWin.IDS_SETTINGS_STOP_SYNC_CONFIRM_DIALOG_TEAMSITE_OK);
    const onPressStopSyncButton = (): void => {
      setStopScopeId(scopeId);
      setIsDialogUserClosed(false);
      setIsDialogOpen(true);
    };

    const userCloseDialog = (): void => {
      setIsDialogUserClosed(true);
      setIsDialogOpen(false);
    };

    const handleAlertConfirmation = (): void => {
      if (isUnlinkConfirm) {
        onAction(unlinkFolderActionParameter);
      } else {
        appContext.onStopSyncingMountedFolder(scopeId);
      }
      userCloseDialog();
    };

    stopSyncButton = (
      <View style={styles.stopSyncButtonView}>
        <CommonButton
          buttonName={useString(StringIDWin.IDS_SETTINGS_STOP_SYNC_CONFIRM_DIALOG_TEAMSITE_OK)}
          onPress={onPressStopSyncButton}
          testID={'stopSyncButton' + index}
        />
        {/**Only render the dialog if the current scopeId is same as the one clicked on
         * This prevents multiple renders/additions of Dialog to the tree since
         * we are in a map
         */}
        {stopScopeId === scopeId ? (
          <Dialog
            buttonOneText={stopSyncConfirmButtonText}
            buttonOneClick={handleAlertConfirmation}
            primary={DialogButtons.First}
            buttonTwoText={useString(StringIDWin.IDS_OPTIN_BTN_CANCEL)}
            buttonTwoClick={userCloseDialog}
            header={stopSyncConfirmHeader}
            body={<Text style={commonStyles.bodyText}>{stopSyncConfirmBodyString}</Text>}
            isOpen={isDialogOpen}
            horizontalOffset={450}
            onDismiss={() => dialogDismissHandler(setIsDialogOpen, isDialogUserClosed)}
          />
        ) : null}
      </View>
    );
  }

  return (
    <GenericSettingWrapper
      name={folderName}
      description={folderDescription}
      labelTestID={'mountedFolderName' + index}
    >
      <View style={styles.mountedFolderButtonsView}>
        <CommonButton
          buttonName={useString(StringIDWin.IDS_IMPORT_SCREENSHOT_SYNC_BTN)}
          onPress={onPressChooseFoldersButton}
          testID={'chooseFoldersButton' + index}
        />
        {stopSyncButton}
      </View>
    </GenericSettingWrapper>
  );
};

const MountedFolderNotSyncingView = (props: IMountedFolder): JSX.Element => {
  const styles = useMountedFolderViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const appContext = React.useContext(AppContext);
  // Assume the non-syncing mounted folder view is always at index 0.
  const index = 0;

  return (
    <GenericSettingWrapper
      name={props.folderName}
      description={useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_NOT_SYNCING)}
      labelTestID={'mountedFolderName' + index}
    >
      <View style={styles.mountedFolderButtonsView}>
        <CommonButton
          buttonName={useString(StringIDWin.IDS_FRE_START_SYNC_BUTTON)}
          onPress={() => {
            appContext.onStartSyncingDefaultMountedFolder();
          }}
          testID={'startSyncButton' + index}
        />
      </View>
    </GenericSettingWrapper>
  );
};

export const MountedFolderList = (): JSX.Element | null => {
  const commonContext = React.useContext(CommonContext);
  const { metadata, useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);

  if (metadata && !metadata.account.isSignedIn) {
    // Simply return null if the user is not signed-in.
    return null;
  } else if (appContext.resourceStrings && appContext.mountedFolderState) {
    const mountedFolderState = appContext.mountedFolderState;
    const mountedFolderInfoList = mountedFolderState.mountedFolderList;
    if (mountedFolderInfoList.length > 0) {
      // If the first Mounted Folder scopeId is empty, this indicates that it is the
      // non-syncing/placeholder mounted folder entry and not a real one.
      // It contains a single button that allows the user to start syncing the default mounted folder.
      const hasNotSyncingView = mountedFolderInfoList[0].scopeId.length === 0;

      // Set the stop-sync button state based on the number of mounted folder entries.
      let stopSyncButtonState = StopSyncButtonState.STOP;
      if (mountedFolderInfoList.length === 1) {
        stopSyncButtonState = StopSyncButtonState.HIDDEN;
      } else if (mountedFolderInfoList.length === 2 && hasNotSyncingView) {
        stopSyncButtonState = StopSyncButtonState.UNLINK;
      }

      // Map each mounted folder entry to its appropriate view.
      const mountedFolderViews = mountedFolderInfoList.map((mountedFolder: IMountedFolder, index: number) => {
        if (hasNotSyncingView && index === 0) {
          return <MountedFolderNotSyncingView key={mountedFolder.scopeId} {...mountedFolder} />;
        } else {
          return (
            <MountedFolderView
              key={mountedFolder.scopeId}
              stopSyncButtonState={stopSyncButtonState}
              unlinkFolderActionParameter={mountedFolderState.unlinkFolderActionParameter}
              {...mountedFolder}
              index={index}
            />
          );
        }
      });

      return <View>{mountedFolderViews}</View>;
    } else {
      // Simply return null if there are no mounted folders to render.
      return null;
    }
  } else if (appContext.settings && appContext.settings!.chooseFoldersLink) {
    // If there are no mounted folders to render, display a simple 'Choose folders' button by default if it exists.
    return (
      <GenericSettingWrapper
        name={useString(StringIDWin.IDS_IMPORT_SCREENSHOT_SYNC_BTN)}
        description={useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_CHOOSE_FOLDERS)}
        labelTestID={'defaultChooseFoldersLabel'}
      >
        <CommonButton
          buttonName={useString(StringIDWin.IDS_IMPORT_SCREENSHOT_SYNC_BTN)}
          onPress={() => {
            onAction((appContext.settings!.chooseFoldersLink as ILinkState).actionParameter);
          }}
          accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_CHOOSE_FOLDERS_LABEL)}
          testID='defaultChooseFoldersButton'
        />
      </GenericSettingWrapper>
    );
  } else {
    /**
     * loading view if mountedFolderState or chooseFolders setting is null/undefined
     */
    return <ActivityIndicator size='large' />;
  }
};
