import * as React from 'react';
import { View, Text, Switch, ViewStyle, TextStyle } from 'react-native';
import { commonThemeManager, ICommonTheme, useCommonStyles } from '../themes/ICommonTheme';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';
import { createKeyDownActivation } from './accessibilityUtils';

// Defines types for Toggle styles.
interface IToggleStyles {
  container: ViewStyle;
  switch: ViewStyle;
  toggleView: ViewStyle;
  textView: TextStyle;
}

/**
 * Creates Toggle styles.
 *
 * NOTE: We aren't customizing colors here because the Switch component doesn't handle color customization well.
 * It can cause crashing and odd behavior when hovering/pressing the Switch.
 */
const useToggleStyles: ThemedStylesHook<IToggleStyles> = createThemedStylesHook<ICommonTheme, IToggleStyles>(
  commonThemeManager,
  (theme: ICommonTheme) => {
    return {
      container: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center'
      },
      switch: {
        marginBottom: 9
      },
      toggleView: {
        marginLeft: 8,
        /**
         * Negative margin here moves the toggle to the right, to make up for the empty space on the
         * right of the Switch component that I can't seem to get rid of.
         */
        marginRight: -11
      },
      textView: {
        maxWidth: 100
      }
    };
  }
);

/**
 * Defines props of toggle setting.
 *
 * NOTE: This component will likely need the wrapping component to manage its checked state.
 */
export interface IToggleProps {
  /**
   * Boolean that determines if toggle is on or off.
   */
  checked: boolean;
  /**
   * Boolean that determines if toggle is togglable or not.
   */
  disabled: boolean;
  /**
   * Callback function passed to onValueChange handler.
   */
  onToggle: (val: boolean) => void;
  /**
   * Label for 'On' (Optional)
   */
  toggleOnLabel?: string;
  /**
   * Label for 'Off' (Optional)
   */
  toggleOffLabel?: string;
  /**
   * Accessibility label for the toggle (Optional)
   */
  accessibilityLabel?: string;
  /**
   * Accessibiility hint for the toggle (Optional)
   */
  accessibilityHint?: string;
  /**
   * Custon tab index for the toggle (Optional)
   */
  tabIndex?: number;
  /**
   * Automation Test ID
   */
  testID?: string;
}

/**
 * Creates a custom styled Switch component.
 * @param {Interface} IToggleProps - The checked, disabled, and onToggle props of a toggle.
 */
export function ToggleSetting(props: IToggleProps): JSX.Element {
  const {
    checked,
    onToggle,
    toggleOnLabel,
    toggleOffLabel,
    disabled,
    accessibilityLabel,
    accessibilityHint,
    tabIndex,
    testID
  } = props;

  const toggleSwitch = (val: boolean): void => {
    onToggle(val);
  };
  const commonStyles = useCommonStyles();
  const styles = useToggleStyles();

  return (
    <View style={styles.container}>
      <Text style={[commonStyles.bodyText, styles.textView]}>{checked ? toggleOnLabel : toggleOffLabel}</Text>
      <View style={styles.toggleView}>
        <Switch
          accessibilityRole='switch'
          disabled={disabled}
          value={checked}
          onValueChange={toggleSwitch}
          style={styles.switch}
          accessibilityLabel={accessibilityLabel}
          accessibilityHint={accessibilityHint}
          {...{
            // Using this syntax since onKeyDown is not yet in the type def for Switch.
            // Only implement 'Enter' with this because 'Space' already works, and we don't want
            // to double-trigger the toggle.
            onKeyDown: createKeyDownActivation(toggleSwitch, ['Enter'], [!checked]),
            tabIndex: tabIndex
          }}
          testID={testID}
        />
      </View>
    </View>
  );
}
