import React from 'react';
import { View, ViewStyle, Text } from 'react-native';
import { NativeDataHandler } from '../common/dataHandler/NativeDataHandler';
import { AppContext, ConfirmDialogResult, DialogButtons } from './models/AppContext';
import { useAppContextData } from './models/useAppContextData';
import { KFMDialogDataModel } from './dataHandler/KFMDialogDataModel';
import { KFMDialogMessageHandler } from './dataHandler/KFMDialogMessageHandler';
import { Header } from './components/Header';
import { IKFMTheme, themeManager } from './themes/IKFMTheme';
import { ThemedStylesHook, createThemedStylesHook } from '../common/themes/themes';
import { KFMFolderList } from './components/KFMFolderList';
import { CommonContext } from '../common/loader/models/CommonContext';
import { QuotaDetails } from './components/QuotaDetails';
import { UserActionArea } from './components/UserActionArea';
import { Dialog, dialogDismissHandler } from '../common/components/Dialog';
import { useGlobalStyles } from './themes/globalStyles';

const dataHandler: NativeDataHandler = new NativeDataHandler();
export const dialogDataModel: KFMDialogDataModel = new KFMDialogDataModel();
interface IKFMViewStyles {
  container: ViewStyle;
  kfmContainer: ViewStyle;
  syncContainer: ViewStyle;
  loadingContainer: ViewStyle;
  quotaContainer: ViewStyle;
}

const useKFMViewStyles: ThemedStylesHook<IKFMViewStyles> = createThemedStylesHook<IKFMTheme, IKFMViewStyles>(
  themeManager,
  (theme: IKFMTheme) => {
    return {
      container: {
        flex: 1,
        backgroundColor: theme.frameBackgroundColor
      },
      loadingContainer: {
        backgroundColor: theme.innerFrameBackgroundColor,
        flex: 1,
        justifyContent: 'center'
      },
      kfmContainer: {
        paddingTop: 13.25,
        paddingRight: 24,
        paddingLeft: 32,
        flex: 1,
        backgroundColor: theme.innerFrameBackgroundColor
      },
      syncContainer: {
        flex: 0.16,
        backgroundColor: theme.bottomFrameBackgroundColor,
        justifyContent: 'center',
        alignItems: 'center'
      },
      quotaContainer: {
        flex: 0.32,
        alignItems: 'center'
      }
    };
  }
);

export const KFMMoveView = (): JSX.Element => {
  const commonContext = React.useContext(CommonContext);
  const globalStyles = useGlobalStyles();
  const appContext = useAppContextData(dialogDataModel);
  const styles = useKFMViewStyles();
  const { failedFetch, failedToSendOnReady, userInputDialogState } = appContext;

  const [isDialogOpen, setIsDialogOpen] = React.useState<boolean>(false);
  const [isDialogUserClosed, setIsDialogUserClosed] = React.useState<boolean>(false);

  React.useEffect(() => {
    if (userInputDialogState && userInputDialogState.userDialogPrimaryText) {
      setIsDialogUserClosed(false);
      setIsDialogOpen(true);
    }
  }, [userInputDialogState]);
  const confirmDialogClosed = (result: ConfirmDialogResult): void => {
    appContext.confirmDialogClosed(result);
    setIsDialogUserClosed(true);
    setIsDialogOpen(false);
  };

  if (failedToSendOnReady || failedFetch) {
    // show error if onReady can't be sent or failed to fetch resource strings
    return (
      <View style={styles.loadingContainer}>
        <Text>ERROR</Text>
      </View>
    );
  }

  return (
    <AppContext.Provider value={appContext}>
      <KFMDialogMessageHandler dataHandler={dataHandler} />
      {commonContext.isAppFullyLoaded ? (
        <View style={styles.container}>
          <View style={styles.kfmContainer}>
            <Header
              title={appContext.dialogState?.kfmPageTitle}
              subtext={appContext.dialogState?.kfmPageSubtext}
              linkText={appContext.dialogState?.learnMore}
              link={appContext.dialogState?.learnMoreLink}
            />
            <KFMFolderList />
          </View>
          <View style={styles.quotaContainer}>
            <QuotaDetails />
          </View>
          <View style={styles.syncContainer}>
            <UserActionArea />
          </View>
        </View>
      ) : null}
      {/**Show a confirmation dialog when there is a need for
       * user input. The dialog opens up if there is a sync error or
       * conflict that requires a user input to dismiss, continue/skip or
       * to perform a specified action.
       *
       * The userInputDialogState is reset regardless of the action selected
       * by the user. This enables the state to be updated with a new response
       * from the platform.
       */}
      {userInputDialogState && userInputDialogState.userDialogPrimaryText ? (
        <Dialog
          isOpen={isDialogOpen}
          header={userInputDialogState.userDialogPrimaryText}
          body={
            <Text style={globalStyles.bodyText}>{userInputDialogState.userInputDialogSecondaryText}</Text>
          }
          buttonOneClick={() => confirmDialogClosed(ConfirmDialogResult.PrimaryButton)}
          buttonOneText={userInputDialogState.userInputDialogButtonOneText}
          buttonTwoText={
            userInputDialogState.userInputDialogButtonTwoText ||
            userInputDialogState.userInputDialogLinkButtonText
          }
          primary={DialogButtons.First}
          buttonTwoClick={() => confirmDialogClosed(ConfirmDialogResult.SecondaryButton)}
          onDismiss={() => dialogDismissHandler(setIsDialogOpen, isDialogUserClosed)}
        />
      ) : null}
    </AppContext.Provider>
  );
};
