import { ITheme, themeManager } from './ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { ImageStyle, TextStyle, ViewStyle } from 'react-native';

// Defines types for global styles.
export interface ISettingsStyles {
  subheaderText: TextStyle;
  secondaryText: TextStyle;
  tabTitleBase: TextStyle;
  tabTitleNormal: TextStyle;
  disabledTabTitle: TextStyle;
  selectedTabTitle: TextStyle;
  pressedTabTitle: TextStyle;
  bodyStrong: TextStyle;
  caption: TextStyle;
  title: TextStyle;
  fullWidth: ViewStyle;
  smallLink: TextStyle;
  section: ViewStyle;
  groupPolicyIconWrapper: ViewStyle;
  groupPolicyIconStyle: ImageStyle;
}
// Creates styles used throughout the Sync app.
export const useSettingsStyles: ThemedStylesHook<ISettingsStyles> = createThemedStylesHook<
  ITheme,
  ISettingsStyles
>(themeManager, (theme: ITheme) => {
  // Font sizes
  const bodyText = 14;
  const smallText = 12;
  const largeText = 28;

  // Font weights
  const semibold = '600';
  const medium = '500';
  const normal = '400';

  const defaultFontFamily = 'Segoe UI';

  // NOTE: Do NOT customize line height, as this will make the "Text size" Windows setting not work
  // as well - multiline text may be crammed together.

  return {
    subheaderText: {
      marginBottom: 12,
      color: theme.bodyTextColor,
      fontSize: bodyText,
      fontWeight: medium,
      fontFamily: defaultFontFamily
    },
    secondaryText: {
      fontWeight: normal,
      fontSize: bodyText,
      color: theme.descriptionColor,
      fontFamily: defaultFontFamily
    },
    tabTitleBase: {
      flex: 1,
      justifyContent: 'center',
      textAlign: 'left',
      fontSize: bodyText,
      paddingLeft: 16,
      fontWeight: normal,
      fontFamily: defaultFontFamily
    },
    tabTitleNormal: {
      color: theme.bodyTextColor
    },
    disabledTabTitle: {
      color: theme.button.buttonTextDisabled
    },
    selectedTabTitle: {
      color: theme.selectedTabText
    },
    pressedTabTitle: {
      color: theme.pressedTabText
    },
    bodyStrong: {
      fontWeight: semibold,
      fontSize: bodyText,
      fontFamily: defaultFontFamily
    },
    caption: {
      fontWeight: normal,
      fontSize: smallText,
      color: theme.descriptionColor,
      fontFamily: defaultFontFamily
    },
    title: {
      color: theme.bodyTextColor,
      fontWeight: medium,
      fontSize: largeText,
      fontFamily: defaultFontFamily
    },
    fullWidth: {
      width: '100%'
    },
    smallLink: {
      fontSize: smallText
    },
    section: {
      marginTop: 24
    },
    groupPolicyIconWrapper: {
      flexDirection: 'row',
      alignItems: 'center'
    },
    groupPolicyIconStyle: {
      width: 14,
      height: 14,
      marginRight: 5,
      tintColor: theme.descriptionColor
    }
  };
});
