import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { TextStyle } from 'react-native';
import { IKFMTheme, themeManager } from './IKFMTheme';

// Defines types for global styles.
export interface IGlobalStyles {
  bodyText: TextStyle;
  title: TextStyle;
  infoText: TextStyle;
  boldBodyText: TextStyle;
  buttonText: TextStyle;
  buttonTextColorDefault: TextStyle;
  buttonTextColorPressed: TextStyle;
  buttonTextColorDisabled: TextStyle;
  buttonPrimaryTextColor: TextStyle;
}
// Creates styles used throughout the Sync app.
export const useGlobalStyles: ThemedStylesHook<IGlobalStyles> = createThemedStylesHook<
  IKFMTheme,
  IGlobalStyles
>(themeManager, (theme: IKFMTheme) => {
  // Font sizes
  const bodyText = 14;
  const largeText = 20;
  const smallText = 12;

  // Font weights
  const semibold = '600';
  const normal = '400';
  const defaultFontFamily = 'Segoe UI';

  return {
    bodyText: {
      color: theme.bodyTextColor,
      fontSize: bodyText,
      fontWeight: normal,
      lineHeight: 20,
      fontFamily: defaultFontFamily
    },
    title: {
      color: theme.bodyTextColor,
      fontWeight: semibold,
      fontSize: largeText,
      lineHeight: 20,
      fontFamily: defaultFontFamily
    },
    infoText: {
      color: theme.bodyTextColor,
      fontSize: smallText,
      fontWeight: normal,
      lineHeight: 16,
      fontFamily: defaultFontFamily
    },
    boldBodyText: {
      fontWeight: semibold,
      fontSize: bodyText,
      color: theme.bodyTextColor,
      fontFamily: defaultFontFamily,
      lineHeight: 20
    },
    buttonText: {
      fontWeight: normal,
      fontSize: bodyText,
      color: theme.primaryButton.textColor,
      fontFamily: defaultFontFamily
    },
    buttonTextColorDefault: {
      color: theme.bodyTextColor
    },
    buttonTextColorPressed: {
      color: theme.button.buttonTextPressed
    },
    buttonTextColorDisabled: {
      color: theme.button.buttonTextDisabled
    },
    buttonPrimaryTextColor: {
      color: theme.primaryButton.textColor
    }
  };
});
