import * as React from 'react';
import { View, ViewStyle, Text, Image, ImageStyle } from 'react-native';
import { IToggleProps, ToggleSetting } from '../../common/components/ToggleSetting';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { RadioButton } from '../../common/components/RadioButton';
import { useSettingsStyles } from '../themes/settingsStyles';
import { Expander } from '../../common/components/Expander';
import { CommonTextInput, Location } from '../../common/components/CommonTextInput';
import { Link } from '../../common/components/Link';
import { useCommonStyles } from '../../common/themes/ICommonTheme';
import { StringIDWin } from '../../common/resources/resources';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { useArrowKeys } from '../../common/components/accessibilityUtils';

// Creates types for network styles.
interface INetworkStyles {
  buttonContainer: ViewStyle;
  iconContainer: ViewStyle;
  childrenContainer: ViewStyle;
  outerContainer: ViewStyle;
  outerContainerExpanded: ViewStyle;
  innerContainer: ViewStyle;
  textContainer: ViewStyle;
  expanderContainer: ViewStyle;
  linkStyle: ViewStyle;
}

// Creates Network styles.
const useNetworkSettingStyles: ThemedStylesHook<INetworkStyles> = createThemedStylesHook<
  ITheme,
  INetworkStyles
>(themeManager, (theme: ITheme) => {
  return {
    buttonContainer: {
      maxWidth: 554,
      height: 50,
      paddingLeft: 15,
      paddingRight: 10,
      borderColor: theme.borderColor,
      borderWidth: 1,
      borderTopWidth: 0,
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      borderBottomLeftRadius: 3,
      borderBottomRightRadius: 3
    },
    iconContainer: {
      height: 16,
      width: 16,
      marginLeft: -16 // to offset the padding added when an icon appears
    },
    childrenContainer: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'flex-end'
    },
    outerContainer: {
      maxWidth: 554,
      paddingLeft: 15,
      paddingRight: 22,
      borderColor: theme.borderColor,
      borderRadius: 2,
      borderWidth: 1,
      position: 'relative',
      backgroundColor: theme.innerContainerColor
    },
    outerContainerExpanded: {
      borderBottomLeftRadius: 0,
      borderBottomRightRadius: 0
    },
    innerContainer: {
      display: 'flex',
      minHeight: 62,
      flexDirection: 'row',
      justifyContent: 'space-between',
      alignItems: 'center'
    },
    textContainer: {
      flex: 1,
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      maxWidth: 356,
      alignItems: 'center'
    },
    expanderContainer: {
      marginLeft: 20,
      width: 30,
      maxWidth: 30
    },
    linkStyle: {
      paddingLeft: 5
    }
  };
});

/**
 * Defines props of network setting.
 */
export interface INetworkProps extends IToggleProps {
  /**
   * Name of network setting.
   */
  name: string;
  /**
   * Names of the radio buttons.
   */
  itemNames: Array<string>;
  /**
   * Callback function passed to checkLimit handler.
   */
  onChangeInput: (props: number) => void;
  /**
   * Current value in textInput.
   */
  currentValue: number;
  /**
   * Units for network settings
   */
  units: string;
  /**
   * Function to call when setting is toggled
   */
  onToggle: () => void;
  /**
   * Accessibility label for toggle panel
   */
  radioPanelToggleLabel: string;
  /**
   * Accessibility hint for network setting textbox
   */
  networkSettingTextboxHint: string;
  /**
   * Whether the "Adjust automatically" radio button should be shown
   * If this is true, the props after this need to be defined
   */
  showAutoOption: boolean;
  /**
   * Function to open popup when link is pressed.
   */
  onLinkPress?: () => void;
  /**
   * Current radio button selected.
   */
  selectedItem?: number;
  /**
   * Name of link in first radio button.
   */
  linkText?: string;
  /**
   * Callback function passed to radio button onClick handler.
   */
  onClick?: (number: number) => void;
  /**
   * Accessibility hint for radio button
   */
  radioButtonHint?: string;
  /**
   * Whether the radio button should be visibly disabled
   */
  radioButtonDisabled?: boolean;
  /**
   * If true, show string about setting being controlled by group policy
   */
  showGroupPolicyDescription?: boolean;
}

// Limits for TextInput.
export const DEFAULT_RATE_LIMIT: number = 125;
export const UPPER_RATE_LIMIT: number = 100000;

// Creates network component with radio buttons and icon container.
export function NetworkSetting(props: INetworkProps): JSX.Element {
  const {
    selectedItem,
    onClick: propsOnClick,
    onToggle: propsOnToggle,
    currentValue,
    units,
    linkText,
    onLinkPress,
    onChangeInput,
    showAutoOption,
    itemNames,
    disabled,
    name,
    toggleOnLabel,
    toggleOffLabel,
    checked,
    radioPanelToggleLabel,
    radioButtonDisabled,
    radioButtonHint,
    showGroupPolicyDescription
  } = props;
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const styles = useNetworkSettingStyles();
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();
  const { tabRefs, createHandleArrowKeys } = useArrowKeys(2);

  const [selected, setSelected] = React.useState<number>(selectedItem === undefined ? 1 : selectedItem);
  const [showButtons, setShowButtons] = React.useState(false);
  const [isToggleEnabled, setIsToggleEnabled] = React.useState(checked);

  const onPress = (): void => {
    setShowButtons((previousState) => !previousState);
  };
  const onClick = (selection: number): void => {
    setSelected(selection);
    if (propsOnClick) {
      propsOnClick(selection);
    }
  };
  const onToggle = (): void => {
    setIsToggleEnabled((previousState) => !previousState);
    propsOnToggle();
  };
  // Checks if text input is a valid value
  const checkLimit = (input: string, onChangeValue: React.Dispatch<React.SetStateAction<string>>): void => {
    const parsedInput = Number.parseInt(input);
    /**
     * If number is not a valid number or out of limits,
     * set value to default rate limit.
     */
    if (Number.isNaN(parsedInput) || parsedInput < DEFAULT_RATE_LIMIT) {
      onChangeValue(DEFAULT_RATE_LIMIT.toString());
      onChangeInput(DEFAULT_RATE_LIMIT);
    } else if (parsedInput > UPPER_RATE_LIMIT) {
      onChangeValue(UPPER_RATE_LIMIT.toString());
      onChangeInput(UPPER_RATE_LIMIT);
    } else {
      onChangeValue(parsedInput.toString());
      onChangeInput(parsedInput);
    }
  };

  const limitTextInput = (textboxLabel: string, textboxHint: string, disabled: boolean): JSX.Element => {
    return (
      <>
        <CommonTextInput
          initialValue={currentValue.toString()}
          accessibilityHint={textboxHint}
          accessibilityLabel={textboxLabel}
          disabled={disabled}
          // When user presses out of text input, check if the value is within limits.
          onEndEditing={checkLimit}
          labelText={units}
          labelLocation={Location.After}
          testID={'networkSettingTextInput'}
        />
        {linkText && onLinkPress ? (
          <View style={styles.linkStyle}>
            <Link accessibilityLabel={linkText} inlineLink={true} text={linkText} onPress={onLinkPress} />
          </View>
        ) : null}
      </>
    );
  };

  const displayButtons = (radioButtonDisabled: boolean, radioButtonHint: string): JSX.Element => {
    return (
      <View>
        {showAutoOption ? (
          <View style={styles.buttonContainer}>
            <RadioButton
              id={0}
              name={itemNames[0]}
              onClick={onClick}
              isSelected={selected === 0}
              radioButtonHint={radioButtonHint}
              disabled={disabled || radioButtonDisabled || !isToggleEnabled}
              innerRef={tabRefs[0]}
              onKeyDown={createHandleArrowKeys(0)}
            />
          </View>
        ) : null}

        {/* If we need to show the auto option, we also need to show the radio button next to "Limit to" */}
        <View style={styles.buttonContainer}>
          {showAutoOption ? (
            <RadioButton
              id={1}
              name={itemNames[1]}
              onClick={onClick}
              isSelected={selected === 1}
              radioButtonHint={radioButtonHint}
              disabled={disabled || radioButtonDisabled || !isToggleEnabled}
              innerRef={tabRefs[1]}
              onKeyDown={createHandleArrowKeys(1)}
            >
              {limitTextInput(
                useString(StringIDWin.IDS_SETTINGS_NETWORK_CUSTOM_LIMIT_UPLOAD_LABEL),
                useString(StringIDWin.IDS_KILOBYTE_PER_SECOND_ABBREVIATION),
                radioButtonDisabled || !isToggleEnabled || selected !== 1
              )}
            </RadioButton>
          ) : (
            <>
              <Text>{itemNames[1]}</Text>
              {limitTextInput(
                useString(StringIDWin.IDS_SETTINGS_NETWORK_CUSTOM_LIMIT_DOWNLOAD_LABEL),
                useString(StringIDWin.IDS_KILOBYTE_PER_SECOND_ABBREVIATION),
                radioButtonDisabled || !isToggleEnabled
              )}
            </>
          )}
        </View>
      </View>
    );
  };

  // Toggle should be disabled if inner radio button is disabled, to prevent users from changing settings
  const toggleDisabled = disabled || (radioButtonDisabled !== undefined ? radioButtonDisabled : false);

  return (
    <View>
      <View
        style={showButtons ? [styles.outerContainer, styles.outerContainerExpanded] : styles.outerContainer}
      >
        <View style={styles.innerContainer}>
          <View style={styles.textContainer}>
            <View style={settingsStyles.fullWidth}>
              <Text style={commonStyles.bodyText}>{name}</Text>
              {showGroupPolicyDescription ? (
                <View style={settingsStyles.groupPolicyIconWrapper}>
                  <Image
                    style={settingsStyles.groupPolicyIconStyle as ImageStyle}
                    source={require('../assets/images/aboutIcon.png')}
                  />
                  <Text style={[settingsStyles.caption]}>
                    {useString(StringIDWin.IDS_RNSETTINGS_UNAVAILABLE_DUE_TO_POLICY)}
                  </Text>
                </View>
              ) : null}
            </View>
          </View>
          <View style={styles.childrenContainer}>
            <View>
              <ToggleSetting
                accessibilityLabel={name}
                disabled={toggleDisabled}
                checked={checked}
                onToggle={onToggle}
                toggleOnLabel={toggleOnLabel}
                toggleOffLabel={toggleOffLabel}
              />
            </View>
          </View>
          <View style={styles.expanderContainer}>
            <Expander
              isExpanded={showButtons}
              label={radioPanelToggleLabel}
              onPress={onPress}
              testID={'networkSettingExpander'}
            />
          </View>
        </View>
      </View>
      {/** Displays radio buttons if toggle is enabled and icon is pressed. */}
      <View>
        {showButtons
          ? displayButtons(radioButtonDisabled || disabled || !isToggleEnabled, radioButtonHint || '')
          : null}
      </View>
    </View>
  );
}
