import React from 'react';
import { MESSAGE_EVENT_TYPE, NativeDataHandler } from '../../common/dataHandler/NativeDataHandler';
import { MESSAGE_TYPES } from '../constants';
import { AppContext } from '../models/AppContext';

interface IKFMDialogMessageHandlerProps {
  dataHandler: NativeDataHandler;
}

/**
 * Add event listeners for push messages from C++ using useLayoutEffect.
 * useLayoutEffect, while synchronous, allows us to run some logic before the screen is painted.
 * That is, before useEffect.
 *
 * We attach the listener before the screen is painted because as soon as its painted,
 * we make a request to C++ to send us UI state changes.
 * We need the listeners attached so we can receive and handle those messages accordingly.
 *
 */

export function KFMDialogMessageHandler(props: IKFMDialogMessageHandlerProps): JSX.Element | null {
  const appContext = React.useContext(AppContext);
  React.useLayoutEffect(() => {
    const callback = (serializedResult: string): void => {
      const result = JSON.parse(serializedResult);
      switch (result.messageType) {
        case MESSAGE_TYPES.KFM_SCAN_INITIALIZED:
          appContext.onScanInitialized(result.value);
          break;
        case MESSAGE_TYPES.KFM_STATE_UPDATE:
          appContext.onStateUpdate(result.value);
          break;
        case MESSAGE_TYPES.KFM_SPACED_USED:
          appContext.onSetSpaceUsedInfo(result.value);
          break;
        case MESSAGE_TYPES.KFM_INFO_STATE:
          appContext.onDialogStateInfoReceived(result.value);
          break;
        case MESSAGE_TYPES.USER_INPUT_DIALOG:
          appContext.onUserInputDialogStateReceived(result.value);
          break;
      }
    };

    props.dataHandler.addEventListener(MESSAGE_EVENT_TYPE, callback);

    return () => {
      props.dataHandler.removeEventListener(MESSAGE_EVENT_TYPE);
    };
  }, []);

  return null;
}
