import React from 'react';
import { View, Text, ViewStyle, TextStyle } from 'react-native';
import { CommonButton } from '../../common/components/CommonButton';
import { StringIDWin } from '../../common/resources/resources';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { ITheme, themeManager } from './../themes/ITheme';
interface ISuccessStyles {
  afterSubmitFrame: ViewStyle;
  afterSubmitActionButton: ViewStyle;
  title: TextStyle;
  subheaderText: TextStyle;
}

export const useInProgressViewStyles: ThemedStylesHook<ISuccessStyles> = createThemedStylesHook<
  ITheme,
  ISuccessStyles
>(themeManager, (theme: ITheme) => {
  // Font sizes
  const largeText = 22;

  // Font weights
  const medium = '500';
  const defaultFontFamily = 'Segoe UI';
  return {
    afterSubmitFrame: {
      height: '80%',
      width: '100%'
    },
    afterSubmitActionButton: {
      width: '100%',
      flexDirection: 'row',
      justifyContent: 'flex-end'
    },
    inProgressViewMargin: {
      marginTop: 120
    },
    title: {
      color: theme.bodyTextColor,
      fontWeight: medium,
      fontSize: largeText,
      lineHeight: 36,
      fontFamily: defaultFontFamily,
      paddingBottom: 8
    },
    subheaderText: {
      marginBottom: 8,
      color: theme.bodyTextColor,
      fontSize: 14,
      fontWeight: '400',
      lineHeight: 20,
      marginTop: 0
    }
  };
});
interface ISuccessViewProps {
  closeDialog: () => void;
}

export const SuccessView = (props: ISuccessViewProps): JSX.Element => {
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const { closeDialog } = props;
  const styles = useInProgressViewStyles();

  return (
    <View>
      <View style={styles.afterSubmitFrame}>
        <Text
          style={styles.title}
          accessibilityRole='header'
          accessibilityLabel={useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_SUCCESSFUL_HEADING)}
          testID='reportABugHeaderSuccessHeader'
        >
          {useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_SUCCESSFUL_HEADING)}
        </Text>
        <Text style={styles.subheaderText}>
          {useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_SUCCESSFUL)}
        </Text>
      </View>
      <View style={styles.afterSubmitActionButton}>
        <CommonButton
          onPress={closeDialog}
          buttonName={useString(StringIDWin.IDS_REPORT_A_BUG_CLOSE_BUTTON)}
        />
      </View>
    </View>
  );
};
