import React from 'react';
import { MESSAGE_EVENT_TYPE, NativeDataHandler } from '../../dataHandler/NativeDataHandler';

// RootTagContext module export is not found by TypeScript, but we can still import and use it without error.
// @ts-ignore
import { AppRegistry, RootTagContext } from 'react-native';

interface ICommonMessageHandlerProps {
  dataHandler: NativeDataHandler;
  appKey: string;
}

export function CommonMessageHandler(props: ICommonMessageHandlerProps): JSX.Element | null {
  const rootTag: number = React.useContext(RootTagContext);
  React.useLayoutEffect(() => {
    const callback = (serializedResult: string): void => {
      const result = JSON.parse(serializedResult);
      if (result.messageType === 'unmount' && result.value === props.appKey) {
        AppRegistry.unmountApplicationComponentAtRootTag(rootTag);
      }
    };

    props.dataHandler.addEventListener(MESSAGE_EVENT_TYPE, callback);

    return () => {
      props.dataHandler.removeEventListener(MESSAGE_EVENT_TYPE);
    };
  }, [rootTag]);

  return null;
}
