import React from 'react';
import { View, Text, ActivityIndicator } from 'react-native';
import { AppContext, IToggleState } from '../models/AppContext';
import { useSettingsViewStyles } from '../SettingsView';
import { GenericSettingWrapper } from '../components/GenericSettingWrapper';
import { ToggleAdapter } from '../components/ToggleAdapter';
import { settingIDs } from '../models/SettingIDs';
import { Footer } from '../components/Footer';
import { useSettingsStyles } from '../themes/settingsStyles';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';

// NotificationsView display
export function NotificationsView(): JSX.Element {
  const settingsViewStyles = useSettingsViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const appContext = React.useContext(AppContext);
  const settingsStyles = useSettingsStyles();

  // check for null or undefined Settings
  if (appContext.settings) {
    return (
      <View>
        <Text
          accessibilityRole='header'
          accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_NOTIFICATION_TAB)}
          style={settingsStyles.title}
          testID='notificationsHeader'
        >
          {useString(StringIDWin.IDS_SETTINGS_NOTIFICATION_TAB)}
        </Text>
        <View style={settingsStyles.section}>
          {appContext.settings.pauseAutomatically ? (
            <GenericSettingWrapper
              name={useString(StringIDWin.IDS_RNSETTINGS_AUTO_PAUSE_NOTIFICATION)}
              description={''}
            >
              <ToggleAdapter
                accessibilityLabel={useString(StringIDWin.IDS_RNSETTINGS_AUTO_PAUSE_NOTIFICATION)}
                settingId={settingIDs.pauseAutomaticallyId}
              />
            </GenericSettingWrapper>
          ) : null}
          <GenericSettingWrapper
            name={useString(StringIDWin.IDS_RNSETTINGS_SHARE_EDIT_NOTIFICATION)}
            description={''}
          >
            <ToggleAdapter
              accessibilityLabel={useString(StringIDWin.IDS_RNSETTINGS_SHARE_EDIT_NOTIFICATION)}
              settingId={settingIDs.shareWithMeId}
            />
          </GenericSettingWrapper>
          <GenericSettingWrapper
            name={useString(StringIDWin.IDS_RNSETTINGS_MASS_DELETE_NOTIFICATION)}
            description={''}
            showGroupPolicyDescription={
              (appContext.settings![settingIDs.largeDeletesId] as IToggleState).disabled
            }
          >
            <ToggleAdapter
              accessibilityLabel={useString(StringIDWin.IDS_RNSETTINGS_MASS_DELETE_NOTIFICATION)}
              settingId={settingIDs.largeDeletesId}
            />
          </GenericSettingWrapper>
          {appContext.settings.onThisDay ? (
            <GenericSettingWrapper
              name={useString(StringIDWin.IDS_SETTINGS_ON_THIS_DAY_MEMORIES_NOTIFICATION)}
              description={''}
            >
              <ToggleAdapter
                accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_ON_THIS_DAY_MEMORIES_NOTIFICATION)}
                settingId={settingIDs.onThisDayId}
              />
            </GenericSettingWrapper>
          ) : null}
          {appContext.settings.beforeFilesRemoved ? (
            <GenericSettingWrapper
              name={useString(StringIDWin.IDS_RNSETTINGS_LOCAL_MASS_DELETE_SETTINGS_CHECKBOX)}
              description={''}
              showGroupPolicyDescription={
                (appContext.settings![settingIDs.beforeFilesRemovedId] as IToggleState).disabled
              }
            >
              <ToggleAdapter
                accessibilityLabel={useString(StringIDWin.IDS_RNSETTINGS_LOCAL_MASS_DELETE_SETTINGS_CHECKBOX)}
                settingId={settingIDs.beforeFilesRemovedId}
              />
            </GenericSettingWrapper>
          ) : null}
        </View>
        <Footer />
      </View>
    );
  } else {
    /**
     * loading view if Settings are null or undefined
     * should not occur because checked for in SettingsView
     */
    return (
      <View style={settingsViewStyles.loadingView}>
        <ActivityIndicator size='large' />
      </View>
    );
  }
}
