import { NativeDataHandler } from '../../common/dataHandler/NativeDataHandler';
import { IDataHandlerSendOptions } from '../../common/dataHandler/IDataHandler';
import { ConfirmDialogResult, FolderType } from '../models/AppContext';

export class KFMDialogDataModel {
  private _dataHandler: NativeDataHandler;

  public constructor() {
    this._dataHandler = new NativeDataHandler();
  }

  /**
   * send on ready message.
   */
  public async sendReady(): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/ready',
      requestType: 'POST'
    };
    return this._dataHandler.send(options);
  }

  public async onToggleFolder(folderType: FolderType): Promise<void> {
    const payload: { [key: string]: FolderType } = {
      folderType
    };
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/toggleFolder',
      requestType: 'PATCH',
      postData: payload
    };
    return this._dataHandler.send(options);
  }

  public async onStartMove(): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/startMove',
      requestType: 'POST'
    };
    return this._dataHandler.send(options);
  }

  public async onSkipBackup(): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/skipBackup',
      requestType: 'POST'
    };
    return this._dataHandler.send(options);
  }

  public async onTryAgain(): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/tryAgain',
      requestType: 'POST'
    };
    return this._dataHandler.send(options);
  }

  public async onStopFolderBackup(folderType: FolderType): Promise<void> {
    const payload: { [key: string]: FolderType } = {
      folderType
    };
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/stopBackup',
      requestType: 'POST',
      postData: payload
    };
    return this._dataHandler.send(options);
  }

  public async confirmDialogClosed(result: ConfirmDialogResult): Promise<void> {
    const payload: { [key: string]: ConfirmDialogResult } = {
      result
    };
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/kfmmove/confirmDialogClosed',
      requestType: 'PATCH',
      postData: payload
    };
    return this._dataHandler.send(options);
  }
}
