import React from 'react';
import { IErrorResponse } from '../../common/dataHandler/NativeDataHandler';
import { settingIDs } from '../models/SettingIDs';
import { NativeSettingsDataModel } from '../dataHandler/NativeSettingsDataModel';
import {
  IAppContextData,
  IDropDownState,
  IMountedFolderState,
  INetworkState,
  isIDropDownState,
  isIToggleState,
  IToggleState,
  Auth,
  MountedFolderState,
  ResourceStrings,
  Settings
} from './AppContext';
import { CommonContext } from '../../common/loader/models/CommonContext';

/**
 * Helper functions, make shallow copies of settings object before passing it to setSettings
 * Functions are currently exported only test purposes
 */
export const updateCheckedState = (
  key: string,
  newValue: boolean
): ((currentSettings: Settings) => Settings) => {
  return (currentSettings) => {
    // This copies all the current settings values that we're not modifying
    const settings = { ...currentSettings! };
    const currentValue = settings[key];
    if (currentValue && isIToggleState(currentValue)) {
      currentValue.checked = newValue;
    }
    return settings;
  };
};

export const updateNetworkState = (newValue: INetworkState): ((currentSettings: Settings) => Settings) => {
  return (currentSettings) => {
    // This copies all the current settings values that we're not modifying
    const settings = { ...currentSettings! };
    settings.network = newValue;
    return settings;
  };
};

export const updateDropdownState = (
  key: string,
  newValue: string
): ((currentSettings: Settings) => Settings) => {
  return (currentSettings) => {
    // This copies all the current settings values that we're not modifying
    const settings = { ...currentSettings! };
    const currentValue = settings[key];
    if (currentValue && isIDropDownState(currentValue)) {
      currentValue.selection = newValue;
    }
    return settings;
  };
};

const handleStopSyncingMountedFolder = (
  scopeId: string
): ((currentMountedFolderState: MountedFolderState) => MountedFolderState) => {
  return (currentMountedFolderState): MountedFolderState => {
    // This copies all the current settings values that we're not modifying
    const mountedFolderState = { ...currentMountedFolderState } as IMountedFolderState;
    const currentMountedFolders = mountedFolderState.mountedFolderList;
    const foundIndex = currentMountedFolders.findIndex((folder) => folder.scopeId === scopeId);

    // If trying to stop syncing the primary root folder (i.e. the user's ODB)
    // then clear the scope ID so it displays as a dummy 'Not syncing' view.
    if (scopeId === mountedFolderState.primaryScopeId) {
      currentMountedFolders[foundIndex].scopeId = '';
      mountedFolderState.primaryScopeId = '';
    } else {
      // Otherwise, remove the entry entirely from the context cache.
      currentMountedFolders.splice(foundIndex, 1 /*deleteCount*/);
    }

    mountedFolderState.mountedFolderList = currentMountedFolders;

    return mountedFolderState;
  };
};

/**
 * useAppContextData() is only called once per start-up.
 * pass dataModel object as an argument, since it plays an important role to the hooks.
 */

export const useAppContextData = (dataModel: NativeSettingsDataModel): IAppContextData => {
  const commonContext = React.useContext(CommonContext);
  /**
   * null means data are fetching/loading, boolean failedFetch determines whether any of the fetch functions fails.
   * the implementation is shown inside the SettingsView component's conditional statements
   */
  const [resourceStrings, setResourceStrings] = React.useState<ResourceStrings>(null);
  const [settings, setSettings] = React.useState<Settings>(null);
  const [mountedFolderState, setMountedFolderState] = React.useState<MountedFolderState>(null);
  const [auth, setAuth] = React.useState<Auth>(null);
  const [failedFetch, setFailedFetch] = React.useState<boolean>(false);

  /**
   * All callback functions have temporary variables to store the original values of components.
   * The components are visually updated before the asynchronous patch functions execute.
   * When the patch functions fail, the temporary variables are used to restore components's previous/original values.
   */

  const onToggleChange = async (key: string, value: boolean): Promise<void> => {
    if (settings) {
      const originalCheckedState = (settings[key] as IToggleState).checked;
      const response = await dataModel.patchSetting(key, value, settings);
      if ((response as IErrorResponse).error) {
        console.error(response);
        setSettings(updateCheckedState(key, originalCheckedState));
      } else {
        setSettings(updateCheckedState(key, value));
      }
    }
  };

  const onNetworkChange = async (value: INetworkState): Promise<void> => {
    if (settings) {
      const originalNetworkState = settings.network as INetworkState;
      const response = await dataModel.patchSetting(settingIDs.networkId, value, settings);
      if ((response as IErrorResponse).error) {
        console.error(response);
        setSettings(updateNetworkState(originalNetworkState));
      } else {
        setSettings(updateNetworkState(value));
      }
    }
  };

  const onDropdownChange = async (key: string, value: string): Promise<void> => {
    if (settings) {
      const originalDropdownValue = (settings[key] as IDropDownState).selection;
      const response = await dataModel.patchSetting(key, value, settings);
      if ((response as IErrorResponse).error) {
        console.error(response);
        setSettings(updateDropdownState(key, originalDropdownValue));
      } else {
        setSettings(updateDropdownState(key, value));
      }
    }
  };

  const onCatfoodChange = async (value: string): Promise<void> => {
    const response = await dataModel.patchCatfood(value);
    if ((response as IErrorResponse).error) {
      console.error(response);
    } else {
      // After patchCatfood, we would want to update the preRelaseBuild to the newest UpdateRing
      // Such that UpdateRing properly gets updated with patchCatfood updates
      const result = await dataModel.fetchAllSettings([settingIDs.preReleaseBuildsId]);
      setSettings({ ...settings, ...result });
    }
  };

  const onStopSyncingMountedFolder = async (scopeId: string): Promise<void> => {
    try {
      await dataModel.stopSyncingMountedFolder(scopeId);
      setMountedFolderState(handleStopSyncingMountedFolder(scopeId));
    } catch (error) {
      console.error('Failed to stop syncing mounted folder with scopeId: ' + scopeId);
    }
  };

  const onStartSyncingDefaultMountedFolder = async (): Promise<void> => {
    await dataModel.startSyncingDefaultMountedFolder();
  };

  const onChooseFolders = async (scopeId: string, libraryType: number): Promise<void> => {
    await dataModel.chooseFolders(scopeId, libraryType);
  };

  const onUpdateMountedFolderState = (updatedMountedFolderState: IMountedFolderState): void => {
    setMountedFolderState({
      ...updatedMountedFolderState
    });
  };

  const beginGetMountedFolders = async (): Promise<void> => {
    try {
      await dataModel.beginGetMountedFolders();
    } catch (error) {
      // Errors from trying to call BeginGetMountedFolders are currently known to only occur if
      // Sync Engine API calls are not currently allowed or available. The main reason this can
      // happen is if the user is paused or in non-logged-in state. Simply log the error silently
      // for now.
      console.error(error);
    }
  };

  //useEffect() trigger data model fetch methods to get data to settings and resource context
  React.useEffect(() => {
    const fetchAllResourceStrings = async (): Promise<void> => {
      try {
        const resources = await dataModel.fetchAllResourceStrings();
        setResourceStrings(resources);
      } catch (error) {
        console.error('Failed to fetch resource strings');
        setFailedFetch(true);
      }
    };
    const fetchAllSettings = async (): Promise<void> => {
      try {
        const settings = await dataModel.fetchAllSettings();
        setSettings(settings);
      } catch (error) {
        console.error('Failed to fetch settings');
        setFailedFetch(true);
      }
    };
    // Disabling this lint rule here because we shouldn't add `await` in front of a promise in a `useEffect` hook.
    /* eslint-disable @typescript-eslint/no-floating-promises */
    fetchAllResourceStrings();
    fetchAllSettings();
    /* eslint-enable @typescript-eslint/no-floating-promises */
  }, []);

  React.useEffect(() => {
    let isMounted = true;

    const fetchProfilePicture = async (scopeStr: string): Promise<Auth> => {
      try {
        const fetchedToken = await dataModel.fetchProfilePicture(scopeStr);
        return fetchedToken;
      } catch (error) {
        console.error('Failed to fetch auth');
        return {
          authToken: 'Token not found',
          profilePicture: ''
        };
      }
    };

    if (commonContext.metadata && commonContext.metadata.account && !auth) {
      const scopeStr = commonContext.metadata.account.isODB
        ? 'https://graph.microsoft.com'
        : 'offline_access+files.readwrite+contacts.readwrite';
      // Disabling this lint rule here because we shouldn't add `await` in front of a promise in a
      // `useEffect` hook.
      /* eslint-disable @typescript-eslint/no-floating-promises */
      fetchProfilePicture(scopeStr).then((authValue) => {
        if (isMounted) {
          // Only set state if the component is still mounted. The auth call can still be running
          // if the user closes the Settings window too quickly
          setAuth(authValue);
        }
      });
    }
    return () => {
      isMounted = false;
    };
  }, [commonContext.metadata?.account.isODB]); // Effect callback is only run whenever metadata is updated.

  // Let the common context know when we are done or if we failed
  React.useEffect(() => {
    if ((!commonContext.isAppFullyLoaded && resourceStrings !== null && settings !== null) || failedFetch) {
      commonContext.onChildDataLoaded(failedFetch);
    }
  }, [resourceStrings, settings, failedFetch]);

  return {
    resourceStrings,
    settings,
    mountedFolderState,
    auth,
    failedFetch,
    onToggleChange,
    onNetworkChange,
    onDropdownChange,
    onCatfoodChange,
    onStopSyncingMountedFolder,
    onStartSyncingDefaultMountedFolder,
    onChooseFolders,
    onUpdateMountedFolderState,
    beginGetMountedFolders
  };
};
