import * as React from 'react';
import { AccessibilityRole, AccessibilityState, Pressable, View } from 'react-native';
import { IKeyboardEvent } from 'react-native-windows';

interface IPressableProps {
  /**
   * The following props are passed to the children of this component
   */
  children: (
    /**
     * Whether the React node is being pressed
     */
    isPressed: boolean,
    /**
     * Whether the React node is being hovered over
     */
    isHovering: boolean
  ) => React.ReactNode;
  /**
   * Callback function passed to the onPress handler in this component
   */
  onPress: () => void;
  /**
   * Whether this component is disabled. Defaults to false. (Optional)
   */
  disabled?: boolean;
  /**
   * Accessibility label for this component and all children. (Optional)
   */
  accessibilityLabel?: string;
  /**
   * Accessibility role for this component and all children. (Optional)
   */
  accessibilityRole?: AccessibilityRole;
  /**
   * Accessibility hint for this component and all children. (Optional)
   */
  accessibilityHint?: string;
  /**
   * Accessibility state for this component and all children. (Optional)
   */
  accessibilityState?: AccessibilityState;
  /**
   * Custon tab index for this button (Optional)
   */
  tabIndex?: number;
  /**
   * Test ID for this component (Optional)
   */
  testID?: string;
  /**
   * Ref to use for the top-level View (Optional)
   */
  innerRef?: React.RefObject<View>;
  /**
   * Custom handler for key presses on this component (Optional)
   */
  onKeyDown?: (event: IKeyboardEvent) => void;
}

export function CommonPressable(props: IPressableProps): JSX.Element {
  const {
    onPress: propsOnPress,
    accessibilityLabel,
    accessibilityRole,
    accessibilityHint,
    accessibilityState,
    tabIndex,
    testID,
    children,
    disabled,
    innerRef,
    onKeyDown
  } = props;

  const [isPressed, setIsPressed] = React.useState(false);
  const [isHovering, setIsHovering] = React.useState(false);

  const onPressIn = React.useCallback(() => {
    setIsPressed(true);
  }, []);

  const onPress = React.useCallback(() => {
    propsOnPress();
    setIsPressed(false);
  }, [propsOnPress]);

  const onPressOut = React.useCallback(() => {
    setIsPressed(false);
  }, []);

  const onHoverIn = React.useCallback(() => {
    setIsHovering(true);
  }, []);

  const onHoverOut = React.useCallback(() => {
    setIsHovering(false);
  }, []);

  return (
    <Pressable
      onPressIn={disabled ? () => {} : onPressIn}
      onPress={disabled ? () => {} : onPress}
      onPressOut={disabled ? () => {} : onPressOut}
      focusable={!disabled}
      accessibilityLabel={accessibilityLabel}
      accessibilityRole={accessibilityRole}
      accessibilityHint={accessibilityHint}
      accessibilityState={accessibilityState}
      testID={testID}
      ref={innerRef}
      {...{
        // Using this syntax since these props are not yet in the type def for View
        onKeyDown: (event: IKeyboardEvent) => {
          if (onKeyDown) {
            onKeyDown(event);
          }
        },
        tabIndex: tabIndex,
        onHoverIn: onHoverIn,
        onHoverOut: onHoverOut
      }}
    >
      {children(isPressed, isHovering)}
    </Pressable>
  );
}
