import React from 'react';
import { NativeDataHandler } from '../../common/dataHandler/NativeDataHandler';
import { AppContext } from '../models/AppContext';

interface ISettingsMessageHandlerProps {
  dataHandler: NativeDataHandler;
}

export function SettingsMessageHandler(props: ISettingsMessageHandlerProps): JSX.Element | null {
  const appContext = React.useContext(AppContext);
  React.useLayoutEffect(() => {
    const callback = (serializedResult: string): void => {
      const result = JSON.parse(serializedResult);
      if (result.messageType === 'mountedFolderStateUpdate') {
        appContext.onUpdateMountedFolderState(result.value);
      }
    };

    props.dataHandler.addEventListener('message', callback);

    return () => {
      props.dataHandler.removeEventListener('message');
    };
  }, []);

  React.useEffect(() => {
    appContext.beginGetMountedFolders();
  }, []);

  return null;
}
