import React from 'react';
import { ToggleSetting } from '../../common/components/ToggleSetting';
import { AppContext, IToggleState } from '../models/AppContext';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';

interface IToggleAdapterProps {
  /**
   * settingID to retrieve data from the Data Model
   */
  settingId: string;
  /**
   * Custom function to run instead of the default when the user toggles the component (Optional)
   */
  customOnToggle?: () => void;
  /**
   * Accessibility label for the toggle (Optional)
   */
  accessibilityLabel?: string;
  /**
   * Accessibiility hint for the toggle (Optional)
   */
  accessibilityHint?: string;
  /**
   * Custon tab index for the toggle (Optional)
   */
  tabIndex?: number;
  /**
   * Additional logic to determine if the toggle should be disabled. This logic
   * will be OR'd with the setting's "disabled" value. Not requred in most cases.
   *
   * Example usage: Pass in a setting's on/off state that this setting depends on,
   * so that when that setting is off, this one is disabled.
   */
  disabled?: boolean;
}

/**
 * used to return ToggleSetting data given just the settingId
 * expecting appContext.settings[props.settingId] to be fully defined
 */
export const ToggleAdapter = (props: IToggleAdapterProps): JSX.Element => {
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const appContext = React.useContext(AppContext);
  const { settingId, customOnToggle, accessibilityLabel, accessibilityHint, tabIndex, disabled } = props;

  const toggleState = appContext.settings![settingId] as IToggleState;

  /**
   * Attach a React state to the toggle state to optimistically accept value changes. The toggle
   * handler sets the toggle state to the new value optimistically, while waiting for the model to
   * compute the next state. A useEffect hook is also used to update the React state when the
   * setting value changes.
   */
  const [isToggleChecked, setIsToggleChecked] = React.useState(toggleState.checked);
  React.useEffect(() => {
    setIsToggleChecked(toggleState.checked);
  }, [toggleState.checked]);

  return (
    <ToggleSetting
      checked={isToggleChecked}
      disabled={disabled || toggleState.disabled}
      onToggle={
        customOnToggle
          ? customOnToggle
          : (newValue: boolean) => {
              setIsToggleChecked(newValue);
              appContext.onToggleChange(settingId, newValue);
            }
      }
      toggleOnLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_ON)}
      toggleOffLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_OFF)}
      accessibilityLabel={accessibilityLabel}
      accessibilityHint={accessibilityHint}
      tabIndex={tabIndex}
      testID={settingId}
    />
  );
};
