import React from 'react';
import { StringIDWin } from '../../resources/resources';
import { IStringResponse, CommonContextDataModel } from '../dataHandler/CommonContextDataModel';
import { ICommonContextData, IQoSTelemetryData, Metadata, QoSTelemetryResultType } from './CommonContext';

type ILocalizedStringTableWindows = {
  [key in StringIDWin]?: string;
};

const loadResultQoSApiName = 'UXRNViewDataLoaded';

export const useCommonContextData = (
  dataModel: CommonContextDataModel,
  appKey: string,
  stringsToLoad: StringIDWin[]
): ICommonContextData => {
  const [metadata, setMetadata] = React.useState<Metadata>(null);
  const [stringsTable, setStringsTable] = React.useState<ILocalizedStringTableWindows | null>(null);
  const [isAppFullyLoaded, setIsAppFullyLoaded] = React.useState<boolean>(false);
  const [isChildDataLoaded, setIsChildDataLoaded] = React.useState<boolean>(false);
  const [failedFetch, setFailedFetch] = React.useState<boolean>(false);
  const [loadStartTime] = React.useState<number>(Date.now());
  const [textScale, setTextScale] = React.useState<number>(1);

  // Child apps call this callback when their data has been loaded
  const onChildDataLoaded = (failedFetch: boolean): void => {
    if (failedFetch) {
      setFailedFetch(true);
    }
    setIsChildDataLoaded(true);
  };

  React.useEffect(() => {
    const fetchAllMetadata = async (): Promise<void> => {
      try {
        const metadata = await dataModel.fetchAllMetadata();
        setMetadata(metadata);
      } catch (error) {
        console.error(`Failed to fetch metadata: ${error}`);
        setFailedFetch(true);
      }
    };

    /**
     * Sends a list of string IDs and expects back a corresponding list of localized strings
     * IDs and strings are matched by their position in the lists
     */
    const fetchResourceStrings = async (stringsToLoad: StringIDWin[]): Promise<void> => {
      try {
        const stringResponse = await dataModel.fetchResourceStrings(stringsToLoad);
        const table: ILocalizedStringTableWindows = {};
        stringResponse.map((resp: IStringResponse, i: number) => {
          table[resp.id] = resp.str;
        });

        setStringsTable(table);
      } catch (error) {
        console.error('Failed to fetch strings');
        console.error(error);
        setFailedFetch(true);
      }
    };

    // Disabling this lint rule here because we shouldn't add `await` in front of a promise in a `useEffect` hook.
    /* eslint-disable @typescript-eslint/no-floating-promises */
    fetchAllMetadata();
    fetchResourceStrings(stringsToLoad);
  }, []);

  React.useEffect(() => {
    if (!isAppFullyLoaded && isChildDataLoaded && metadata !== null && stringsTable !== null) {
      setIsAppFullyLoaded(true);
    }
  }, [isChildDataLoaded, metadata, stringsTable]);

  React.useEffect(() => {
    if (failedFetch) {
      // If fetch failed, record the result as a QoS Telemetry point.
      const duration: number = Date.now() - loadStartTime;
      const qosData: IQoSTelemetryData = {
        apiName: loadResultQoSApiName,
        resultType: QoSTelemetryResultType.UnexpectedFailure,
        resultCode: -1,
        httpCode: 500,
        duration: duration
      };
      dataModel.recordQoSTelemetry(qosData, appKey);
    } else if (isAppFullyLoaded) {
      // When the app is fully loaded, record the total load duration as a QoS Telemetry point.
      const duration: number = Date.now() - loadStartTime;
      const qosData: IQoSTelemetryData = {
        apiName: loadResultQoSApiName,
        resultType: QoSTelemetryResultType.Success,
        resultCode: 0,
        httpCode: 200,
        duration: duration
      };
      dataModel.recordQoSTelemetry(qosData, appKey);
    }
  }, [failedFetch, isAppFullyLoaded]);

  React.useEffect(() => {
    if (metadata && metadata.windows && metadata.windows.textScaleFactor) {
      setTextScale(metadata.windows.textScaleFactor);
    }
  }, [metadata]);

  const useString = (id: StringIDWin): string => {
    if (stringsTable === null) {
      return '';
    } else {
      return stringsTable[id] ?? '';
    }
  };

  const onAction = async (action: string): Promise<void> => {
    await dataModel.handleAction(action, appKey);
  };

  const onLog = async (message: string): Promise<void> => {
    await dataModel.log(message, appKey);
  };

  const onRecordQoSTelemetry = async (data: IQoSTelemetryData): Promise<void> => {
    await dataModel.recordQoSTelemetry(data, appKey);
  };

  return {
    metadata,
    textScale,
    useString,
    isAppFullyLoaded,
    failedFetch,
    onChildDataLoaded,
    onAction,
    onLog,
    onRecordQoSTelemetry
  };
};
