import * as React from 'react';
import { TextInput, ViewStyle, TextStyle, Text } from 'react-native';
import { ITheme, themeManager } from '../../settingsView/themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';

interface ICommonTextInputStyles {
  textInputStyle: TextStyle;
  textInputDisabled: TextStyle;
  textInputColors: TextStyle;
  textInputDisabledColors: TextStyle;
  textInputFocusedColors: TextStyle;
}

const useCommonTextInputStyles: ThemedStylesHook<ICommonTextInputStyles> = createThemedStylesHook<
  ITheme,
  ICommonTextInputStyles
>(themeManager, (theme: ITheme) => {
  return {
    textInputStyle: {
      marginLeft: 10,
      marginRight: 10
    },
    textInputColors: {
      borderColor: theme.textInput.textInputBorderColor,
      backgroundColor: theme.textInput.textInputBackgroundColor,
      color: theme.textInput.textInputTextColor
    },
    textInputDisabled: {
      marginLeft: 10,
      borderColor: theme.borderColor,
      marginRight: 10
    },
    textInputDisabledColors: {
      backgroundColor: theme.textInput.textInputDisabledBackgroundColor,
      color: theme.textInput.textInputDisabledTextColor
    },
    textInputFocusedColors: {
      borderColor: theme.textInput.textInputBorderFocusedColor,
      backgroundColor: theme.textInput.textInputFocusBackgroundColor,
      color: theme.textInput.textInputTextColor
    }
  };
});

export enum Location {
  Before,
  After
}

interface ICommonTextInputProps {
  /**
   * Accessibility hint for the TextInput. Defaults to the empty string. (Optional)
   */
  accessibilityHint?: string;
  /**
   * Accessibility label for the TextInput. Defaults to the empty string. (Optional)
   */
  accessibilityLabel?: string;
  /**
   * Whether the component should be disabled. Defaults to enabled. (Optional)
   */
  disabled?: boolean;
  /**
   * Event handler for when text input changes end. Commonly used to validate contents. By default
   * this is a no-op. (Optional)
   */
  onEndEditing?: (value: string, setValue: React.Dispatch<React.SetStateAction<string>>) => void;
  /**
   * Event handler for when the text input's text changes. By default this calls
   * setValue(newValue). (Optional)
   */
  onChangeText?: (newValue: string, setValue: React.Dispatch<React.SetStateAction<string>>) => void;
  /**
   * The text that should appear in the label (Optional)
   */
  labelText?: string;
  /**
   * Indicates whether the label for the text input should be before or after the input box. (Optional)
   */
  labelLocation?: Location;
  /**
   * Initial value of the TextInput. Defaults to the empty string. (Optional)
   */
  initialValue?: string;
  /**
   * Custom TextInput styling (Optional)
   */
  customStyles?: [ViewStyle];
  /**
   * Test ID for TextInput
   */
  testID?: string;
}

export const CommonTextInput = (props: ICommonTextInputProps): JSX.Element => {
  const {
    initialValue,
    disabled,
    labelLocation,
    labelText,
    customStyles,
    onEndEditing,
    onChangeText,
    accessibilityHint,
    accessibilityLabel,
    testID
  } = props;
  const styles = useCommonTextInputStyles();
  const [isFocused, setIsFocused] = React.useState(false);
  const [value, setValue] = React.useState<string>(initialValue || '');

  let baseInputStyle = styles.textInputStyle;
  let colorStyle = styles.textInputColors;
  if (disabled) {
    baseInputStyle = styles.textInputDisabled;
    colorStyle = styles.textInputDisabledColors;
  } else if (isFocused) {
    colorStyle = styles.textInputFocusedColors;
  }

  return (
    <>
      {labelLocation === Location.Before ? <Text>{labelText}</Text> : null}
      <TextInput
        style={customStyles ? [baseInputStyle, colorStyle, ...customStyles] : [baseInputStyle, colorStyle]}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        keyboardType='numeric'
        onEndEditing={() => {
          if (onEndEditing) {
            onEndEditing(value, setValue);
          }
        }}
        onChangeText={(newValue) => {
          if (onChangeText) {
            onChangeText(newValue, setValue);
          } else {
            setValue(newValue);
          }
        }}
        value={value}
        editable={!disabled}
        focusable={!disabled}
        accessible={true}
        accessibilityState={{ disabled: disabled }}
        accessibilityHint={accessibilityHint}
        accessibilityLabel={accessibilityLabel}
        testID={testID || ''}
      />
      {labelLocation === Location.After ? <Text>{labelText}</Text> : null}
    </>
  );
};
