import React from 'react';

import { ActivityIndicator, View, ViewStyle, Text } from 'react-native';
import { KFMMoveView } from '../../kfmMoveView/KFMMoveView';
import { kfmMoveStringsList } from '../../kfmMoveView/resources/KFMViewResources';
import { SettingsView } from '../../settingsView/SettingsView';
import { settingsStringList } from '../../settingsView/resources/SettingsViewResources';
import { ReportABugView } from '../../reportABugView/ReportABugView';
import { reportABugStringList } from '../../reportABugView/resources/ReportABugViewResources';
import { DebugView } from '../../debugView/DebugView';
import { CommonContext } from './models/CommonContext';
import { useCommonContextData } from './models/useCommonContextData';
import { CommonContextDataModel } from './dataHandler/CommonContextDataModel';
import { ITheme, themeManager } from './themes/LoadingTheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { StringIDWin } from '../resources/resources';
import { CommonMessageHandler } from './dataHandler/CommonMessageHandler';
import { NativeDataHandler } from '../dataHandler/NativeDataHandler';
import { HiddenRootView } from '../../hiddenRootView/HiddenRootView';

export interface ICommonLoader {
  appKey: string;
}

interface ILoaderStyle {
  loadingView: ViewStyle;
  containerView: ViewStyle;
}

export const useLoadingStyles: ThemedStylesHook<ILoaderStyle> = createThemedStylesHook<ITheme, ILoaderStyle>(
  themeManager,
  (theme: ITheme) => {
    return {
      loadingView: {
        backgroundColor: theme.innerFrameBackgroundColor,
        height: '100%',
        justifyContent: 'center'
      },
      containerView: {
        flex: 1
      }
    };
  }
);

export const commonContextDataModel: CommonContextDataModel = new CommonContextDataModel();

const renderSwitch = (appKey: string): JSX.Element => {
  switch (appKey) {
    case 'hiddenRoot':
      return <HiddenRootView />;
    case 'kfmMove':
      return <KFMMoveView />;
    case 'settings':
      return <SettingsView />;
    case 'debug':
      return <DebugView />;
    case 'reportABug':
      return <ReportABugView />;
    default:
      return (
        <View>
          <Text>ERROR</Text>
        </View>
      );
  }
};

const windowsStringsListSwitch = (appKey: string): StringIDWin[] => {
  switch (appKey) {
    case 'kfmMove':
      return kfmMoveStringsList;
    case 'settings':
      return settingsStringList;
    case 'reportABug':
      return reportABugStringList;
    case 'hiddenRoot':
    case 'debug':
    default:
      return [];
  }
};

export const CommonLoader = (props: ICommonLoader): JSX.Element => {
  const dataHandler = React.useMemo(() => new NativeDataHandler(), []);
  const styles = useLoadingStyles();
  const commonContext = useCommonContextData(
    commonContextDataModel,
    props.appKey,
    windowsStringsListSwitch(props.appKey)
  );
  const { metadata } = commonContext;
  const isRTL = metadata?.account?.isRTL || false;

  if (commonContext.failedFetch) {
    return (
      <View style={styles.loadingView}>
        <Text>ERROR</Text>
      </View>
    );
  } else {
    return (
      <View style={[styles.containerView, { direction: isRTL ? 'rtl' : 'ltr' }]}>
        <CommonMessageHandler dataHandler={dataHandler} appKey={props.appKey} />
        {!commonContext.isAppFullyLoaded ? (
          <View style={styles.loadingView}>
            <ActivityIndicator size='large' />
          </View>
        ) : null}
        {/* Always ender the child app to allow it to load its data. */}
        <CommonContext.Provider value={commonContext}>{renderSwitch(props.appKey)}</CommonContext.Provider>
      </View>
    );
  }
};

export const exportedForTesting = {
  renderSwitch
};
