import { NativeEventEmitter, EmitterSubscription, NativeModules } from 'react-native';
import { ISettings } from '../../settingsView/models/AppContext';
import { IDataHandler, IDataHandlerSendOptions, IListenerArgs } from './IDataHandler';

// Create a NativeEventEmitter based on the NativeDataBridge NativeModule.
const DataHandlerEventEmitter: NativeEventEmitter = new NativeEventEmitter(NativeModules.NativeDataBridge);

// All messages received from the C++ layer will use 'message' as their event type qualifier string.
export const MESSAGE_EVENT_TYPE: string = 'message';

export interface IErrorResponse {
  error: {
    code: string;
    message: string;
  };
}

export type PatchResponse = ISettings | IErrorResponse;

// This is a wrapper class that makes async requests to the native C++ layer.
// It is the native implementation of IDataHandler, and requires this view to be
// correctly initialized from native code to be used.
export class NativeDataHandler implements IDataHandler {
  private _subscription: EmitterSubscription | undefined;

  public async send<T, IErrorResponse>(options: IDataHandlerSendOptions): Promise<T | IErrorResponse> {
    // Parse out the options parameters.
    const { url, requestType, postData } = options;
    let finalResponse: T | IErrorResponse;

    // Pass the parameters to the native C++ data bridge 'send' API and wait for the result.
    // Each call here will generate a new worker thread through the ucrtbased.dll and a high volume
    // of simultaneous calls to this API will not be well supported due to problems like heap churning.
    const jsonResponse = (await NativeModules.NativeDataBridge.send(
      url,
      requestType,
      JSON.stringify(postData)
    )) as string;

    // Parse out the JSON response string into the expected format.
    const { statusCode, response } = JSON.parse(jsonResponse) as {
      statusCode: number;
      response: T;
    };
    finalResponse = response;

    // If the call failed, return the error.
    if (statusCode !== 200) {
      finalResponse = response as {} as IErrorResponse;
    }

    return finalResponse;
  }

  public addEventListener(type: string, listener: (arg: IListenerArgs) => void): void {
    if (type === MESSAGE_EVENT_TYPE) {
      this._subscription = DataHandlerEventEmitter.addListener(MESSAGE_EVENT_TYPE, listener, this);
    }
  }

  public removeEventListener(type: string): void {
    if (type === MESSAGE_EVENT_TYPE) {
      this._subscription?.remove();
      delete this._subscription;
    }
  }
}
