import React, { useReducer } from 'react';
import { KFMDialogDataModel } from '../dataHandler/KFMDialogDataModel';
import {
  DialogState,
  IAppContextData,
  IKFMDialogState,
  IKFMUpdatedDialogState,
  ISpaceUsedInfo,
  FolderType,
  ConfirmDialogResult,
  FolderState,
  IDialogInfoState,
  IUserInputDialog,
  DialogStateEnum
} from './AppContext';
import { serializeFoldersArray } from './helpers';
import { CommonContext } from '../../common/loader/models/CommonContext';

import { reducer, initialState } from './reducer';
/**
 * useAppContextData() is only called once per start-up.
 * pass dataModel object as an argument, since it plays an important role to the hooks.
 */

export const useAppContextData = (dataModel: KFMDialogDataModel): IAppContextData => {
  const commonContext = React.useContext(CommonContext);
  // get state and dispatch function from useReducer
  const [state, dispatch] = useReducer(reducer, initialState);

  const onScanInitialized = (dialogState: IKFMDialogState): void => {
    // update dialog with initial state info
    const kfmDialogState: DialogState = {
      ...dialogState
    };
    const serializedFoldersArray = serializeFoldersArray(dialogState.folders);
    dispatch({ type: 'setDialogState', dialogState: kfmDialogState, folders: serializedFoldersArray });
  };

  const onDialogStateInfoReceived = (infoState: IDialogInfoState): void => {
    dispatch({ type: 'setDialogInfoState', data: infoState });
  };

  const onStateUpdate = (updatedDialogState: IKFMUpdatedDialogState): void => {
    dispatch({ type: 'setUpdatedPageState', data: updatedDialogState });
  };

  const onSetSpaceUsedInfo = (spaceInfo: ISpaceUsedInfo): void => {
    dispatch({ type: 'setSpaceUsedInfo', data: spaceInfo });
  };

  const onToggleFolder = async (folderType: FolderType): Promise<void> => {
    try {
      await dataModel.onToggleFolder(folderType);
    } catch (error) {
      console.error(`Failed to toggle folder state, ${error}`);
    }
  };

  const onStopFolderBackup = async (folderType: FolderType): Promise<void> => {
    try {
      await dataModel.onStopFolderBackup(folderType);
    } catch (error) {
      console.error(`Failed to stop folder backup, ${error}`);
    }
  };

  const onStartMove = async (): Promise<void> => {
    try {
      await dataModel.onStartMove();
    } catch (error) {
      console.error(`Failed to start migration, ${error}`);
    }
  };

  const onSkipBackup = async (): Promise<void> => {
    try {
      await dataModel.onSkipBackup();
    } catch (error) {
      console.error(`Failed to skip backup, ${error}`);
    }
  };

  const onTryAgain = async (): Promise<void> => {
    try {
      await dataModel.onTryAgain();
    } catch (error) {
      console.log(`Failed to try scanning again, ${error}`);
    }
  };

  const onUserInputDialogStateReceived = async (userInputDialogState: IUserInputDialog): Promise<void> => {
    dispatch({ type: 'setUserInputDialogState', data: userInputDialogState });
  };

  const confirmDialogClosed = async (result: ConfirmDialogResult): Promise<void> => {
    try {
      await dataModel.confirmDialogClosed(result);
      //remove userInputDialog state data to hide dismiss modal
      dispatch({ type: 'setUserInputDialogState', data: null });
    } catch (error) {
      console.error(`Failed to confirm backup choice, ${error}`);
    }
  };

  const sendReady = async (): Promise<void> => {
    try {
      await dataModel.sendReady();
    } catch (error) {
      console.error('Failed to send ready notification');
      dispatch({ type: 'setFailedToSendOnReady', data: true });
    }
  };

  React.useEffect(() => {
    // Disabling this lint rule here because we shouldn't add `await` in front of a promise in a `useEffect` hook.
    /* eslint-disable @typescript-eslint/no-floating-promises */
    sendReady();
    /* eslint-enable @typescript-eslint/no-floating-promises */
  }, []);

  React.useEffect(() => {
    const { dialogState, updatedPageState } = state;
    if (dialogState && updatedPageState) {
      // update dialog states
      const updatedKFMDialogState: DialogState = {
        ...dialogState,
        ...updatedPageState
      };

      // serialize and join initial and updated state
      const updatedFolders = serializeFoldersArray(updatedPageState.folders);
      const initialFolders = serializeFoldersArray(dialogState.folders);
      const folderKeys = Object.keys(updatedFolders);
      folderKeys.forEach((key) => {
        updatedFolders[key] = {
          ...initialFolders[key],
          ...updatedFolders[key]
        };
      });
      dispatch({ type: 'setDialogState', dialogState: updatedKFMDialogState, folders: updatedFolders });
      // Disable start sync button if all the active folders have already been synced
      const allFolders = Object.values(updatedFolders);
      const canSync = allFolders.some((folder) => folder.folderState === FolderState.Selected);
      dispatch({ type: 'setCanSync', data: canSync });

      // Show the try again button if there is at least one folder in error and the user can give input
      const atLeastOneFolderInError = allFolders.some((folder) => folder.folderState === FolderState.Error);
      const userInputAccepted = [DialogStateEnum.UserInput, DialogStateEnum.UserInputDialog].includes(
        dialogState.kfmCurrentDialogState
      );
      dispatch({ type: 'setCanTryAgain', data: atLeastOneFolderInError && userInputAccepted });
    }
  }, [state.dialogState?.folders, state.updatedPageState?.folders]);

  // Let the common context know when we are done or if we failed
  React.useEffect(() => {
    if (!commonContext.isAppFullyLoaded && state.dialogState !== null) {
      commonContext.onChildDataLoaded(false);
    } else if (state.failedFetch || state.failedToSendOnReady) {
      commonContext.onChildDataLoaded(true);
    }
  }, [state.dialogState, state.failedFetch, state.failedToSendOnReady]);

  return {
    ...state,
    sendReady,
    onStateUpdate,
    onScanInitialized,
    onSetSpaceUsedInfo,
    onToggleFolder,
    onStartMove,
    onSkipBackup,
    onTryAgain,
    onStopFolderBackup,
    confirmDialogClosed,
    onDialogStateInfoReceived,
    onUserInputDialogStateReceived
  };
};
