import * as React from 'react';
import { View, Text, ViewStyle, StyleSheet, Linking, Image, ImageStyle } from 'react-native';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { useSettingsStyles, ISettingsStyles } from '../themes/settingsStyles';
import { useCommonStyles } from '../../common/themes/ICommonTheme';
import { Link } from '../../common/components/Link';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';
import { Expander } from '../../common/components/Expander';

/**
 * Defines props for GenericSettingWrapper.
 */
export interface IGenericProps {
  /**
   * Name to be displayed in setting box
   */
  name: string;
  /**
   * Standard children prop: accepts any valid React Node
   */
  children: React.ReactNode;
  /**
   * Description to be displayed in setting box. (Optional)
   */
  description?: string;
  /**
   * Define this value if there should be a "Learn more" link at the end of the
   * setting description. The link will go to this URL.
   */
  link?: string;
  /**
   * Custon tab index for the "Learn more" link in this setting (Optional)
   */
  linkTabIndex?: number;
  /**
   * Optional additional styling
   */
  customContainerStyles?: ViewStyle;
  /**
   * Test ID for the label of the setting, for unit test purposes
   */
  labelTestID?: string;
  /**
   * Boolean to optionally show the expander component
   */
  hasExpander?: boolean;
  /**
   * React Node for expander content.
   */
  expanderContent?: React.ReactNode;
  /**
   * Optional custom description
   */
  customDescription?: React.ReactNode;
  /**
   * If true, show string about setting being controlled by group policy
   */
  showGroupPolicyDescription?: boolean;
}

// Defines types for Generic styles.
interface IGenericSettingStyles {
  outerContainer: ViewStyle;
  innerContainer: ViewStyle;
  textContainer: ViewStyle;
  componentContainer: ViewStyle;
  descriptionContainer: ViewStyle;
  pageContainer: ViewStyle;
  expanderContentContainer: ViewStyle;
}

export const useGenericSettingStyles: ThemedStylesHook<IGenericSettingStyles> = createThemedStylesHook<
  ITheme,
  IGenericSettingStyles
>(themeManager, (theme: ITheme) => {
  return {
    outerContainer: {
      position: 'relative',
      backgroundColor: theme.innerContainerColor,
      borderColor: theme.borderColor,
      borderRadius: 3,
      borderWidth: 1,
      flex: 1
    },
    pageContainer: {
      maxWidth: 554,
      marginBottom: 7,
      display: 'flex',
      flex: 1
    },
    innerContainer: {
      display: 'flex',
      minHeight: 62,
      flexDirection: 'row',
      justifyContent: 'space-between',
      alignItems: 'center',
      paddingLeft: 15,
      paddingRight: 22,
      paddingTop: 13,
      paddingBottom: 13
    },
    textContainer: {
      flex: 1,
      maxWidth: 392,
      minHeight: 20
    },
    componentContainer: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      justifyContent: 'space-between',
      paddingLeft: 20
    },
    descriptionContainer: {
      display: 'flex',
      flexDirection: 'column'
    },
    expanderContentContainer: {
      minHeight: 75,
      paddingHorizontal: 10,
      borderColor: theme.borderColor,
      borderWidth: 1,
      borderBottomLeftRadius: 3,
      borderBottomRightRadius: 3,
      borderTopWidth: 0,
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center'
    }
  };
});

/**
 * Renders description.
 * Component should have description for this function to be called.
 * Contains styling for centering names with no description.
 */
const renderDescription = (
  settingsStyles: StyleSheet.NamedStyles<ISettingsStyles>,
  description: string,
  link: string | undefined,
  linkText: string,
  linkAccessibilityLabel: string,
  linkTabIndex: number | undefined,
  icon?: JSX.Element
): JSX.Element => {
  const styles = useGenericSettingStyles();

  return (
    <View style={settingsStyles.fullWidth}>
      <View style={styles.descriptionContainer}>
        {icon ? (
          <View style={settingsStyles.groupPolicyIconWrapper}>
            {icon}
            <Text style={[settingsStyles.caption]}>{description}</Text>
          </View>
        ) : (
          <Text style={[settingsStyles.caption]}>{description}</Text>
        )}

        {link && linkText ? (
          <Link
            accessibilityLabel={linkAccessibilityLabel + '. ' + linkText}
            inlineLink={true}
            text={linkText}
            onPress={async () => {
              await Linking.openURL(link);
            }}
            customTextStyles={[settingsStyles.smallLink]}
            tabIndex={linkTabIndex}
          />
        ) : null}
      </View>
    </View>
  );
};

/**
 * Wraps dropdowns, buttons, and toggles in Generic styling.
 * @param {Interface} IGenericProps
 */
export function GenericSettingWrapper(props: IGenericProps): JSX.Element {
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();
  const styles = useGenericSettingStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const [isExpanded, setIsExpanded] = React.useState<boolean>(false);
  const {
    customContainerStyles,
    labelTestID,
    name,
    description,
    link,
    children,
    linkTabIndex,
    hasExpander,
    expanderContent,
    customDescription,
    showGroupPolicyDescription
  } = props;

  const showContent = (): JSX.Element | undefined => {
    return (
      <>
        {expanderContent ? (
          <View testID='expander-content' style={styles.expanderContentContainer}>
            {expanderContent}
          </View>
        ) : null}
      </>
    );
  };

  // Determine which description to show
  let finalDescription = null;
  if (showGroupPolicyDescription) {
    // Group policy description should overwrite any other description
    finalDescription = renderDescription(
      settingsStyles,
      useString(StringIDWin.IDS_RNSETTINGS_UNAVAILABLE_DUE_TO_POLICY),
      link,
      useString(StringIDWin.IDS_SETTINGS_LEARN_MORE_TEXT),
      name,
      linkTabIndex,
      <Image
        style={settingsStyles.groupPolicyIconStyle as ImageStyle}
        source={require('../assets/images/aboutIcon.png')}
      />
    );
  } else if (customDescription) {
    finalDescription = customDescription;
  } else if (description) {
    finalDescription = renderDescription(
      settingsStyles,
      description,
      link,
      useString(StringIDWin.IDS_SETTINGS_LEARN_MORE_TEXT),
      name,
      linkTabIndex
    );
  }

  return (
    <View style={styles.pageContainer}>
      <View style={styles.outerContainer}>
        <View style={styles.innerContainer}>
          <View
            style={
              customContainerStyles ? [styles.textContainer, customContainerStyles] : styles.textContainer
            }
          >
            <View style={settingsStyles.fullWidth}>
              <Text testID={labelTestID} style={commonStyles.bodyText}>
                {name}
              </Text>
            </View>
            {finalDescription}
          </View>
          <View
            style={
              customContainerStyles
                ? [styles.componentContainer, customContainerStyles]
                : styles.componentContainer
            }
          >
            {children}
          </View>
          {hasExpander ? (
            <View style={{ marginLeft: 20 }}>
              <Expander
                isExpanded={isExpanded}
                label={useString(StringIDWin.IDS_SETTINGS_OFFICE_COLLAB_BOX)}
                onPress={() => setIsExpanded(!isExpanded)}
                testID={'collaboration-expander'}
              />
            </View>
          ) : null}
        </View>
      </View>
      {isExpanded && expanderContent ? showContent() : null}
    </View>
  );
}
