import {
  DialogState,
  KFMFolders,
  UpdatedDialogState,
  SpaceUsedInfo,
  DialogInfoState,
  UserInputDialog
} from './AppContext';

interface IState {
  updatedPageState: UpdatedDialogState;
  dialogState: DialogState;
  folders: KFMFolders;
  failedToSendOnReady: boolean;
  failedFetch: boolean;
  spaceUsedInfo: SpaceUsedInfo;
  canSync: boolean;
  canTryAgain: boolean;
  dialogInfoState: DialogInfoState;
  userInputDialogState: UserInputDialog;
}

// create initial state representation for useReducer
export const initialState: IState = {
  updatedPageState: null,
  dialogState: null,
  folders: null,
  failedToSendOnReady: false,
  failedFetch: false,
  spaceUsedInfo: null,
  canSync: false,
  canTryAgain: false,
  dialogInfoState: null,
  userInputDialogState: null
};

// define action type and expected payload/body
type Action =
  | { type: 'setUpdatedPageState'; data: UpdatedDialogState }
  | { type: 'setDialogState'; dialogState: DialogState; folders: KFMFolders }
  | { type: 'setFolders'; data: KFMFolders }
  | { type: 'setFailedToSendOnReady'; data: boolean }
  | { type: 'setCanSync'; data: boolean }
  | { type: 'setCanTryAgain'; data: boolean }
  | { type: 'setFailedFetch'; data: boolean }
  | { type: 'setSpaceUsedInfo'; data: SpaceUsedInfo }
  | { type: 'setUserInputDialogState'; data: UserInputDialog }
  | { type: 'setDialogInfoState'; data: DialogInfoState };
// reducer accepts current state, and action.
//returns an updated state after appending the new data based on the action type
export function reducer(state: IState, action: Action): IState {
  switch (action.type) {
    case 'setUpdatedPageState':
      return {
        ...state,
        updatedPageState: action.data
      };
    case 'setDialogState':
      return {
        ...state,
        dialogState: action.dialogState,
        folders: action.folders
      };
    case 'setFolders':
      return {
        ...state,
        folders: action.data
      };
    case 'setSpaceUsedInfo':
      return {
        ...state,
        spaceUsedInfo: action.data
      };
    case 'setFailedFetch':
      return {
        ...state,
        failedFetch: action.data
      };
    case 'setCanSync':
      return {
        ...state,
        canSync: action.data
      };
    case 'setCanTryAgain':
      return {
        ...state,
        canTryAgain: action.data
      };
    case 'setFailedToSendOnReady':
      return {
        ...state,
        failedToSendOnReady: action.data
      };
    case 'setDialogInfoState':
      return {
        ...state,
        dialogInfoState: action.data
      };
    case 'setUserInputDialogState':
      return {
        ...state,
        userInputDialogState: action.data
      };
  }
}
