import { NativeDataHandler } from '../../common/dataHandler/NativeDataHandler';
import { IDataHandlerSendOptions } from '../../common/dataHandler/IDataHandler';

export interface ICategory {
  category: string;
  subCategories: string[];
}

export interface ICategoriesResponse {
  categories: ICategory[];
}

export interface IReportABugResponse {
  statusCode: number;
  message: string;
}

export class ReportABugDataModel {
  private _dataHandler: NativeDataHandler;

  public constructor() {
    this._dataHandler = new NativeDataHandler();
  }

  public async fetchAllCategories(): Promise<ICategoriesResponse> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/ReportABug/categories',
      requestType: 'GET'
    };
    return this._dataHandler.send(options);
  }

  public async reportBug(description: string, category: string): Promise<IReportABugResponse> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/ReportABug/submitBug',
      requestType: 'POST',
      postData: {
        description: description,
        category: category
      }
    };
    return this._dataHandler.send(options);
  }
}
