import * as React from 'react';
import { View, Text, AccessibilityRole, ViewStyle, TextStyle, AccessibilityState } from 'react-native';
import { CommonPressable } from './CommonPressable';
import { commonThemeManager, ICommonTheme, useCommonStyles } from '../themes/ICommonTheme';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';

interface ILinkProps {
  /**
   * Text to be displayed as a link
   */
  text: string;
  /**
   * Callback function passed to the onPress handler
   */
  onPress: () => void;
  /**
   * Set to true if this is an inline link (Inline links should have underlines
   * for readability). Defaults to false.
   */
  inlineLink?: boolean;
  /**
   * Accessibility label for the link (optional)
   */
  accessibilityLabel?: string;
  /**
   * Accessibility hint for the link (optional)
   */
  accessibilityHint?: string;
  /**
   * Accessibility role for the link (optional, defaults to "link")
   */
  accessibilityRole?: AccessibilityRole;
  /**
   * Accessibility state for the link (optional)
   */
  accessibilityState?: AccessibilityState;
  /**
   * ID used to find element in jest tests (optional)
   */
  testID?: string;
  /**
   * Custom link styling (Optional)
   */
  customStyles?: ViewStyle[];
  /**
   * Custom link text styling (Optional)
   */
  customTextStyles?: TextStyle[];
  /**
   * Custon tab index for this link (Optional)
   */
  tabIndex?: number;
  /**
   * Optional prop for link to have customContents (Optional)
   */
  customContents?: React.ReactNode;
}

interface ILinkStyles {
  linkDefault: TextStyle;
  linkHover: TextStyle;
  linkPressed: TextStyle;
  hoverBackground: TextStyle;
  row: ViewStyle;
}

const useLinkStyles: ThemedStylesHook<ILinkStyles> = createThemedStylesHook<ICommonTheme, ILinkStyles>(
  commonThemeManager,
  (theme: ICommonTheme) => {
    return {
      linkDefault: {
        color: theme.link.linkTextColor,
        padding: 6,
        paddingHorizontal: 12
      },
      linkHover: {
        color: theme.link.linkHover
      },
      linkPressed: {
        color: theme.link.linkPressed
      },
      hoverBackground: {
        borderRadius: 3,
        backgroundColor: theme.link.linkHoverBackgroundColor,
        borderColor: 'transparent'
      },
      row: {
        flexDirection: 'row',
        alignItems: 'center'
      }
    };
  }
);

/**
 * Creates clickable and styled Text component to act as a link
 * @param {Interface} ILinkProps
 */
export function Link(props: ILinkProps): JSX.Element {
  const commonStyles = useCommonStyles();
  const styles = useLinkStyles();

  const {
    text,
    inlineLink,
    onPress: propsOnPress,
    customStyles,
    customTextStyles,
    accessibilityLabel,
    accessibilityHint,
    accessibilityRole,
    accessibilityState,
    tabIndex,
    customContents,
    testID
  } = props;

  return (
    <View
      style={customStyles ? [{ alignItems: 'flex-start' }, ...customStyles] : { alignItems: 'flex-start' }}
    >
      <CommonPressable
        onPress={propsOnPress}
        accessibilityLabel={accessibilityLabel}
        accessibilityHint={accessibilityHint}
        accessibilityRole={accessibilityRole || 'link'}
        accessibilityState={accessibilityState}
        tabIndex={tabIndex}
        testID={testID}
      >
        {(isPressed, isHovering) => {
          // Determine base style
          const linkStyleBase = inlineLink ? commonStyles.inlineLinkText : commonStyles.linkText;

          // Determine additional styles based on link state
          let linkStateStyle = styles.linkDefault;
          if (isPressed) {
            linkStateStyle = styles.linkPressed;
          } else if (isHovering) {
            linkStateStyle = styles.linkHover;
          } else if (inlineLink) {
            linkStateStyle = commonStyles.inlineLink;
          }
          return (
            <View style={isHovering ? [styles.hoverBackground, styles.row] : styles.row}>
              {customContents}
              <Text
                style={
                  customTextStyles
                    ? [linkStyleBase, linkStateStyle, customTextStyles]
                    : [linkStyleBase, linkStateStyle]
                }
              >
                {text}
              </Text>
            </View>
          );
        }}
      </CommonPressable>
    </View>
  );
}
