import React from 'react';
import { View, Text, TextInput, ViewStyle, Linking, TextStyle } from 'react-native';
import { CommonButton } from '../common/components/CommonButton';
import { Dropdown } from '../common/components/Dropdown';
import { Link } from '../common/components/Link';
import { CommonContext } from '../common/loader/models/CommonContext';
import { ThemedStylesHook, createThemedStylesHook } from '../common/themes/themes';
import { ITheme, themeManager } from './themes/ITheme';
import { StringIDWin } from '../common/resources/resources';
import { ReportABugDataModel } from './dataHandler/ReportABugDataModel';
import { ICategory } from './dataHandler/ReportABugDataModel';
import { InProgressView } from './views/InProgressView';
import { SuccessView } from './views/SuccessView';
import { FailureView } from './views/FailureView';

export const reportABugDataModel: ReportABugDataModel = new ReportABugDataModel();
interface IReportABugStyles {
  frame: ViewStyle;
  innerFrame: ViewStyle;
  title: TextStyle;
  subheaderText: TextStyle;
  privacyText: TextStyle;
  description: TextStyle;
  actionButton: ViewStyle;
  closeButton: ViewStyle;
}

export const useReportABugViewStyles: ThemedStylesHook<IReportABugStyles> = createThemedStylesHook<
  ITheme,
  IReportABugStyles
>(themeManager, (theme: ITheme) => {
  // Font sizes
  const largeText = 22;

  // Font weights
  const medium = '500';
  const defaultFontFamily = 'Segoe UI';

  return {
    frame: {
      height: 530,
      width: 590
    },
    innerFrame: {
      padding: 50,
      backgroundColor: theme.innerFrameBackgroundColor,
      height: '100%',
      paddingBottom: 0,
      flexDirection: 'row',
      marginBottom: 0,
      borderColor: theme.bodyTextColor,
      flexWrap: 'wrap'
    },
    title: {
      color: theme.bodyTextColor,
      fontWeight: medium,
      fontSize: largeText,
      lineHeight: 36,
      fontFamily: defaultFontFamily,
      paddingBottom: 20
    },
    subheaderText: {
      marginBottom: 8,
      color: theme.bodyTextColor,
      fontSize: 14,
      fontWeight: '500',
      lineHeight: 20,
      marginTop: 0
    },
    removePadding: {
      paddingBottom: 0
    },
    privacyText: {
      color: theme.bodyTextColor
    },
    description: {
      marginBottom: 10,
      textAlignVertical: 'top',
      height: 100
    },
    actionButton: {
      flexDirection: 'row',
      alignSelf: 'flex-end'
    },
    closeButton: {
      marginRight: 10
    }
  };
});

type CategoryOption = [string, string][];

export const ReportABugView = (): JSX.Element => {
  const categoriesList = [] as ICategory[];
  const privacyStatementLink = 'https://go.microsoft.com/fwlink/?LinkId=521839';
  const requestFailed = 'Request Failed';
  const requestSuccessful = 'Request Successful';
  const styles = useReportABugViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const [desc, onChangeDescription] = React.useState('');
  const [category, onChangeCategory] = React.useState(useString(StringIDWin.IDS_REPORT_A_BUG_CATEGORY_TEXT));
  const [categories, setCategories] = React.useState<ICategory[]>(categoriesList);
  const [inProgress, onSubmit] = React.useState(false);
  const [bugSubmissionResponse, setResponse] = React.useState<string>();
  const [submitted, onBugSubmissionComplete] = React.useState(false);

  const reportBug = async (): Promise<void> => {
    onSubmit(true); // start spinner
    try {
      const reportABugResponse = await reportABugDataModel.reportBug(desc, category);
      setResponse(reportABugResponse.message);
      onSubmit(false);
      onBugSubmissionComplete(true);
    } catch (error) {
      console.error('Failed to fetch from ReportABug/submitBug: ' + error);
      setResponse(JSON.stringify(error));
    }
  };

  const closeDialog = (): void => {
    // TODO: handle close dialog
  };

  const tryAgain = (): void => {
    onSubmit(false);
    onBugSubmissionComplete(false);
  };

  /*
   * format categories list to display each dropdown option following the
   * pattern category - sub-category
   */
  const formatCategories = (): CategoryOption => {
    const categoryList: CategoryOption = [];
    categories.forEach((category) => {
      if (category.subCategories) {
        category.subCategories.forEach((subCategory) => {
          const categoryOption: [string, string] = [
            category.category + ' - ' + subCategory,
            category.category + ' - ' + subCategory
          ];
          categoryList.push(categoryOption);
        });
      } else {
        const categoryOption: [string, string] = [category.category, category.category];
        categoryList.push(categoryOption);
      }
    });

    return categoryList;
  };

  React.useEffect(() => {
    const fetchAllCategories = async (): Promise<void> => {
      try {
        const categoriesObj = await reportABugDataModel.fetchAllCategories();
        setCategories(categoriesObj.categories);
        commonContext.onChildDataLoaded(false);
      } catch (error) {
        console.error('Failed to fetch categories');
        onChangeCategory('Unknown');
      }
    };

    /* eslint-disable @typescript-eslint/no-floating-promises */
    fetchAllCategories();
    /* eslint-enable @typescript-eslint/no-floating-promises */
  }, []);

  if (inProgress) {
    return (
      <View style={[styles.frame]}>
        <View style={styles.innerFrame}>
          <InProgressView />
        </View>
      </View>
    );
  } else if (submitted && bugSubmissionResponse === requestSuccessful) {
    return (
      <View style={[styles.frame]}>
        <View style={styles.innerFrame}>
          <SuccessView closeDialog={closeDialog} />
        </View>
      </View>
    );
  } else if (submitted && bugSubmissionResponse === requestFailed) {
    return (
      <View style={[styles.frame]}>
        <View style={styles.innerFrame}>
          <FailureView tryAgain={tryAgain} />
        </View>
      </View>
    );
  } else {
    return (
      <View style={[styles.frame]}>
        <View style={styles.innerFrame}>
          <View>
            <Text
              style={styles.title}
              accessibilityRole='header'
              accessibilityLabel={useString(StringIDWin.IDS_REPORT_A_BUG_TITLE)}
              testID='reportABugHeader'
            >
              {useString(StringIDWin.IDS_REPORT_A_BUG_TITLE)}
            </Text>
            {categories && categories.length > 0 && (
              <View>
                <Text style={styles.subheaderText}>
                  {useString(StringIDWin.IDS_REPORT_A_BUG_CATEGORY_TEXT)}
                </Text>
                <Dropdown
                  selectedItem={
                    category ? category : useString(StringIDWin.IDS_REPORT_A_BUG_DEFAULT_CATEGORY_OPTION)
                  }
                  itemValues={formatCategories()}
                  onClick={(newValue: string) => onChangeCategory(newValue)}
                />
              </View>
            )}
            <View>
              <Text style={styles.subheaderText}>
                {useString(StringIDWin.IDS_REPORT_A_BUG_DESCRIPTION_TEXT)}
              </Text>
              <TextInput
                testID='description'
                onChangeText={onChangeDescription}
                placeholder={useString(StringIDWin.IDS_REPORT_A_BUG_PLACEHOLDER_TEXT)}
                style={styles.description}
                value={desc}
                multiline={true}
                numberOfLines={4}
              />
            </View>
            <View>
              <Text style={styles.privacyText}>
                {useString(StringIDWin.IDS_REPORT_A_BUG_PRIVACY_STATEMENT_BUSINESS)}
              </Text>
              <Link
                accessibilityLabel={useString(StringIDWin.IDS_REPORT_A_BUG_PRIVACY_LINK)}
                text={useString(StringIDWin.IDS_REPORT_A_BUG_PRIVACY_LINK)}
                inlineLink
                onPress={async () => {
                  await Linking.openURL(privacyStatementLink);
                }}
              />
            </View>
            {/* TODO: Add date picker */}
            <View style={styles.actionButton}>
              <View style={styles.closeButton}>
                <CommonButton
                  onPress={closeDialog}
                  buttonName={useString(StringIDWin.IDS_REPORT_A_BUG_CANCEL_BUTTON)}
                />
              </View>
              <View>
                <CommonButton
                  onPress={reportBug}
                  testID='Submit'
                  disabled={
                    desc === '' ||
                    category === useString(StringIDWin.IDS_REPORT_A_BUG_DEFAULT_CATEGORY_OPTION)
                  }
                  buttonName={useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUTTON)}
                />
              </View>
            </View>
          </View>
        </View>
      </View>
    );
  }
};
