/**
 * This file contains the code necessary to create themed stylesheets for your individual React
 * Native app. See below for a full example.
 *
 * // Example theme code, in a separate file or files:
 *
 * // Note: the name of this interface can be customized, and should be defined in your own app's
 * // code, not in common code. You do not have to extend ICommonTheme unless you intend to
 * // directly use some of its colors or modify its colors.
 * export interface IMyTheme extends ICommonTheme {
 *   windowBackgroundColor: string;
 * }
 *
 * // Note: You can overwrite any theme color from CommonLightTheme (and other themes) here if you
 * // redefine that color as a new property. If you did not extend ICommonTheme, you don't need to
 * // include CommonLightTheme here (and below in similar examples).
 * export const LightTheme: IMyTheme {
 *   ...CommonLightTheme,
 *   windowBackgroundColor: '#FFFFFF'
 * }
 *
 * export const DarkTheme: IMyTheme {
 *    ...CommonDarkTheme,
 *   windowBackgroundColor: '#000000'
 * }
 *
 * export function createHighContrastTheme(systemTheme: ISystemHighContrastTheme): IMyTheme {
 *   return {
 *     ...createCommonHighContrastTheme(systemTheme),
 *     windowBackgroundColor: systemTheme.WindowColor
 *   }
 * }
 *
 * export const themeManager: ThemeManager<IMyTheme> = new ThemeManager(
 *   LightTheme,
 *   DarkTheme,
 *   createHighContrastTheme
 * );
 *
 *
 * // Then, in each separate component file:
 *
 * interface IMyComponentStyles {
 *   window: ViewStyle;
 * }
 *
 * const useMyComponentStyles: ThemedStylesHook<IMyComponentStyles> = createthemedStylesHook<
 *   IMyTheme,
 *   IMyComponentStyles
 * >(themeManager, (theme: IMyTheme) => {
 *   return {
 *     window: {
 *       backgroundColor: theme.windowBackgroundColor
 *     }
 *   }
 * });
 *
 * function MyComponent(): {
 *   const styles = useMyComponentStyles();
 *   // Optionally you can also use styles from common themes with this:
 *   const commonStyles = useCommonStyles();
 *
 *   return (
 *     <View style={styles.window} />
 *   )
 * }
 */

import React from 'react';
import { StyleSheet, useColorScheme } from 'react-native';
import { AppTheme } from 'react-native-windows';

/**
 * Instantiate this class to define colors for each theme, where TTheme is the list of semantic
 * colors for this app. The instance should be passed to all calls to createThemedStylesHook.
 */
export class ThemeManager<TTheme> {
  public lightTheme: TTheme;
  public darkTheme: TTheme;
  public createHighContrastTheme: (systemTheme: ISystemHighContrastTheme) => TTheme;

  public constructor(
    newLightTheme: TTheme,
    newDarkTheme: TTheme,
    newCreateHighContrastTheme: (systemTheme: ISystemHighContrastTheme) => TTheme
  ) {
    this.lightTheme = newLightTheme;
    this.darkTheme = newDarkTheme;
    this.createHighContrastTheme = newCreateHighContrastTheme;
  }
}

/**
 * Example Windows 11 built-in theme colors:
 *
 * Aquatic:
 * NOTE: ButtonTextColor & WindowTextColor are the same. ButtonFaceColor & WindowColor are the same.
 * ButtonFaceColor: "#202020"
 * ButtonTextColor: "#ffffff"
 * GrayTextColor: "#a6a6a6"
 * HighlightColor: "#8ee3f0"
 * HighlightTextColor: "#263b50"
 * HotlightColor: "#75e9fc"
 * WindowColor: "#202020"
 * WindowTextColor: "#ffffff"
 *
 * Desert:
 * NOTE: ButtonFaceColor & WindowColor are the same (for all themes, as far as I can tell).
 * ButtonFaceColor: "#fffaef"
 * ButtonTextColor: "#202020"
 * GrayTextColor: "#676767"
 * HighlightColor: "#903909"
 * HighlightTextColor: "#fff5e3"
 * HotlightColor: "#1c5e75"
 * WindowColor: "#fffaef"
 * WindowTextColor: "#3d3d3d"
 *
 * NOTE: Today, all of the Win 11 built-in high contrast themes have ButtonFaceColor and WindowColor
 * as the same color, but technically users can modify these colors, so we're using ButtonFaceColor
 * just for buttons (via "button.buttonColor"), and WindowColor everywhere else.
 *
 * Similar case for ButtonTextColor and WindowTextColor - only one of the built-in themes has them
 * the exact same, but the rest of the themes have these being very close in color. For now we're using
 * ButtonTextColor for the normal state of buttons, button borders, and radio buttons and their borders.
 */
export interface ISystemHighContrastTheme {
  ButtonFaceColor: string;
  ButtonTextColor: string;
  GrayTextColor: string;
  HighlightColor: string;
  HighlightTextColor: string;
  HotlightColor: string;
  WindowColor: string;
  WindowTextColor: string;
}

export type ThemedStylesHook<TStyles> = () => StyleSheet.NamedStyles<TStyles>;

/**
 * Use this function in each component that needs its own stylesheet.
 *
 * @param themeManager The ThemeManager instance for this app, which defines the themes to use for
 * light/dark/high contrast
 * @param themedStyles A function that takes a theme and returns a React Native StyleSheet for the
 * given component
 * @returns A hook that can be used in the component to reference these styles.
 *
 * Note that when you create themes, you need to define your own theme interface of your semantic colors.
 */
export const createThemedStylesHook = <TTheme, TStyles>(
  themeManager: ThemeManager<TTheme>,
  themedStyles: (theme: TTheme) => TStyles
): ThemedStylesHook<TStyles> => {
  const useThemedStyles: ThemedStylesHook<TStyles> = () => {
    // useColorScheme reads in whether the system is in light or dark mode
    // NOTE: it does not account for high contrast
    const colorScheme = useColorScheme();

    const [isHighContrast, setHighContrast] = React.useState(AppTheme ? AppTheme.isHighContrast : false);
    const [highContrastColors, setHighContrastColors] = React.useState<ISystemHighContrastTheme | undefined>(
      AppTheme ? AppTheme.currentHighContrastColors : undefined
    );

    // Listen for high contrast state changes (on/off or theme change)
    React.useEffect(() => {
      const subscription = AppTheme.addListener('highContrastChanged', () => {
        setHighContrast(AppTheme.isHighContrast);
        setHighContrastColors(AppTheme.currentHighContrastColors);
      });
      return () => subscription.remove();
    }, [isHighContrast, highContrastColors, AppTheme]);

    // Refresh the stylesheet if the theme changes
    const styleSheet = React.useMemo<TStyles>(() => {
      let currentTheme;
      if (isHighContrast && highContrastColors) {
        // Convert 8 high contrast system colors to an ITheme
        currentTheme = themeManager.createHighContrastTheme(AppTheme.currentHighContrastColors);
      } else {
        switch (colorScheme) {
          case 'dark':
            currentTheme = themeManager.darkTheme;
            break;
          default:
            currentTheme = themeManager.lightTheme;
            break;
        }
      }

      return StyleSheet.create<TStyles>(themedStyles(currentTheme));
    }, [colorScheme, isHighContrast, highContrastColors, AppTheme]);

    return styleSheet;
  };

  return useThemedStyles;
};
