import * as React from 'react';
import { TextStyle } from 'react-native';
import { View, Text, ViewStyle } from 'react-native';
import { QuotaState } from '../../common/loader/models/CommonContext';
import { useSettingsStyles } from '../themes/settingsStyles';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { CommonButton } from '../../common/components/CommonButton';

interface IStorageProps {
  /**
   * Number that represents used storage (bytes).
   */
  storageUsedInBytes: number;
  /**
   * Number that represents total storage (bytes).
   */
  storageTotalInBytes: number;
  /**
   * String that determines color fill of bar.
   */
  quotaState: QuotaState;
  /**
   * String displayed on top of storage bar.
   */
  storageName: string;
  /**
   * Display name of button under storage bar.
   */
  buttonName: string;
  /**
   * Function to open popup when 'Upgrade Button' is pressed
   */
  onPress: () => void;
  /**
   * Display string of amount of quota used
   */
  quotaUsageLabel: string;
  /**
   * Accessibility label for quota bar (percentage used)
   */
  quotaBarAccessibilityLabel: string;
  /**
   * Accessibility label for button
   */
  buttonAccessibilityLabel: string;
}

// Create types for storage styles.
interface IStorageStyles {
  wholeBar: ViewStyle;
  greyBar: ViewStyle;
  colorBar: ViewStyle;
  colorBarNormal: ViewStyle;
  colorBarCritical: ViewStyle;
  textView: ViewStyle;
  storageText: TextStyle;
  storageButton: ViewStyle;
  quotaUsedMargin: ViewStyle;
}

// Create storage styles.
const useStorageSettingStyles: ThemedStylesHook<IStorageStyles> = createThemedStylesHook<
  ITheme,
  IStorageStyles
>(themeManager, (theme: ITheme) => {
  return {
    wholeBar: {
      marginBottom: 8
    },
    greyBar: {
      height: 1,
      width: 200,
      borderRadius: 2,
      backgroundColor: theme.storageTotalBar
    },
    colorBar: {
      height: 5,
      borderRadius: 2,
      position: 'relative',
      top: -3 // This overlaps the colored bar on top of the grey bar
    },
    colorBarNormal: {
      backgroundColor: theme.link.linkTextColor
    },
    colorBarCritical: {
      backgroundColor: theme.storageBarCritical
    },
    textView: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'space-between',
      flexWrap: 'wrap',
      maxWidth: 200
    },
    storageText: {
      fontWeight: '500',
      color: theme.bodyTextColor
    },
    storageButton: {
      alignSelf: 'flex-start'
    },
    quotaUsedMargin: {
      marginBottom: 21
    }
  };
});

const bytesPerGigabyte: number = 1073741824;
const greyBarWidth: number = 200;

// Convert storageUsed to gigabytes and round
export const roundStorageUsedGB = (storageUsed: number): string => {
  return (storageUsed / bytesPerGigabyte).toFixed(1);
};
// Convert storageTotal to gigabytes
export const roundStorageTotalGB = (storageTotal: number): string => {
  return (storageTotal / bytesPerGigabyte).toFixed(0);
};

// Calculate bar width using storageUsed : storageTotal ratio
export const calculateBarWidth = (storageUsed: number, storageTotal: number): number => {
  const storageUsedGB = storageUsed / bytesPerGigabyte;
  const storageTotalGB = storageTotal / bytesPerGigabyte;
  if (storageUsedGB === 0 || storageTotalGB === 0) {
    return 0;
  } else {
    return Math.round((storageUsedGB / storageTotalGB) * greyBarWidth);
  }
};

const ProgressBar = (props: {
  fillWidth: number;
  fillColor: ViewStyle;
  accessibilityLabel: string;
}): JSX.Element => {
  const { fillWidth, fillColor, accessibilityLabel } = props;
  const styles = useStorageSettingStyles();

  return (
    <View accessibilityRole={'image'} accessibilityLabel={accessibilityLabel} style={styles.wholeBar}>
      {/* Progress Bar Background, greyBar */}
      <View style={styles.greyBar} />
      {/* Progress Bar, blue bar will be shown based on the user's storage progess */}
      <View style={[styles.colorBar, fillColor, { width: fillWidth }]} />
    </View>
  );
};

/**
 * Creates storage component with visual storage indicator and upgrade plan button.
 * @param {Interface} IStorageProps - The button name, text, storageUsed, and storageTotal for a
 * storage setting.
 */
export function StorageSetting(props: IStorageProps): JSX.Element {
  const {
    storageUsedInBytes,
    storageTotalInBytes,
    quotaState,
    storageName,
    quotaBarAccessibilityLabel,
    quotaUsageLabel,
    buttonName,
    onPress,
    buttonAccessibilityLabel
  } = props;
  const styles = useStorageSettingStyles();
  const settingsStyles = useSettingsStyles();
  const barWidth = calculateBarWidth(storageUsedInBytes, storageTotalInBytes);

  // Changes color depending on storage state from data layer.
  let colorStyle: ViewStyle = styles.colorBarNormal;
  switch (quotaState) {
    case QuotaState.Critical:
    case QuotaState.Full:
    case QuotaState.OverLimit:
      colorStyle = styles.colorBarCritical;
      break;
    default:
      break;
  }
  return (
    <View>
      <View style={styles.textView}>
        <Text style={settingsStyles.subheaderText}>{storageName}</Text>
      </View>
      <ProgressBar
        fillWidth={barWidth}
        fillColor={colorStyle}
        // This should be a full sentence describing how much percentage of quota is used.
        accessibilityLabel={quotaBarAccessibilityLabel}
      />
      {/* This should be a shortened sentence describing how much percentage of quota is used. */}
      <Text style={[settingsStyles.caption, styles.quotaUsedMargin]} testID='quotaUsageText'>
        {quotaUsageLabel}
      </Text>
      <View style={styles.storageButton}>
        {buttonName !== '' ? (
          <CommonButton
            buttonName={buttonName}
            onPress={onPress}
            // This should be a full sentence describing how much quota is used, then a period,
            // then read the button text.
            accessibilityLabel={buttonAccessibilityLabel.trim() + '. ' + buttonName}
          />
        ) : null}
      </View>
    </View>
  );
}
