import React from 'react';
import { View, Text, ViewStyle, TextStyle, ScrollView } from 'react-native';
import { NativeDataHandler } from '../common/dataHandler/NativeDataHandler';
import { SettingsMessageHandler } from './dataHandler/SettingsMessageHandler';
import { ITheme, themeManager } from './themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../common/themes/themes';
import { AppContext } from './models/AppContext';
import { TabContextProvider } from './components/TabContextManagement';
import { LeftNav } from './components/LeftNav';
import { RightPane } from './components/RightPane';
import { useAppContextData } from './models/useAppContextData';
import { NativeSettingsDataModel } from './dataHandler/NativeSettingsDataModel';
import { CommonContext } from '../common/loader/models/CommonContext';

const dataHandler: NativeDataHandler = new NativeDataHandler();
export const nativeSettingsDataModel: NativeSettingsDataModel = new NativeSettingsDataModel();

interface ISettingsViewStyles {
  frame: ViewStyle;
  innerFrame: ViewStyle;
  loadingView: ViewStyle;
  bodyText: TextStyle;
  leftNav: ViewStyle;
  rightPane: ViewStyle;
}

export const useSettingsViewStyles: ThemedStylesHook<ISettingsViewStyles> = createThemedStylesHook<
  ITheme,
  ISettingsViewStyles
>(themeManager, (theme: ITheme) => {
  return {
    frame: {
      height: 600,
      width: 865
    },
    innerFrame: {
      backgroundColor: theme.innerFrameBackgroundColor,
      height: '100%',
      paddingBottom: 0,
      flexDirection: 'row',
      marginBottom: 0
    },
    loadingView: {
      backgroundColor: theme.innerFrameBackgroundColor,
      height: '100%',
      justifyContent: 'center'
    },
    bodyText: {
      color: theme.bodyTextColor
    },
    leftNav: {
      width: 220,
      paddingTop: 20
    },
    rightPane: {
      width: 635,
      paddingLeft: 40,
      paddingRight: 40,
      paddingTop: 17
    }
  };
});

export const SettingsView: () => JSX.Element = () => {
  const styles = useSettingsViewStyles();

  const commonContext = React.useContext(CommonContext);
  const appContext = useAppContextData(nativeSettingsDataModel);

  if (appContext.failedFetch) {
    // Resource strings failed to load, show an error
    return (
      <View style={styles.loadingView}>
        <Text>ERROR</Text>
      </View>
    );
  } else if (!commonContext.isAppFullyLoaded) {
    // Resource strings are still loading, let the common loader show its spinner
    return <></>;
  } else {
    return (
      <AppContext.Provider value={appContext}>
        <View style={[styles.frame]}>
          <View style={styles.innerFrame}>
            <SettingsMessageHandler dataHandler={dataHandler} />
            <TabContextProvider>
              <View style={styles.leftNav}>
                <LeftNav />
              </View>
              <View style={{ flex: 1 }}>
                <ScrollView>
                  <View style={styles.rightPane}>
                    <RightPane />
                  </View>
                </ScrollView>
              </View>
            </TabContextProvider>
          </View>
        </View>
      </AppContext.Provider>
    );
  }
};
