import { ThemeManager } from '../../../common/themes/themes';
import { ThemeName } from '../../../common/themes/themeUtils';
import { ISystemHighContrastTheme } from '../../../common/themes/themes';

/**
 * Simplified theming for the common loading spinner
 */

export interface ITheme {
  themeName: ThemeName;
  innerFrameBackgroundColor: string;
}

// Dark theme color palette
export const DarkTheme: ITheme = {
  themeName: ThemeName.Dark,
  innerFrameBackgroundColor: '#202020'
};

// light theme color palette
export const LightTheme: ITheme = {
  themeName: ThemeName.Light,
  innerFrameBackgroundColor: '#F3F3F3'
};

export function createHighContrastTheme(systemTheme: ISystemHighContrastTheme): ITheme {
  return {
    themeName: ThemeName.HighContrast,
    innerFrameBackgroundColor: systemTheme.WindowColor
  };
}

// Define themes for this app
export const themeManager: ThemeManager<ITheme> = new ThemeManager(
  LightTheme,
  DarkTheme,
  createHighContrastTheme
);
