import React from 'react';
import { View, Text, ActivityIndicator, ViewStyle, TextStyle } from 'react-native';
import { StringIDWin } from '../../common/resources/resources';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { ITheme, themeManager } from './../themes/ITheme';
interface IInProgressStyles {
  afterSubmitFrame: ViewStyle;
  inProgressViewMargin: ViewStyle;
  title: TextStyle;
}

export const useInProgressViewStyles: ThemedStylesHook<IInProgressStyles> = createThemedStylesHook<
  ITheme,
  IInProgressStyles
>(themeManager, (theme: ITheme) => {
  // Font sizes
  const largeText = 22;

  // Font weights
  const medium = '500';
  const defaultFontFamily = 'Segoe UI';
  return {
    afterSubmitFrame: {
      height: '80%',
      width: 500
    },
    inProgressViewMargin: {
      marginTop: 120
    },
    title: {
      color: theme.bodyTextColor,
      fontWeight: medium,
      fontSize: largeText,
      lineHeight: 36,
      fontFamily: defaultFontFamily,
      paddingBottom: 8
    }
  };
});

export const InProgressView = (): JSX.Element => {
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const styles = useInProgressViewStyles();

  return (
    <View>
      <View style={styles.afterSubmitFrame}>
        <View>
          <Text
            style={styles.title}
            accessibilityRole='header'
            accessibilityLabel={useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_IN_PROGRESS)}
            testID='reportABugInProgressHeader'
          >
            {useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_IN_PROGRESS)}
          </Text>
        </View>
        <View style={styles.inProgressViewMargin}>
          <ActivityIndicator size='large' />
        </View>
      </View>
    </View>
  );
};
