import { ISystemHighContrastTheme } from '../../common/themes/themes';
import { ITheme } from './ITheme';
import { createCommonHighContrastTheme } from '../../common/themes/CommonHighContrastTheme';

/**
 * High Contrast themes come in from Windows dynamically as a limited color pallete,
 * this needs to be mapped to our ITheme type.
 */
export function createHighContrastTheme(systemTheme: ISystemHighContrastTheme): ITheme {
  return {
    ...createCommonHighContrastTheme(systemTheme),
    componentAccentColor: systemTheme.HighlightColor,
    innerFrameBackgroundColor: systemTheme.WindowColor,
    innerContainerColor: systemTheme.WindowColor,
    secondaryContainerColor: systemTheme.WindowColor,
    selectedTabColor: systemTheme.HighlightColor,
    selectedTabAccent: systemTheme.HighlightTextColor,
    selectedTabText: systemTheme.HighlightTextColor,
    pressedTabColor: systemTheme.HighlightColor,
    pressedTabText: systemTheme.HighlightTextColor,
    tabIconColor: systemTheme.WindowTextColor,
    tabIconSelectedColor: systemTheme.WindowColor,
    borderColor: systemTheme.WindowTextColor,
    storageTotalBar: systemTheme.WindowTextColor,
    storageBarCritical: systemTheme.HotlightColor,
    descriptionColor: systemTheme.WindowTextColor
  };
}
