import {
  IDropDownState,
  INetworkState,
  IResourceStrings,
  ISettings,
  isIDropDownState,
  isINetworkState,
  isIToggleState,
  IToggleState,
  Auth
} from '../models/AppContext';
import { IErrorResponse, NativeDataHandler, PatchResponse } from '../../common/dataHandler/NativeDataHandler';
import { IDataHandlerSendOptions } from '../../common/dataHandler/IDataHandler';
import { Buffer } from 'buffer';

export class NativeSettingsDataModel {
  private _dataHandler: NativeDataHandler;

  public constructor() {
    this._dataHandler = new NativeDataHandler();
  }

  /**
   * Get all resource string values for settings.
   * Resource strings cannot be modified.
   * They are localized strings to be displayed in the UI, and they are all expected to be pre-formatted by design.
   */
  public async fetchAllResourceStrings(): Promise<IResourceStrings> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/RNSettings/resources',
      requestType: 'GET'
    };
    return this._dataHandler.send(options);
  }

  /**
   * Get all setting values.
   *
   * If keys are included they will be added to the url as params, like so:
   *     ...settings?select=key1&key2&key3
   * In this case, only the setting values for those keys will be returned.
   */
  public async fetchAllSettings(keys?: string[]): Promise<ISettings> {
    let url = 'http://localhost:9001/sync/RNSettings/settings';
    if (keys !== undefined && keys.length >= 1) {
      url = url + '?select=';
      for (let i = 0; i < keys.length; i++) {
        if (i === 0) {
          url += keys[i];
        } else {
          url += '&' + keys[i];
        }
      }
    }
    const options: IDataHandlerSendOptions = {
      url: url,
      requestType: 'GET'
    };
    return this._dataHandler.send(options);
  }

  /**
   * Get authentication token for fetching profile picture, and set auth profilePicture data bytes.
   */
  public async fetchProfilePicture(scopeStr: string): Promise<Auth> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/auth/graphAuthToken',
      requestType: 'GET',
      postData: { scopeStr: scopeStr }
    };

    let auth: Auth = null;

    auth = await this._dataHandler.send(options);
    if (auth === null || auth.authToken === '' || auth.authToken === null) {
      throw new Error('Failed to get auth token');
    }

    auth.profilePicture = '';
    const headers = new Map();
    headers.set('Authorization', 'Bearer ' + auth.authToken);
    const resp = await getArrayBuffer('https://graph.microsoft.com/v1.0/me/photo/$value', headers);
    const base64 = arrayBufferToBase64(resp);
    const str = 'data:image/jpeg;base64,' + base64;
    auth.profilePicture = str;
    return auth;
  }

  public async patchSetting(
    key: string,
    value: boolean | string | INetworkState,
    settings: ISettings
  ): Promise<PatchResponse> {
    const currentValue = settings[key];
    let postData: { [key: string]: IToggleState | IDropDownState | INetworkState } | null = null;
    let finalPromise: Promise<PatchResponse>;

    // Update the mock representation of the setting state
    if (typeof value === 'boolean') {
      if (isIToggleState(currentValue)) {
        postData = { [key]: { ...settings[key], checked: value } as IToggleState };
      }
    } else if (typeof value === 'string') {
      if (isIDropDownState(currentValue)) {
        postData = { [key]: { ...settings[key], selection: value } as IDropDownState };
      }
    } else if (isINetworkState(value)) {
      // Only send the settings the user can edit
      const { limitDownload, limitUpload, uploadRateAuto, uploadLimit, downloadLimit } = value;
      postData = {
        [key]: {
          ...settings[key],
          limitDownload,
          limitUpload,
          uploadRateAuto,
          uploadLimit,
          downloadLimit
        } as INetworkState
      };
    }

    if (postData) {
      const options: IDataHandlerSendOptions = {
        url: 'http://localhost:9001/sync/RNSettings/settings',
        requestType: 'PATCH',
        postData: postData
      };
      finalPromise = this._dataHandler.send(options);
    } else {
      finalPromise = Promise.reject({ error: 'Malformed input' });
    }
    return finalPromise;
  }

  public async patchCatfood(value: string): Promise<PatchResponse> {
    let finalPromise: Promise<PatchResponse>;
    if (value) {
      const options: IDataHandlerSendOptions = {
        url: 'http://localhost:9001/sync/RNSettings/catfood',
        requestType: 'PATCH',
        postData: value
      };
      finalPromise = this._dataHandler.send(options);
    } else {
      finalPromise = Promise.reject({ error: 'Malformed input for catfood' });
    }
    return finalPromise;
  }

  /**
   * Initiate sync engine call to start getting all mounted folder information.
   */
  public async beginGetMountedFolders(): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/mountedFolders/beginGetMountedFolders',
      requestType: 'GET'
    };
    const errorRespone: IErrorResponse = await this._dataHandler.send(options);
    if (errorRespone && errorRespone.error) {
      throw new Error(JSON.stringify(errorRespone.error));
    }
  }

  public async stopSyncingMountedFolder(scopeId: string): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/mountedFolders/stopSync',
      requestType: 'POST',
      postData: { scopeId: scopeId }
    };
    return this._dataHandler.send(options);
  }

  public async startSyncingDefaultMountedFolder(): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/mountedFolders/startDefaultSync',
      requestType: 'POST',
      postData: {}
    };
    return this._dataHandler.send(options);
  }

  public async chooseFolders(scopeId: string, libraryType: number): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/mountedFolders/chooseFolders',
      requestType: 'POST',
      postData: {
        scopeId: scopeId,
        libraryType: libraryType
      }
    };
    return this._dataHandler.send(options);
  }
}

export function getArrayBuffer(url: string, headers: Map<string, string>): Promise<string> {
  return new Promise((resolve, reject) => {
    const req = new XMLHttpRequest();
    req.open('GET', url, true);
    headers.forEach((value: string, key: string) => {
      req.setRequestHeader(key, value);
    });
    req.responseType = 'arraybuffer';

    req.onload = function () {
      const resp = req.response;
      if (req.status >= 200 && req.status < 300) {
        resolve(resp);
      } else {
        reject({
          status: req.status,
          statusText: req.statusText
        });
      }
    };
    req.onerror = function () {
      reject({
        status: req.status,
        statusText: req.statusText
      });
    };
    req.send();
  });
}

function arrayBufferToBase64(raw: string): string {
  const buffer = Buffer.from(raw, 'base64');
  return buffer.toString('base64');
}
