import React from 'react';
import { View, Text, ViewStyle, TextStyle } from 'react-native';
import { CommonButton } from '../../common/components/CommonButton';
import { StringIDWin } from '../../common/resources/resources';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { ITheme, themeManager } from './../themes/ITheme';
interface IFailureStyles {
  afterSubmitFrame: ViewStyle;
  afterSubmitActionButton: ViewStyle;
  title: TextStyle;
  subheaderText: TextStyle;
}

export const useInProgressViewStyles: ThemedStylesHook<IFailureStyles> = createThemedStylesHook<
  ITheme,
  IFailureStyles
>(themeManager, (theme: ITheme) => {
  // Font sizes
  const largeText = 22;

  // Font weights
  const medium = '500';
  const defaultFontFamily = 'Segoe UI';
  return {
    afterSubmitFrame: {
      height: '80%',
      width: '100%'
    },
    afterSubmitActionButton: {
      width: '100%',
      flexDirection: 'row',
      justifyContent: 'flex-end'
    },
    inProgressViewMargin: {
      marginTop: 120
    },
    title: {
      color: theme.bodyTextColor,
      fontWeight: medium,
      fontSize: largeText,
      lineHeight: 36,
      fontFamily: defaultFontFamily,
      paddingBottom: 8
    },
    subheaderText: {
      marginBottom: 8,
      color: theme.bodyTextColor,
      fontSize: 14,
      fontWeight: '400',
      lineHeight: 20,
      marginTop: 0
    }
  };
});
interface IFailureViewProps {
  tryAgain: () => void;
}

export const FailureView = (props: IFailureViewProps): JSX.Element => {
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const styles = useInProgressViewStyles();
  const { tryAgain } = props;

  return (
    <View>
      <View style={styles.afterSubmitFrame}>
        <View>
          <Text
            style={styles.title}
            accessibilityRole='header'
            accessibilityLabel={useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_FAILURE)}
            testID='reportABugHeaderFailureHeader'
          >
            {useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_FAILURE)}
          </Text>
          <Text style={styles.subheaderText}>
            {useString(StringIDWin.IDS_REPORT_A_BUG_SUBMIT_BUG_FAILURE_MESSAGE)}
          </Text>
        </View>
      </View>
      <View style={styles.afterSubmitActionButton}>
        <CommonButton
          onPress={tryAgain}
          buttonName={useString(StringIDWin.IDS_REPORT_A_BUG_TRY_AGAIN_BUTTON)}
        />
      </View>
    </View>
  );
};
