export function OneDriveInfo(): JSX.Element | null {
  const styles = useAboutViewStyles();
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);
  const [copyComplete, setCopyComplete] = React.useState(false);
  const [copyButtonText, setCopyButtonText] = React.useState(
    useString(StringIDWin.IDS_ABOUT_DIALOG_DEVICEID_COPY_SHORT)
  );

  React.useEffect(() => {
    if (copyComplete) {
      setCopyButtonText(useString(StringIDWin.IDS_ABOUT_DIALOG_DEVICEID_COPIED_SHORT));
      setTimeout(() => {
        setCopyComplete(false);
        setCopyButtonText(useString(StringIDWin.IDS_ABOUT_DIALOG_DEVICEID_COPY_SHORT));
      }, 7000);
    }
  }, [copyComplete]);

  if (appContext.resourceStrings && appContext.settings) {
    const onPressCopyLink = (): void => {
      onAction((appContext.settings!.copyInfoLink as ILinkState).actionParameter);
      setCopyComplete(true);
    };

    return (
      <View>
        <View style={styles.infoOuterContainerFirst}>
          <View style={styles.infoInnerContainer}>
            <View style={styles.infoTextContainer}>
              <View style={settingsStyles.fullWidth}>
                <Text style={commonStyles.bodyText}>
                  {useString(StringIDWin.IDS_ABOUT_DIALOG_ONEDRIVE_INFO_LABEL)}
                </Text>
              </View>
            </View>
            <View style={styles.infoChildrenContainer}>
              <View style={styles.infoButtonContainer}>
                <CommonButton
                  buttonName={copyButtonText}
                  onPress={onPressCopyLink}
                  testID={'copyButton'}
                  accessibilityLiveRegion={'polite'}
                  accessibilityLabel={useString(StringIDWin.IDS_ABOUT_DIALOG_DEVICEID_COPY)}
                  accessibilityHint={useString(StringIDWin.IDS_ABOUT_DIALOG_COPY_BUTTON_HINT)}
                />
              </View>
            </View>
          </View>
        </View>
        <View>
          <View style={styles.infoOuterContainerSecond}>
            <View style={styles.infoInnerTextRow}>
              <View style={styles.infoLabelFlex}>
                <Text style={[commonStyles.bodyText, styles.textSpacing]}>
                  {useString(StringIDWin.IDS_ABOUT_DIALOG_DEVICEID_LABEL)}
                </Text>
              </View>
              <View style={styles.infoValueFlex}>
                <Text style={[settingsStyles.secondaryText, styles.textSpacing]} testID={'deviceIdText'}>
                  {appContext.resourceStrings.deviceIdName}
                </Text>
              </View>
            </View>
            <View style={styles.infoInnerTextRow}>
              <View style={styles.infoLabelFlex}>
                <Text style={styles.textPadding}>
                  {useString(StringIDWin.IDS_ABOUT_DIALOG_VERSION_LABEL)}
                </Text>
              </View>
              <View style={styles.infoValueFlex}>
                <Link
                  accessibilityLabel={appContext.resourceStrings.versionName}
                  customStyles={[settingsStyles.secondaryText, styles.versionLinkSpacing]}
                  text={appContext.resourceStrings.versionName}
                  testID={'versionText'}
                  onPress={async () => {
                    if (appContext.settings) {
                      await Linking.openURL((appContext.settings.versionLink as ILinkState).actionParameter);
                    }
                  }}
                />
              </View>
            </View>
          </View>
          <View style={styles.infoOuterContainerThird}>
            <View style={styles.infoSecondaryContainerLabel}>
              <Text style={commonStyles.bodyText}>
                {useString(StringIDWin.IDS_ABOUT_DIALOG_RELATED_LINKS)}
              </Text>
            </View>
            <View style={styles.infoSecondaryContainerContent}>
              <View style={styles.linkContainer}>
                <Link
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_HELP_PRIVACY_COOKIES_WO_TAGS)}
                  text={useString(StringIDWin.IDS_SETTINGS_HELP_PRIVACY_COOKIES_WO_TAGS)}
                  onPress={async () => {
                    if (appContext.settings) {
                      await Linking.openURL(
                        (appContext.settings.privacyStatementLink as ILinkState).actionParameter
                      );
                    }
                  }}
                />
              </View>
              <View style={styles.linkContainer}>
                <Link
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_HELP_TERMS_OF_USE_WO_TAGS)}
                  text={useString(StringIDWin.IDS_SETTINGS_HELP_TERMS_OF_USE_WO_TAGS)}
                  onPress={async () => {
                    if (appContext.settings) {
                      await Linking.openURL(
                        (appContext.settings.termsOfUseLink as ILinkState).actionParameter
                      );
                    }
                  }}
                />
              </View>
              <View style={styles.linkContainer}>
                <Link
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_HELP_THIRD_PARTY_NOTICES_WO_TAGS)}
                  text={useString(StringIDWin.IDS_SETTINGS_HELP_THIRD_PARTY_NOTICES_WO_TAGS)}
                  onPress={() => {
                    if (appContext.settings) {
                      onAction((appContext.settings.thirdPartyNoticesLink as ILinkState).actionParameter);
                    }
                  }}
                />
              </View>
              <View style={styles.linkContainer}>
                <Link
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_HELP_REPORT_CONCERN_NEW)}
                  text={useString(StringIDWin.IDS_SETTINGS_HELP_REPORT_CONCERN_NEW)}
                  onPress={() => {
                    if (appContext.settings) {
                      onAction((appContext.settings.reportAConcernLink as ILinkState).actionParameter);
                    }
                  }}
                />
              </View>
            </View>
          </View>
        </View>
      </View>
    );
  } else {
    return null;
  }
}

interface ICatfoodDescriptionProps {
  description: string;
  isCatfoodAllowed: boolean;
}

const CatfoodDescription = (props: ICatfoodDescriptionProps): JSX.Element => {
  const settingsStyles = useSettingsStyles();
  const styles = useGenericSettingStyles();
  const { description, isCatfoodAllowed } = props;
  const [isTextInputShown, setIsTextInputShown] = React.useState<boolean>(false);
  const [descriptionBlock, setDescriptionBlock] = React.useState<string>(description);
  const [isFocusable, setIsFocusable] = React.useState<boolean>(false);
  const [keyPressed, setKeyPressed] = React.useState<boolean>(false);
  const appContext = React.useContext(AppContext);

  const textRef = React.useRef<View>(null);

  // Checks if text input is a valid value
  const checkString = (input: string): void => {
    appContext.onCatfoodChange(input);
    // re render update ring
    setDescriptionBlock(input);
    setIsTextInputShown(false);
    setIsFocusable(false);
    setKeyPressed(false);
  };

  React.useEffect(() => {
    if (isFocusable) {
      textRef.current?.focus();
    }
  }, [isFocusable]);

  return (
    <View style={settingsStyles.fullWidth}>
      <View style={styles.descriptionContainer}>
        {isTextInputShown ? (
          <CommonTextInput
            customStyles={[{ width: 150 }]}
            initialValue={descriptionBlock}
            onEndEditing={checkString}
            testID={'UpdateRingTextInput'}
          />
        ) : (
          <Pressable
            {...{
              onKeyDown: (event: IKeyboardEvent): void => {
                if (event.nativeEvent.altKey && event.nativeEvent.ctrlKey && event.nativeEvent.shiftKey) {
                  setKeyPressed(true);
                }
              },
              onKeyUp: (event: IKeyboardEvent): void => {
                if (event.nativeEvent.altKey || event.nativeEvent.ctrlKey || event.nativeEvent.shiftKey) {
                  setKeyPressed(false);
                }
              }
            }}
            focusable={isFocusable}
            onBlur={() => {
              setIsFocusable(false);
            }}
            ref={textRef}
            onPress={() => {
              if (isCatfoodAllowed) {
                setIsFocusable(true);
                if (keyPressed) {
                  setIsTextInputShown(true);
                }
              }
            }}
          >
            {(appContext.settings?.preReleaseBuilds as IFasterRingState).isPolicySet ? (
              <View style={[settingsStyles.groupPolicyIconWrapper, { alignItems: 'center' }]}>
                <Image
                  style={settingsStyles.groupPolicyIconStyle as ImageStyle}
                  source={require('../assets/images/aboutIcon.png')}
                />
                <Text style={[settingsStyles.caption]}>{descriptionBlock}</Text>
              </View>
            ) : (
              <Text style={settingsStyles.caption}>{descriptionBlock}</Text>
            )}
          </Pressable>
        )}
      </View>
    </View>
  );
};

export function AboutView(): JSX.Element {
  const styles = useAboutViewStyles();
  const settingsViewStyles = useSettingsViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const appContext = React.useContext(AppContext);
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();
  let description = '';
  let updateRing = '';
  let isCatfoodAllowed = false;

  if (appContext.resourceStrings && appContext.settings) {
    let buildToggleName = useString(StringIDWin.IDS_ABOUT_DIALOG_UPDATERING_PRODUCTION);
    if (appContext.settings.preReleaseBuilds) {
      const ringName = (appContext.settings.preReleaseBuilds as IFasterRingState).ringName.toLowerCase();
      const isPolicySet = (appContext.settings?.preReleaseBuilds as IFasterRingState).isPolicySet;

      if (ringName.includes('msit') || ringName === 'catfood') {
        isCatfoodAllowed = true;
        buildToggleName = useString(StringIDWin.IDS_ABOUT_DIALOG_UPDATERING_MSIT);
        if (ringName === 'msitslow') {
          updateRing = 'MSIT Slow';
        } else if (ringName === 'msitfast') {
          updateRing = 'MSIT Fast';
        } else {
          updateRing = 'Catfood';
        }
      }

      if (isPolicySet) {
        description = useString(StringIDWin.IDS_RNSETTINGS_UNAVAILABLE_DUE_TO_POLICY);
        if (updateRing !== '') {
          description += '\n' + updateRing;
        }
      } else {
        description = updateRing;
      }
    }

    return (
      <>
        <View style={styles.section}>
          <Text
            accessibilityRole='header'
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_HELP_TAB)}
            style={settingsStyles.title}
            testID='aboutHeader'
          >
            {useString(StringIDWin.IDS_SETTINGS_HELP_TAB)}
          </Text>
        </View>

        <View style={styles.section}>
          <OneDriveInfo />
        </View>

        {appContext.settings.preReleaseBuilds ? (
          <>
            <Text style={settingsStyles.subheaderText}>
              {useString(StringIDWin.IDS_SETTINGS_UPDATERING_MSIT_TITLE)}
            </Text>
            <GenericSettingWrapper
              name={buildToggleName}
              customDescription={
                <CatfoodDescription description={description} isCatfoodAllowed={isCatfoodAllowed} />
              }
            >
              <ToggleAdapter accessibilityLabel={buildToggleName} settingId={settingIDs.preReleaseBuildsId} />
            </GenericSettingWrapper>
          </>
        ) : null}

        <Footer />

        <Text style={commonStyles.bodyText}>{appContext.resourceStrings.copyrightName}</Text>
      </>
    );
  } else {
    /**
     * loading view if ResourceStrings or Settings are null or undefined
     * should not occur because checked for in SettingsView
     */
    return (
      <View style={settingsViewStyles.loadingView}>
        <ActivityIndicator size='large' />
      </View>
    );
  }
}
