import React from 'react';
import { ActivityIndicator, ImageStyle, Text, TextStyle, View, ViewStyle } from 'react-native';
import { useSettingsViewStyles } from '../SettingsView';
import { settingIDs } from '../models/SettingIDs';
import { AccountSetting } from '../components/AccountSetting';
import { CommonButton } from '../../common/components/CommonButton';
import { Dropdown } from '../../common/components/Dropdown';
import { GenericSettingWrapper } from '../components/GenericSettingWrapper';
import { MountedFolderList } from '../components/MountedFolderList';
import { AppContext, IButtonState, IDropDownState, VaultLockTimeout } from '../models/AppContext';
import { useSettingsStyles } from '../themes/settingsStyles';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { Footer } from '../components/Footer';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';
import { useCommonStyles } from '../../common/themes/ICommonTheme';

// additional styling for network settings
interface IAccountViewStyles {
  accountHeader: ViewStyle;
  account: ViewStyle;
  container: ViewStyle;
  addAccountImage: ImageStyle;
  accountSection: ViewStyle;
  addAccountButtonView: ViewStyle;
  bodyText: TextStyle;
  sectionPadding: ViewStyle;
}

// About page styling specifications
const useAccountViewStyles: ThemedStylesHook<IAccountViewStyles> = createThemedStylesHook<
  ITheme,
  IAccountViewStyles
>(themeManager, (theme: ITheme) => {
  return {
    accountHeader: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'space-between'
    },
    account: {
      marginBottom: 23,
      flexDirection: 'row',
      flex: 1
    },
    container: {
      paddingBottom: 83
    },
    bodyText: {
      /**
       * to offset the extra marginBottom of the subheader text, a special case
       *  in the Personal Vault section of the Account tab
       */
      marginTop: -9,
      fontSize: 12,
      marginBottom: 13
    },
    sectionPadding: {
      marginTop: 11
    },
    accountSection: {
      display: 'flex',
      flexDirection: 'row'
    },
    addAccountButtonView: {
      marginTop: 30
    },
    addAccountImage: {
      marginLeft: 11
    }
  };
});

// AccountView display
export function AccountView(): JSX.Element {
  const styles = useAccountViewStyles();
  const settingsViewStyles = useSettingsViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();

  if (appContext.settings) {
    let vaultSection: JSX.Element | null = null;
    if (appContext.settings.personalVaultTimeout) {
      vaultSection = (
        <View style={styles.sectionPadding}>
          <Text
            style={settingsStyles.subheaderText}
            accessibilityRole='header'
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_AUTOLOCK)}
          >
            {useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_AUTOLOCK)}
          </Text>
          <Text style={[commonStyles.bodyText, styles.bodyText]}>
            {useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_NOTIFICATION)}
          </Text>
          <GenericSettingWrapper
            name={useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_LOCK_AFTER_TEXT)}
            description={''}
          >
            <Dropdown
              itemValues={[
                [VaultLockTimeout.TwentyMinutes, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_20MINUTES)],
                [VaultLockTimeout.OneHour, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_1HOUR)],
                [VaultLockTimeout.TwoHours, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_2HOURS)],
                [VaultLockTimeout.FourHours, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_4HOURS)]
              ]}
              selectedItem={(appContext.settings.personalVaultTimeout as IDropDownState).selection}
              onClick={(newValue: string) =>
                appContext.onDropdownChange(settingIDs.personalVaultId, newValue)
              }
              accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_ACCESSIBILITY_LABEL)}
            />
          </GenericSettingWrapper>
        </View>
      );
    }

    return (
      <View>
        <View style={styles.accountHeader}>
          <Text
            accessibilityRole='header'
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_TAB)}
            style={settingsStyles.title}
            testID='accountHeader'
          >
            {useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_TAB)}
          </Text>
        </View>
        <View style={[styles.sectionPadding, styles.accountSection]}>
          <View style={styles.account}>
            <AccountSetting />
          </View>
          <View style={styles.addAccountButtonView}>
            <CommonButton
              buttonName={useString(StringIDWin.IDS_SETTINGS_ADD_ACCOUNT_BUTTON_NAME)}
              disabled={(appContext.settings.addAccountButton as IButtonState).disabled}
              onPress={() =>
                onAction((appContext.settings!.addAccountButton as IButtonState).actionParameter)
              }
              icon={require('../assets/images/ic_fluent_add_24_filled.png')}
              customIconStyle={styles.addAccountImage as ImageStyle}
              tabIndex={0}
            />
          </View>
        </View>
        <View style={styles.sectionPadding}>
          <MountedFolderList />
        </View>
        {vaultSection}
        <Footer />
      </View>
    );
  } else {
    /**
     * loading view if Settings are null or undefined
     * should not occur because checked for in SettingsView
     */
    return (
      <View style={settingsViewStyles.loadingView}>
        <ActivityIndicator size='large' />
      </View>
    );
  }
}
