import { StringIDWin } from '../../common/resources/resources';
import { FolderState, IFolder, IKFMFolders, KFMFolder } from './AppContext';

/*
  Given an array of folders, convert into a key-value pair. 
  The unique ID of the folder being the key and the content being the value.
  Input: [{folderName:"Desktop", folderType: 1},{folderName: "Documents", folderType: 2}]
  Ouput: {1:{folderName: "Desktop", ...}, 2:{folderName: "Documents", ...}}
  This will make it easier to manipulate the folders.
  */
export const serializeFoldersArray = (folders: Array<KFMFolder>): IKFMFolders => {
  const initialFolders: { [key: string]: IFolder } = {};
  const serialized = folders.reduce((prev, curr) => {
    if (curr) {
      prev[curr.folderType.toString()] = curr;
    }
    return prev;
  }, initialFolders);
  return serialized;
};
/**
 * Given the current state of a folder, return the matching localized value of that state
 * @param stateId - current folder state
 * @param resourceStrings - resource strings to match wit folder state
 * @returns localized string matching folder state
 */
export const getFolderStateString = (stateId: FolderState): StringIDWin => {
  switch (stateId) {
    case FolderState.Scanning:
      return StringIDWin.IDS_KFM_WINDOW_SCANNING_TEXT;
    case FolderState.Unselected:
      return StringIDWin.IDS_KFM_WINDOW_FOLDER_STATUS_UNSELECTED;
    case FolderState.Selected:
      return StringIDWin.IDS_KFM_WINDOW_FOLDER_STATUS_SELECTED;
    case FolderState.InProgress:
      return StringIDWin.IDS_KFM_WINDOW_FOLDER_STATUS_SYNCING;
    case FolderState.Done:
    case FolderState.DoneWithStop:
      return StringIDWin.IDS_KFM_WINDOW_FOLDER_STATUS_SYNCED;
    case FolderState.Error:
      return StringIDWin.IDS_KFM_WINDOW_FOLDER_SYNC_ERROR_TEXT;
    default:
      return StringIDWin.IDS_KFM_WINDOW_SCANNING_TEXT;
  }
};
