import * as React from 'react';
import { View, Image, ViewStyle, ImageStyle } from 'react-native';
import { CommonPressable } from './CommonPressable';
import { commonThemeManager, ICommonTheme } from '../themes/ICommonTheme';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';

interface IExpanderStyles {
  expanderNormal: ViewStyle;
  expanderPressedBackground: ViewStyle;
  expanderHoveringBackground: ViewStyle;
  expanderNormalColor: ImageStyle;
  expanderDisabledColor: ImageStyle;
  expanderHoveringPressed: ImageStyle;
}

const useExpanderStyles: ThemedStylesHook<IExpanderStyles> = createThemedStylesHook<
  ICommonTheme,
  IExpanderStyles
>(commonThemeManager, (theme: ICommonTheme) => {
  return {
    expanderNormal: {
      padding: 7,
      alignItems: 'center',
      borderRadius: 3
    },
    expanderPressedBackground: {
      backgroundColor: theme.expander.expanderBackgroundPressed
    },
    expanderHoveringBackground: {
      backgroundColor: theme.expander.expanderBackgroundHover
    },
    expanderNormalColor: {
      tintColor: theme.bodyTextColor
    },
    expanderDisabledColor: {
      tintColor: theme.expander.expanderDisabled
    },
    expanderHoveringPressed: {
      tintColor: theme.expander.expanderHoveringPressed
    }
  };
});

interface IExpanderProps {
  /**
   * Accessibility label for expander
   */
  label: string;
  /**
   * Whether the expander should be expanded
   */
  isExpanded: boolean;
  /**
   * The callback function passed to the onPress handler
   */
  onPress: () => void;
  /**
   * Accessibility hint for expander (Optional)
   */
  hint?: string;
  /**
   * Whether the expander should be disabled, defaults to false (Optional)
   */
  disabled?: boolean;
  /**
   * Test ID for this component (Optional)
   */
  testID?: string;
}

export function Expander(props: IExpanderProps): JSX.Element {
  const styles = useExpanderStyles();
  const { onPress, label, hint, isExpanded, testID, disabled } = props;

  return (
    <CommonPressable
      onPress={onPress}
      accessibilityLabel={label}
      accessibilityRole='button'
      accessibilityState={{ expanded: isExpanded }}
      accessibilityHint={hint}
      testID={testID}
    >
      {(isPressed, isHovering) => {
        let backgroundColor = styles.expanderNormal;
        let expanderColor = styles.expanderNormalColor;
        if (disabled) {
          expanderColor = styles.expanderDisabledColor;
        } else if (isPressed) {
          expanderColor = styles.expanderHoveringPressed;
          backgroundColor = styles.expanderPressedBackground;
        } else if (isHovering) {
          expanderColor = styles.expanderHoveringPressed;
          backgroundColor = styles.expanderHoveringBackground;
        }

        return (
          <View style={[styles.expanderNormal, backgroundColor]}>
            <Image
              source={
                isExpanded
                  ? require('../assets/images/chevronUp.png')
                  : require('../assets/images/chevronDown.png')
              }
              style={[
                {
                  width: 16,
                  height: 16
                },
                expanderColor as ImageStyle
              ]}
            />
          </View>
        );
      }}
    </CommonPressable>
  );
}
