import * as React from 'react';
import { TabLabel } from './RightPane';

// Tab properties
interface IContextProps {
  tab: TabLabel;
  setTab: (tab: TabLabel) => void;
}

// create TabContext
export const TabContext: React.Context<IContextProps> = React.createContext<IContextProps>({
  // set default tab to Sync
  tab: TabLabel.Sync,
  setTab: (tab: TabLabel) => {}
});

// generic Provider props
interface IProviderProps {
  children: React.ReactNode;
}

export const TabContextProvider = (props: IProviderProps): JSX.Element => {
  const [tab, setTab] = React.useState(TabLabel.Sync);
  const value = { tab, setTab };

  return <TabContext.Provider value={value}>{props.children}</TabContext.Provider>;
};
