import { ThemeManager } from '../../common/themes/themes';
import { LightTheme } from './LightTheme';
import { DarkTheme } from './DarkTheme';
import { createHighContrastTheme } from './HighContrast';
import { ICommonTheme } from '../../common/themes/ICommonTheme';

export interface ITheme extends ICommonTheme {
  componentAccentColor: string;
  innerFrameBackgroundColor: string;
  innerContainerColor: string;
  secondaryContainerColor: string;
  selectedTabColor: string;
  selectedTabAccent: string;
  selectedTabText: string;
  pressedTabColor: string;
  pressedTabText: string;
  tabIconColor: string;
  tabIconSelectedColor: string;
  borderColor: string;
  storageTotalBar: string;
  storageBarCritical: string;
  descriptionColor: string;
}

// Define themes for this app
export const themeManager: ThemeManager<ITheme> = new ThemeManager(
  LightTheme,
  DarkTheme,
  createHighContrastTheme
);
