import React from 'react';
import { NetworkSetting } from '../components/NetworkSetting';
import { View, Text, ActivityIndicator, I18nManager, Button } from 'react-native';
import { AppTheme } from 'react-native-windows';
import { AppContext, ILinkState } from '../models/AppContext';
import { StorageSetting } from '../components/StorageSetting';
import { useSettingsViewStyles } from '../SettingsView';
import { Dropdown } from '../../common/components/Dropdown';
import { VaultLockTimeout } from '../models/AppContext';
import { GenericSettingWrapper } from '../components/GenericSettingWrapper';
import { AccountSetting } from '../components/AccountSetting';
import { CommonButton } from '../../common/components/CommonButton';
import { NativeSettingsDataModel } from '../dataHandler/NativeSettingsDataModel';
import { useSettingsStyles } from '../themes/settingsStyles';
import { useCommonStyles } from '../../common/themes/ICommonTheme';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';

const nativeSettingsDataModel: NativeSettingsDataModel = new NativeSettingsDataModel();

/**
 * Debug display
 * to test components
 */
export function SettingsDebugView(): JSX.Element {
  const settingsViewStyles = useSettingsViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { metadata, useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();
  const [isHighContrast, setHighContrast] = React.useState(AppTheme.isHighContrast);
  const [highContrastColors, setHighContrastColors] = React.useState(AppTheme.currentHighContrastColors);

  React.useEffect(() => {
    const subscription = AppTheme.addListener('highContrastChanged', () => {
      setHighContrast(AppTheme.isHighContrast);
      setHighContrastColors(AppTheme.currentHighContrastColors);
    });
    return () => subscription.remove();
  });

  /**
   * As a one-time effect, call into the Native Data Model to test that we can fetch localized string resources
   * directly from the Sync Client. Simply log the result for now to aid with debugging.
   */
  const chooseFolders = 'sc://OpenSelectiveSync?source=Settings';

  const fetchAllStrings = async (): Promise<void> => {
    const localizedResourcesFromSyncClient = await nativeSettingsDataModel.fetchAllResourceStrings();
    console.log(localizedResourcesFromSyncClient);
  };

  const handleAction = async (): Promise<void> => {
    await onAction(chooseFolders);
  };

  //  Bug #1171160 - change to use context resources when ready
  if (appContext.resourceStrings && appContext.settings && metadata) {
    return (
      <View>
        <Text style={settingsStyles.title}>Debug</Text>

        <CommonButton buttonName='bidi' onPress={() => I18nManager.forceRTL(!I18nManager.isRTL)} />

        {isHighContrast ? <Text>High Contrast Enabled</Text> : <Text>High Contrast Disabled</Text>}
        <Text style={commonStyles.bodyText}>{JSON.stringify(highContrastColors)}</Text>

        {/**Components to test requests made by Native Data Model*/}
        <Button
          onPress={handleAction}
          title='Choose Folders'
          color='#841584'
          accessibilityLabel='Learn more about choose folders'
        />
        <Button
          onPress={fetchAllStrings}
          title='Load Strings'
          color='#841584'
          accessibilityLabel='Learn more about strings'
        />

        {/** Demonstration of Storage Setting */}
        <StorageSetting
          storageUsedInBytes={metadata.quota.storageUsedInBytes}
          storageTotalInBytes={metadata.quota.storageTotalInBytes}
          quotaState={metadata.quota.quotaState}
          storageName={useString(StringIDWin.IDS_SETTINGS_GENERAL_STORAGE_SECTION)}
          buttonName={useString(StringIDWin.IDS_SETTINGS_GET_MORE_STORAGE_BUTTON)}
          quotaUsageLabel={appContext.resourceStrings.quotaUsageLabel}
          quotaBarAccessibilityLabel={appContext.resourceStrings.quotaBarAccessibilityLabel}
          buttonAccessibilityLabel={appContext.resourceStrings.quotaAccessibilityLabel}
          onPress={async () => {
            if (appContext.settings) {
              onAction((appContext.settings.getMoreStorageLink as ILinkState).actionParameter);
            }
          }}
        />
        <NetworkSetting
          name='Limit download rates'
          itemNames={['Adjust automatically', 'Limit to:']}
          linkText='learn more'
          onClick={() => {}}
          onChangeInput={() => {}}
          onLinkPress={() => {}}
          selectedItem={1}
          currentValue={125}
          checked={true}
          disabled={true}
          radioButtonDisabled={true}
          onToggle={() => {}}
          units={useString(StringIDWin.IDS_KILOBYTE_PER_SECOND_ABBREVIATION)}
          showAutoOption={true}
          radioPanelToggleLabel={useString(StringIDWin.IDS_SETTINGS_RADIO_PANEL_TOGGLE_LABEL)}
          networkSettingTextboxHint={useString(StringIDWin.IDS_SETTINGS_NETWORK_SETTING_TEXTBOX_HINT)}
          radioButtonHint={useString(StringIDWin.IDS_SETTINGS_RADIO_BUTTON_HINT)}
          toggleOnLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_ON)}
          toggleOffLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_OFF)}
        />
        <GenericSettingWrapper name='Lock Personal Vault after:' description={undefined}>
          <Dropdown
            itemValues={[
              [VaultLockTimeout.TwentyMinutes, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_20MINUTES)],
              [VaultLockTimeout.OneHour, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_1HOUR)],
              [VaultLockTimeout.TwoHours, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_2HOURS)],
              [VaultLockTimeout.FourHours, useString(StringIDWin.IDS_SETTINGS_ACCOUNT_VAULT_4HOURS)]
            ]}
            selectedItem={VaultLockTimeout.TwentyMinutes}
            onClick={() => {}}
          />
        </GenericSettingWrapper>
        <AccountSetting />
      </View>
    );
  } else {
    return (
      <View style={settingsViewStyles.loadingView}>
        <ActivityIndicator size='large' />
      </View>
    );
  }
}
