import { ISystemHighContrastTheme } from '../../common/themes/themes';
import { ICommonTheme } from './ICommonTheme';
import { ThemeName } from './themeUtils';

export function createCommonHighContrastTheme(systemTheme: ISystemHighContrastTheme): ICommonTheme {
  return {
    themeName: ThemeName.HighContrast,
    bodyTextColor: systemTheme.WindowTextColor,
    button: {
      buttonColor: systemTheme.ButtonFaceColor,
      buttonColorPress: systemTheme.HighlightTextColor,
      buttonColorDisabled: systemTheme.WindowColor,
      buttonColorHover: systemTheme.HighlightTextColor,
      buttonTextColor: systemTheme.ButtonTextColor,
      buttonTextHover: systemTheme.HighlightColor,
      buttonTextPressed: systemTheme.HighlightColor,
      buttonTextDisabled: systemTheme.GrayTextColor,
      buttonBorderColor: systemTheme.ButtonTextColor,
      buttonBorderHover: systemTheme.HighlightColor,
      buttonBorderPress: systemTheme.WindowColor
    },
    link: {
      linkTextColor: systemTheme.HotlightColor,
      linkHover: systemTheme.WindowTextColor,
      linkPressed: systemTheme.HotlightColor,
      linkHoverBackgroundColor: systemTheme.HighlightColor
    },
    dropdown: {
      dropdownBackgroundColor: systemTheme.WindowColor,
      dropdownBackgroundColorHover: systemTheme.WindowColor
    },
    expander: {
      expanderBackgroundPressed: systemTheme.HighlightColor,
      expanderBackgroundHover: systemTheme.HighlightColor,
      expanderDisabled: systemTheme.GrayTextColor,
      expanderHoveringPressed: systemTheme.HighlightTextColor
    },
    radioButton: {
      radioButtonFill: systemTheme.ButtonTextColor,
      radioButtonInnerFill: systemTheme.HighlightColor,
      radioButtonHover: systemTheme.HighlightTextColor,
      radioButtonHoverInnerFill: systemTheme.ButtonTextColor,
      radioButtonBorder: systemTheme.ButtonTextColor,
      radioButtonNormal: systemTheme.WindowColor,
      radioButtonSelected: systemTheme.HighlightTextColor,
      radioButtonBorderSelected: systemTheme.HighlightColor,
      radioButtonSelectedHover: systemTheme.WindowColor,
      radioButtonBorderSelectedHover: systemTheme.ButtonTextColor,
      radioButtonBorderHover: systemTheme.HighlightColor,
      radioButtonPressed: systemTheme.HighlightTextColor,
      radioButtonInnerPressed: systemTheme.HighlightColor,
      radioButtonInnerSelectedPressed: systemTheme.ButtonTextColor,
      radioButtonSelectedPressed: systemTheme.WindowColor,
      radioButtonBorderPressed: systemTheme.HighlightTextColor,
      radioButtonDisabled: systemTheme.WindowColor,
      radioButtonInnerDisabled: systemTheme.GrayTextColor,
      radioButtonSelectedDisabled: systemTheme.WindowColor,
      radioButtonBorderSelectedDisabled: systemTheme.GrayTextColor,
      radioButtonBorderDisabled: systemTheme.GrayTextColor
    },
    textInput: {
      textInputBorderColor: systemTheme.WindowTextColor,
      textInputBackgroundColor: systemTheme.WindowColor,
      textInputTextColor: systemTheme.WindowTextColor,
      textInputDisabledBackgroundColor: systemTheme.WindowColor,
      textInputDisabledTextColor: systemTheme.GrayTextColor,
      textInputBorderFocusedColor: systemTheme.HighlightColor,
      textInputFocusBackgroundColor: systemTheme.WindowColor
    },
    primaryButton: {
      backgroundColor: systemTheme.ButtonFaceColor,
      textColor: systemTheme.ButtonTextColor,
      disabledBackgroundColor: systemTheme.WindowColor,
      disabledTextColor: systemTheme.GrayTextColor,
      buttonHoverBorderColor: systemTheme.HighlightColor,
      buttonTextHoverColor: systemTheme.HighlightColor,
      buttonHoverColor: systemTheme.HighlightTextColor,
      buttonBorderColor: systemTheme.ButtonTextColor,
      disabledButtonBorderColor: systemTheme.GrayTextColor
    },
    dialog: {
      backgroundColor: systemTheme.WindowColor,
      dialogBorderColor: systemTheme.WindowTextColor,
      buttonBackgroundColor: systemTheme.WindowColor
    }
  };
}
