import * as React from 'react';
import { View, Text, ViewStyle, TextStyle } from 'react-native';
import { CommonPressable } from './CommonPressable';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';
import { ICommonTheme, commonThemeManager } from '../themes/ICommonTheme';
import { IKeyboardEvent } from 'react-native-windows';

/**
 * Defines props of RadioButton component.
 */
export interface IRadioButtonProps {
  /**
   * Visual text label for this radio button
   */
  name: string;
  /**
   * Handler for when this radio button is clicked or activated by keyboard
   */
  onClick: (number: number) => void;
  /**
   * Whether this radio button is currently selected
   */
  isSelected: boolean;
  /**
   * The ID For this radio button in the radio group
   */
  id: number;
  /**
   * The accessibility hint for this radio button
   */
  radioButtonHint: string;
  /**
   * Whether this radio button is disabled
   */
  disabled: boolean;
  /**
   * Any additional elements to include after the visual label of this radio button (Optional)
   */
  children?: React.ReactNode;
  /**
   * Ref to use for this component (Optional)
   */
  innerRef: React.RefObject<View>;
  /**
   * Custom handler for key presses on this component (Optional)
   */
  onKeyDown: (event: IKeyboardEvent) => void;
}

interface IRadioButtonStyles {
  container: ViewStyle;
  outerCircle: ViewStyle;
  outerCircleColor: ViewStyle;
  outerCircleBorder: ViewStyle;
  selectedOuterCircle: ViewStyle;
  selectedOuterCircleColor: ViewStyle;
  selectedOuterCircleBorder: ViewStyle;
  innerCircleBase: ViewStyle;
  innerCircle: ViewStyle;
  innerCircleHover: ViewStyle;
  innerCircleDisabled: ViewStyle;
  innerCirclePressed: ViewStyle;
  innerCircleSelectedPressed: ViewStyle;
  textContainer: TextStyle;
  outerCircleHover: ViewStyle;
  outerCircleBorderHover: ViewStyle;
  selectedOuterCircleHover: ViewStyle;
  selectedOuterCircleBorderHover: ViewStyle;
  outerCirclePressed: ViewStyle;
  outerCircleBorderPressed: ViewStyle;
  selectedOuterCirclePressed: ViewStyle;
  selectedOuterCircleBorderPressed: ViewStyle;
  outerCircleDisabled: ViewStyle;
  outerCircleBorderDisabled: ViewStyle;
  selectedOuterCircleDisabled: ViewStyle;
  selectedOuterCircleBorderDisabled: ViewStyle;
}

const useRadioButtonStyles: ThemedStylesHook<IRadioButtonStyles> = createThemedStylesHook<
  ICommonTheme,
  IRadioButtonStyles
>(commonThemeManager, (theme: ICommonTheme) => {
  return {
    container: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center'
    },
    outerCircle: {
      height: 18,
      width: 18,
      borderRadius: 12,
      borderWidth: 1,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center'
    },
    outerCircleColor: {
      backgroundColor: theme.radioButton.radioButtonNormal
    },
    outerCircleBorder: {
      borderColor: theme.radioButton.radioButtonBorder
    },
    selectedOuterCircle: {
      height: 18,
      width: 18,
      borderRadius: 12,
      borderWidth: 1,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center'
    },
    selectedOuterCircleColor: {
      backgroundColor: theme.radioButton.radioButtonSelected
    },
    selectedOuterCircleBorder: {
      borderColor: theme.radioButton.radioButtonBorderSelected
    },
    innerCircleBase: {
      borderRadius: 999
    },
    innerCircle: {
      height: 8,
      width: 8,
      backgroundColor: theme.radioButton.radioButtonInnerFill
    },
    innerCircleHover: {
      height: 10,
      width: 10,
      backgroundColor: theme.radioButton.radioButtonHoverInnerFill
    },
    innerCircleDisabled: {
      height: 8,
      width: 8,
      backgroundColor: theme.radioButton.radioButtonInnerDisabled
    },
    innerCirclePressed: {
      height: 6,
      width: 6,
      backgroundColor: theme.radioButton.radioButtonInnerPressed
    },
    innerCircleSelectedPressed: {
      height: 6,
      width: 6,
      backgroundColor: theme.radioButton.radioButtonInnerSelectedPressed
    },
    selectedInnerCircle: {
      height: 8,
      width: 8,
      backgroundColor: theme.radioButton.radioButtonInnerFill
    },
    textContainer: {
      marginLeft: 10
    },
    outerCircleHover: {
      backgroundColor: theme.radioButton.radioButtonHover
    },
    outerCircleBorderHover: {
      borderColor: theme.radioButton.radioButtonBorderHover
    },
    selectedOuterCircleHover: {
      backgroundColor: theme.radioButton.radioButtonSelectedHover
    },
    selectedOuterCircleBorderHover: {
      borderColor: theme.radioButton.radioButtonBorderSelectedHover
    },
    outerCirclePressed: {
      backgroundColor: theme.radioButton.radioButtonPressed
    },
    outerCircleBorderPressed: {
      borderColor: theme.radioButton.radioButtonBorderPressed
    },
    selectedOuterCirclePressed: {
      backgroundColor: theme.radioButton.radioButtonSelectedPressed
    },
    selectedOuterCircleBorderPressed: {
      borderColor: theme.radioButton.radioButtonSelectedPressed
    },
    outerCircleDisabled: {
      backgroundColor: theme.radioButton.radioButtonDisabled
    },
    outerCircleBorderDisabled: {
      borderColor: theme.radioButton.radioButtonBorderDisabled
    },
    selectedOuterCircleDisabled: {
      backgroundColor: theme.radioButton.radioButtonSelectedDisabled
    },
    selectedOuterCircleBorderDisabled: {
      borderColor: theme.radioButton.radioButtonBorderSelectedDisabled
    }
  };
});
export function RadioButton(props: IRadioButtonProps): JSX.Element {
  const { onClick: propsOnClick, id, name, isSelected, disabled, children, innerRef, onKeyDown } = props;
  const styles = useRadioButtonStyles();
  const onClick = (): void => {
    propsOnClick(id);
  };

  return (
    <CommonPressable
      onPress={onClick}
      accessibilityLabel={name}
      accessibilityRole='radio'
      accessibilityState={{ selected: isSelected, disabled: disabled }}
      disabled={disabled}
      innerRef={innerRef}
      onKeyDown={onKeyDown}
    >
      {(isPressed, isHovering) => {
        let outerCircleStyle = styles.outerCircleColor;
        let outerCircleBorderStyle = styles.outerCircleBorder;
        let selectedOuterCircleStyle = styles.selectedOuterCircleColor;
        let selectedOuterCircleBorderStyle = styles.selectedOuterCircleBorder;
        let innerCircleStyle = styles.innerCircle;
        if (disabled) {
          outerCircleStyle = styles.outerCircleDisabled;
          outerCircleBorderStyle = styles.outerCircleBorderDisabled;
          innerCircleStyle = styles.innerCircleDisabled;
          selectedOuterCircleStyle = styles.selectedOuterCircleDisabled;
          selectedOuterCircleBorderStyle = styles.selectedOuterCircleBorderDisabled;
        } else if (isPressed) {
          outerCircleStyle = styles.outerCirclePressed;
          outerCircleBorderStyle = styles.outerCircleBorderPressed;
          innerCircleStyle = isSelected ? styles.innerCircleSelectedPressed : styles.innerCirclePressed;
          selectedOuterCircleStyle = styles.selectedOuterCirclePressed;
          selectedOuterCircleBorderStyle = styles.selectedOuterCircleBorderPressed;
        } else if (isHovering) {
          outerCircleStyle = styles.outerCircleHover;
          outerCircleBorderStyle = styles.outerCircleBorderHover;
          innerCircleStyle = styles.innerCircleHover;
          selectedOuterCircleStyle = styles.selectedOuterCircleHover;
          selectedOuterCircleBorderStyle = styles.selectedOuterCircleBorderHover;
        }
        return (
          <View style={styles.container}>
            <>
              <View
                style={
                  isSelected
                    ? [styles.selectedOuterCircle, selectedOuterCircleStyle, selectedOuterCircleBorderStyle]
                    : [styles.outerCircle, outerCircleStyle, outerCircleBorderStyle]
                }
              >
                {isSelected || isPressed ? (
                  <View style={[styles.innerCircleBase, innerCircleStyle]} />
                ) : (
                  <View />
                )}
              </View>
              <View>
                <Text style={styles.textContainer}>{name}</Text>
              </View>
              {children}
            </>
          </View>
        );
      }}
    </CommonPressable>
  );
}
