import React from 'react';
import { View, Text, ViewStyle, Linking } from 'react-native';
import { Link } from '../../common/components/Link';
import { useGlobalStyles } from '../themes/globalStyles';
import { IKFMTheme, themeManager } from '../themes/IKFMTheme';
import { StringIDWin } from '../../common/resources/resources';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { CommonContext } from '../../common/loader/models/CommonContext';

// line height for the Header, used for custom lineHeight
// orinaly set to 20, but for Header reduced to base 15,
// which other 5 with textScale ratio will be applied to have
// smaller line height compared to Info Area Text
const LINEHEIGHT = 15;

interface IKFMHeaderStyles {
  marginTop: ViewStyle;
  paddingBottom: ViewStyle;
}

interface IHeaderProps {
  title: string | undefined;
  subtext: string | undefined;
  link: string | undefined;
  linkText: string | undefined;
}

/* Create header specific styles using the theme hook. 
The theme hook allows us to use the theme colors for our styles. */

const useKFMHeaderStyles: ThemedStylesHook<IKFMHeaderStyles> = createThemedStylesHook<
  IKFMTheme,
  IKFMHeaderStyles
>(themeManager, (theme: IKFMTheme) => {
  return {
    marginTop: {
      marginTop: 9
    },
    paddingBottom: {
      paddingBottom: 3
    }
  };
});

export const Header = (props: IHeaderProps): JSX.Element => {
  const { subtext, title, link, linkText } = props;
  const styles = useKFMHeaderStyles();
  const globalStyles = useGlobalStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const newLineHeight = LINEHEIGHT + 5 * commonContext.textScale;
  return (
    <View style={styles.paddingBottom}>
      <Text testID='kfmMoveHeaderTitle' style={[globalStyles.title, { lineHeight: newLineHeight }]}>
        {title}
      </Text>
      <Text
        testID='kfmMoveHeaderSubText'
        style={[globalStyles.bodyText, styles.marginTop, { lineHeight: newLineHeight }]}
      >
        {subtext}
      </Text>
      {linkText && link ? (
        <Link
          testID='learn-more'
          accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_MANAGE_BACKUP) + '. ' + linkText}
          accessibilityRole='link'
          inlineLink
          onPress={() => Linking.openURL(link)}
          text={linkText}
        />
      ) : null}
    </View>
  );
};
