import * as React from 'react';
import { ViewStyle, View, Text, TextStyle, Image } from 'react-native';
import { AppContext, ILinkState } from '../models/AppContext';
import { useSettingsStyles } from '../themes/settingsStyles';
import { Link } from '../../common/components/Link';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { useCommonStyles } from '../../common/themes/ICommonTheme';
import { StringIDWin } from '../../common/resources/resources';
import { Dialog, dialogDismissHandler } from '../../common/components/Dialog';
import { DialogButtons } from '../../kfmMoveView/models/AppContext';

// Define types for Account Styles
interface IAccountStyles {
  accountContainer: ViewStyle;
  circleContainer: ViewStyle;
  textContainer: ViewStyle;
  nameStyle: TextStyle;
  emailStyle: TextStyle;
  initialStyle: TextStyle;
  headerLink: ViewStyle;
  linkContainer: ViewStyle;
}

const CIRCLE_SIZE = 96;

// Create Account styles.
const useAccountStyles: ThemedStylesHook<IAccountStyles> = createThemedStylesHook<ITheme, IAccountStyles>(
  themeManager,
  (theme: ITheme) => {
    return {
      accountContainer: {
        flex: 1,
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'flex-start'
      },
      circleContainer: {
        width: CIRCLE_SIZE,
        height: CIRCLE_SIZE,
        borderRadius: CIRCLE_SIZE / 2,
        borderWidth: 1,
        borderColor: 'rgba(0, 0, 0, 0.06)',
        backgroundColor: 'rgba(0, 0, 0, 0.1)',
        justifyContent: 'center',
        alignItems: 'center',
        marginRight: 23
      },
      textContainer: {
        alignItems: 'flex-start',
        marginTop: 10,
        marginRight: 7,
        flex: 1
      },
      nameStyle: {
        marginBottom: 2
      },
      emailStyle: {
        marginBottom: 10
      },
      initialStyle: {
        fontSize: 40,
        fontWeight: '600'
      },
      headerLink: {
        marginLeft: -6
      },
      linkContainer: {
        flexDirection: 'row'
      }
    };
  }
);

/**
 * Creates account setting.
 * @param {Interface} IAccountProps
 */
export function AccountSetting(): JSX.Element | null {
  const styles = useAccountStyles();
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();

  const commonContext = React.useContext(CommonContext);
  const { metadata, useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);

  if (!metadata || !appContext.settings || !appContext.resourceStrings) {
    return null;
  }

  const [isDialogOpen, setIsDialogOpen] = React.useState<boolean>(false);
  const [isDialogUserClosed, setIsDialogUserClosed] = React.useState<boolean>(false);

  const { displayName, email } = metadata.account;

  const onPress = (): void => {
    setIsDialogUserClosed(false);
    setIsDialogOpen(true);
  };

  const userCloseDialog = (): void => {
    setIsDialogUserClosed(true);
    setIsDialogOpen(false);
  };

  // Displays profile picture.
  const displayProfilePicture = (profilePictreByte: string): JSX.Element => {
    return (
      <Image
        style={{
          height: CIRCLE_SIZE - 2,
          width: CIRCLE_SIZE - 2,
          borderRadius: (CIRCLE_SIZE - 2) / 2
        }}
        source={{
          uri: profilePictreByte
        }}
      />
    );
  };

  let profilePictureView = (
    <View
      accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_ACCOUNT_PROFILE_PICTURE_LABEL)}
      accessibilityRole='image'
      style={styles.circleContainer}
    >
      {/** By default display profile picture.*/}
      {<Image source={require('../assets/images/profile.png')} />}
    </View>
  );

  let accountInfo = null;
  if (metadata.account.isSignedIn) {
    if (appContext.auth) {
      const isProfile = appContext.auth!.profilePicture !== '' && appContext.auth!.profilePicture !== null;
      profilePictureView = (
        <View
          accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_ACCOUNT_PROFILE_PICTURE_LABEL)}
          accessibilityRole='image'
          style={styles.circleContainer}
        >
          {/** If profile picture link is given, display profile picture.*/}
          {isProfile ? (
            displayProfilePicture(appContext.auth!.profilePicture)
          ) : (
            <Image source={require('../assets/images/profile.png')} />
          )}
        </View>
      );
    }
    accountInfo = (
      <View style={styles.textContainer}>
        <Text style={[styles.nameStyle, settingsStyles.bodyStrong]}>{displayName}</Text>
        <Text style={[styles.emailStyle, commonStyles.bodyText]} testID='emailText'>
          {email}
        </Text>
        <View style={styles.linkContainer}>
          <Link
            customStyles={[styles.headerLink]}
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_UNLINK_WO_TAGS)}
            text={useString(StringIDWin.IDS_SETTINGS_SYNC_UNLINK_WO_TAGS)}
            onPress={onPress}
          />
          {appContext.settings.privacySettingsLink ? (
            <Link
              customStyles={[styles.headerLink, { marginLeft: 0 }]}
              accessibilityLabel={useString(StringIDWin.IDS_DATA_PRIVACY_SETTINGS_WO_TAGS)}
              text={useString(StringIDWin.IDS_DATA_PRIVACY_SETTINGS_WO_TAGS)}
              onPress={() => {
                onAction((appContext.settings!.privacySettingsLink as ILinkState).actionParameter);
              }}
            />
          ) : null}
        </View>
      </View>
    );
  } else {
    accountInfo = (
      <View style={styles.textContainer}>
        <Text style={[styles.emailStyle, commonStyles.bodyText]}>
          {useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_QUOTA_STATUS_NO_ACCOUNT)}
        </Text>
        <View style={styles.linkContainer}>
          <Link
            customStyles={[styles.headerLink]}
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_UNLINK_WO_TAGS)}
            text={useString(StringIDWin.IDS_SETTINGS_SYNC_UNLINK_WO_TAGS)}
            onPress={onPress}
          />
          {appContext.settings.privacySettingsLink ? (
            <Link
              customStyles={[styles.headerLink, { marginLeft: 0 }]}
              accessibilityLabel={useString(StringIDWin.IDS_DATA_PRIVACY_SETTINGS_WO_TAGS)}
              text={useString(StringIDWin.IDS_DATA_PRIVACY_SETTINGS_WO_TAGS)}
              onPress={() => {
                onAction((appContext.settings!.privacySettingsLink as ILinkState).actionParameter);
              }}
            />
          ) : null}
        </View>
      </View>
    );
  }

  return (
    <View style={styles.accountContainer}>
      {profilePictureView}
      {accountInfo}
      <Dialog
        buttonOneText={useString(StringIDWin.IDS_SETTINGS_UNLINK_CONFIRM_DIALOG_OK)}
        buttonOneClick={() => {
          onAction((appContext.settings!.unlinkAccountAction as ILinkState).actionParameter);
          userCloseDialog();
        }}
        primary={DialogButtons.First}
        buttonTwoText={useString(StringIDWin.IDS_COMMON_CANCEL_BUTTON)}
        buttonTwoClick={userCloseDialog}
        header={useString(StringIDWin.IDS_SETTINGS_UNLINK_CONFIRM_DIALOG_PRIMARY)}
        body={
          <Text style={commonStyles.bodyText}>{appContext.resourceStrings!.unlinkConfirmDialogBody}</Text>
        }
        isOpen={isDialogOpen}
        horizontalOffset={450}
        onDismiss={() => dialogDismissHandler(setIsDialogOpen, isDialogUserClosed)}
      />
    </View>
  );
}
