import { createThemedStylesHook, ThemedStylesHook, ThemeManager } from './themes';
import { ThemeName } from './themeUtils';
import { CommonLightTheme } from './CommonLightTheme';
import { CommonDarkTheme } from './CommonDarkTheme';
import { createCommonHighContrastTheme } from './CommonHighContrastTheme';
import { TextStyle, OpaqueColorValue } from 'react-native';

export interface ICommonTheme {
  themeName: ThemeName;
  bodyTextColor: string;
  button: {
    buttonColor: string;
    buttonColorPress: string;
    buttonColorDisabled: string;
    buttonColorHover: string;
    buttonTextColor: string;
    buttonTextHover: string;
    buttonTextPressed: string;
    buttonTextDisabled: string;
    buttonBorderColor: string;
    buttonBorderHover: string;
    buttonBorderPress: string;
  };
  link: {
    linkTextColor: OpaqueColorValue | string;
    linkHover: OpaqueColorValue | string;
    linkPressed: OpaqueColorValue | string;
    linkHoverBackgroundColor: string;
  };
  dropdown: {
    dropdownBackgroundColor: string;
    dropdownBackgroundColorHover: string;
  };
  expander: {
    expanderBackgroundPressed: string;
    expanderBackgroundHover: string;
    expanderDisabled: string;
    expanderHoveringPressed: string;
  };
  radioButton: {
    radioButtonFill: string;
    radioButtonInnerFill: string;
    radioButtonHover: string;
    radioButtonHoverInnerFill: string;
    radioButtonBorder: string;
    radioButtonNormal: string;
    radioButtonSelected: string;
    radioButtonBorderSelected: string;
    radioButtonSelectedHover: string;
    radioButtonBorderSelectedHover: string;
    radioButtonBorderHover: string;
    radioButtonPressed: string;
    radioButtonInnerPressed: string;
    radioButtonInnerSelectedPressed: string;
    radioButtonSelectedPressed: string;
    radioButtonBorderPressed: string;
    radioButtonDisabled: string;
    radioButtonInnerDisabled: string;
    radioButtonSelectedDisabled: string;
    radioButtonBorderSelectedDisabled: string;
    radioButtonBorderDisabled: string;
  };
  textInput: {
    textInputBorderColor: string;
    textInputBackgroundColor: string;
    textInputTextColor: string;
    textInputDisabledBackgroundColor: string;
    textInputDisabledTextColor: string;
    textInputBorderFocusedColor: string;
    textInputFocusBackgroundColor: string;
  };
  primaryButton: {
    backgroundColor: string;
    textColor: string;
    disabledTextColor: string;
    disabledBackgroundColor: string;
    buttonHoverColor: string;
    buttonHoverBorderColor: string;
    buttonTextHoverColor: string;
    buttonBorderColor: string;
    disabledButtonBorderColor: string;
  };
  dialog: {
    backgroundColor: string;
    dialogBorderColor: string;
    buttonBackgroundColor: string;
  };
}

export const commonThemeManager: ThemeManager<ICommonTheme> = new ThemeManager(
  CommonLightTheme,
  CommonDarkTheme,
  createCommonHighContrastTheme
);

export interface ICommonStyles {
  bodyText: TextStyle;
  buttonText: TextStyle;
  linkText: TextStyle;
  inlineLink: TextStyle;
  inlineLinkText: TextStyle;
}

export const useCommonStyles: ThemedStylesHook<ICommonStyles> = createThemedStylesHook<
  ICommonTheme,
  ICommonStyles
>(commonThemeManager, (theme: ICommonTheme) => {
  // Font constants
  const bodyText = 14;
  const normal = '400';
  const defaultFontFamily = 'Segoe UI';

  // NOTE: Do NOT customize line height, as this will make the "Text size" Windows setting not work
  // as well - multiline text may be crammed together.

  return {
    bodyText: {
      fontWeight: normal,
      fontSize: bodyText,
      color: theme.bodyTextColor,
      fontFamily: defaultFontFamily
    },
    buttonText: {
      fontWeight: normal,
      fontSize: bodyText,
      alignSelf: 'center',
      textAlign: 'center',
      paddingTop: 4,
      paddingBottom: 4,
      marginLeft: 11,
      marginRight: 11,
      fontFamily: defaultFontFamily
    },
    linkText: {
      fontWeight: normal,
      fontSize: bodyText,
      fontFamily: defaultFontFamily
    },
    inlineLink: {
      color: theme.link.linkTextColor
    },
    inlineLinkText: {
      fontSize: bodyText
    }
  };
});
