import { LightTheme } from './LightTheme';
import { ThemeManager } from '../../common/themes/themes';
import { DarkTheme } from './DarkTheme';
import { createHighContrastTheme } from './HighContrast';
import { ICommonTheme } from '../../common/themes/ICommonTheme';

// These colors should be named more generically as the app evolves
export interface IKFMTheme extends ICommonTheme {
  borderColor: string;
  secondaryTextColor: string;
  frameBackgroundColor: string;
  innerFrameBackgroundColor: string;
  storageBackgroundColor: string;
  storageBorderColor: string;
  secondaryButtonColor: string;
  secondaryButtonText: string;
  criticalQuotaBackgroundColor: string;
  bottomFrameBackgroundColor: string;
  errorTextColor: string;
}

// Define themes for this app
export const themeManager: ThemeManager<IKFMTheme> = new ThemeManager(
  LightTheme,
  DarkTheme,
  createHighContrastTheme
);
