import React from 'react';
import { MESSAGE_EVENT_TYPE, NativeDataHandler } from '../common/dataHandler/NativeDataHandler';

interface IDebugViewMessageHandlerProps {
  dataHandler: NativeDataHandler;
}

export function DebugViewMessageHandler(props: IDebugViewMessageHandlerProps): JSX.Element | null {
  React.useLayoutEffect(() => {
    const callback = (serializedResult: string): void => {
      const result = JSON.parse(serializedResult);

      // Simply log the message type and value for now.
      console.log('Received Message Type: ' + result.messageType);
      console.log('Received Message Value: ' + result.value);
    };

    // On initial render, add our message event listener callback handler.
    props.dataHandler.addEventListener(MESSAGE_EVENT_TYPE, callback);

    return () => {
      // On cleanup for this component, remove the message event listener callback handler.
      props.dataHandler.removeEventListener(MESSAGE_EVENT_TYPE);
    };
  }, []);

  return null;
}
