import React from 'react';
import { View, ViewStyle } from 'react-native';
import { IKFMTheme, themeManager } from '../themes/IKFMTheme';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { AppContext, DialogStateEnum, Variants } from '../models/AppContext';
import { CommonButton } from '../../common/components/CommonButton';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';

interface ISyncKFMViewStyles {
  container: ViewStyle;
}

// Sync page styling specifications
const useSyncKFMViewStyles: ThemedStylesHook<ISyncKFMViewStyles> = createThemedStylesHook<
  IKFMTheme,
  ISyncKFMViewStyles
>(themeManager, (theme: IKFMTheme) => {
  return {
    container: {
      width: '100%',
      flexDirection: 'row',
      justifyContent: 'space-between',
      alignItems: 'center',
      paddingRight: 24,
      paddingLeft: 32
    }
  };
});

export const UserActionArea = (): JSX.Element => {
  const appContext = React.useContext(AppContext);
  const styles = useSyncKFMViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const { dialogState, canSync, canTryAgain } = appContext;
  const isBlocking =
    appContext.spaceUsedInfo?.isSpaceBlocking ||
    dialogState?.kfmCurrentDialogState === DialogStateEnum.Blocked;
  const isLoading =
    dialogState &&
    [DialogStateEnum.Scanning, DialogStateEnum.Migrating].includes(dialogState.kfmCurrentDialogState);
  const buttonName =
    dialogState?.kfmCurrentDialogState === DialogStateEnum.Migrating
      ? useString(StringIDWin.IDS_KFM_WINDOW_FOLDER_STATUS_SYNCING)
      : dialogState?.isOpenedThroughSettings
      ? useString(StringIDWin.IDS_KFM_WINDOW_SAVE_CHANGE_TEXT)
      : useString(StringIDWin.IDS_KFM_WINDOW_START_SYNC_TEXT);
  const isDisabled = isLoading || isBlocking || !canSync;

  const handleStartSync = (): void => {
    appContext.onStartMove();
  };

  const handleSkipBackup = (): void => {
    appContext.onSkipBackup();
  };

  const handleRetryKFMScan = (): void => {
    appContext.onTryAgain();
  };

  return (
    <View style={styles.container}>
      {canTryAgain ? (
        <CommonButton
          testID={'tryAgainButton'}
          width={257}
          buttonName={useString(StringIDWin.IDS_ERROR_PAGE_TRY_AGAIN_BUTTON)}
          onPress={handleRetryKFMScan}
        />
      ) : (
        <CommonButton
          width={257}
          buttonName={useString(StringIDWin.IDS_KFM_WINDOW_SKIP_BACKUP_TEXT)}
          onPress={handleSkipBackup}
        />
      )}
      <CommonButton
        width={257}
        buttonName={buttonName || ''}
        onPress={handleStartSync}
        variant={Variants.Primary}
        disabled={isDisabled}
        accessibilityState={{ disabled: isDisabled }}
        testID={'startSyncButton'}
      />
    </View>
  );
};
