import * as React from 'react';
import {
  ViewStyle,
  Text,
  View,
  TextStyle,
  ImageStyle,
  Image,
  ImageSourcePropType,
  AccessibilityState
} from 'react-native';
import { CommonPressable } from './CommonPressable';
import { commonThemeManager, ICommonTheme, useCommonStyles } from '../themes/ICommonTheme';
import { createThemedStylesHook, ThemedStylesHook } from '../themes/themes';
import { ThemeName } from '../themes/themeUtils';
import { Variants } from '../../kfmMoveView/models/AppContext';

/**
 * Defines types for Button styles.
 */
interface IButtonStyles {
  buttonStyle: ViewStyle;
  buttonHover: ViewStyle;
  buttonPress: ViewStyle;
  buttonDisabled: ViewStyle;
  buttonTextColorDefault: TextStyle;
  buttonTextColorPressed: TextStyle;
  buttonTextColorDisabled: TextStyle;
  buttonTextColorHovering: TextStyle;
  manageButtonContainer: ViewStyle;
  iconBase: ImageStyle;
  iconTintColor: ImageStyle;
  iconTintColorPressed: ImageStyle;
  iconTintColorDisabled: ImageStyle;
  iconTintColorHovering: ImageStyle;
  primaryButtonStyle: ViewStyle;
  primaryButtonText: TextStyle;
  primaryButtonHover: TextStyle;
  disabledPrimaryButton: ViewStyle;
  disabledPrimaryButtonText: TextStyle;
  primaryButtonTextColorHovering: TextStyle;
  buttonDropshadowNormal: ViewStyle;
  buttonDropshadowPressed: ViewStyle;
}

/**
 * Creates Button styles.
 */
const useButtonStyles: ThemedStylesHook<IButtonStyles> = createThemedStylesHook<ICommonTheme, IButtonStyles>(
  commonThemeManager,
  (theme: ICommonTheme) => {
    return {
      buttonStyle: {
        backgroundColor: theme.button.buttonColor,
        borderColor: theme.button.buttonBorderColor,
        borderRadius: 3,
        borderWidth: 1,
        alignSelf: 'center',
        alignItems: 'center',
        flexDirection: 'row',
        justifyContent: 'space-evenly'
      },
      buttonHover: {
        backgroundColor: theme.button.buttonColorHover,
        borderColor: theme.button.buttonBorderHover
      },
      buttonPress: {
        backgroundColor: theme.button.buttonColorPress,
        borderColor: theme.button.buttonBorderPress
      },
      buttonDisabled: {
        backgroundColor: theme.button.buttonColorDisabled
      },
      iconBase: {
        width: 16,
        height: 16
      },
      buttonTextColorDefault: {
        color: theme.button.buttonTextColor
      },
      iconTintColor: {
        tintColor: theme.button.buttonTextColor
      },
      buttonTextColorPressed: {
        color: theme.button.buttonTextPressed
      },
      iconTintColorPressed: {
        tintColor: theme.button.buttonTextPressed
      },
      buttonTextColorDisabled: {
        color: theme.button.buttonTextDisabled
      },
      iconTintColorDisabled: {
        tintColor: theme.button.buttonTextDisabled
      },
      buttonTextColorHovering: {
        color: theme.button.buttonTextHover
      },
      iconTintColorHovering: {
        tintColor: theme.button.buttonTextHover
      },
      manageButtonContainer: {
        marginRight: 10
      },
      primaryButtonStyle: {
        backgroundColor: theme.primaryButton.backgroundColor,
        borderColor: theme.primaryButton.buttonBorderColor,
        borderWidth: 1
      },
      primaryButtonHover: {
        opacity: theme.themeName === ThemeName.HighContrast ? 1 : 0.8,
        backgroundColor: theme.primaryButton.buttonHoverColor,
        borderColor: theme.primaryButton.buttonHoverBorderColor
      },
      primaryButtonText: {
        color: theme.primaryButton.textColor
      },
      disabledPrimaryButton: {
        backgroundColor: theme.primaryButton.disabledBackgroundColor,
        borderColor: theme.primaryButton.disabledButtonBorderColor
      },
      disabledPrimaryButtonText: {
        color: theme.primaryButton.disabledTextColor
      },
      primaryButtonTextColorHovering: {
        color: theme.primaryButton.buttonTextHoverColor
      },
      buttonDropshadowNormal: {
        borderBottomWidth: 2
      },
      buttonDropshadowPressed: {
        borderBottomWidth: 1
      }
    };
  }
);

/**
 * Defines props of CommonButton component.
 */
export interface IButtonProps {
  /**
   * Text to be displayed on button
   */
  buttonName: string;
  /**
   * Callback function passed to the onPress handler
   */
  onPress: () => void;
  /**
   * The asset to use as an icon to the left of the text.
   * Example: icon={require('../assets/myImage.png')}
   */
  icon?: ImageSourcePropType;
  /**
   * Custom image styling for the icon (Optional)
   */
  customIconStyle?: ImageStyle;
  /**
   * Whether or not the button should be disabled (Optional)
   */
  disabled?: boolean;
  /**
   * Custom width (Optional)
   */
  width?: number;
  /**
   * Custom height (Optional)
   */
  height?: number;
  /**
   * Button variant "primary" or "default"
   * If no variant is provided, default button styles are applied
   * Primary variant is the button with a blue background
   * Default is a button with plain background
   */
  variant?: Variants;
  /**
   * Standard children prop: accepts any valid React Node (Optional)
   * Contents appear in front of the built-in Text component
   */
  children?: React.ReactNode;
  /**
   * Custom accessibiility label for this button (Optional)
   */
  accessibilityLabel?: string;
  /**
   * Custom accessibiility hint for this button (Optional)
   */
  accessibilityHint?: string;
  /**
   * Accessibility state for the button (optional)
   */
  accessibilityState?: AccessibilityState;
  /**
   * Enable the accessibilityLiveRegion prop on the button's text. This will have the screen reader
   * announce any text changes on the button. Value should be either "assertive", "polite", or
   * "none". Assertive will interrupt anything being read when the button changes. Polite will wait
   * till the screen reader finishes whatever it's currently saying first. Defaults to "none".
   * (Optional)
   */
  accessibilityLiveRegion?: 'none' | 'polite' | 'assertive';
  /**
   * Custon tab index for this button (Optional)
   */
  tabIndex?: number;
  /**
   * Custom test ID string for this button to aid with automation (Optional)
   */
  testID?: string;
  /**
   * Ref to use for the top-level View (Optional)
   */
  innerRef?: React.RefObject<View>;
}

/**
 * Creates custom styled CommonPressable component.
 * @param {Interface} IButtonProps - The button name and onPress props of a button.
 */
export const CommonButton = (props: IButtonProps): JSX.Element => {
  const styles = useButtonStyles();
  const commonStyles = useCommonStyles();
  const {
    variant,
    disabled,
    height,
    width,
    icon,
    buttonName,
    customIconStyle,
    accessibilityHint,
    accessibilityLabel,
    accessibilityState,
    tabIndex,
    accessibilityLiveRegion,
    testID,
    innerRef,
    onPress
  } = props;
  const isPrimaryVariant = variant === Variants.Primary;
  const primaryStyles = isPrimaryVariant ? styles.primaryButtonStyle : {};

  const buttonStyle = {
    ...styles.buttonStyle,
    ...primaryStyles,
    minWidth: width || 118,
    width: width,
    minHeight: height || 30,
    maxWidth: width || 240
  };

  return (
    <CommonPressable
      onPress={onPress}
      accessibilityLabel={accessibilityLabel || buttonName}
      accessibilityRole='button'
      accessibilityHint={accessibilityHint}
      accessibilityState={accessibilityState}
      tabIndex={tabIndex}
      testID={testID}
      innerRef={innerRef}
      disabled={disabled}
    >
      {(isPressed, isHovering) => {
        let buttonColorModifier;
        let buttonShadowModifier = styles.buttonDropshadowNormal;
        let buttonTextColor = isPrimaryVariant ? styles.primaryButtonText : styles.buttonTextColorDefault;
        let iconTintColor = styles.iconTintColor;

        if (disabled) {
          if (isPrimaryVariant) {
            buttonColorModifier = styles.disabledPrimaryButton;
            buttonTextColor = styles.disabledPrimaryButtonText;
          } else {
            buttonColorModifier = styles.buttonDisabled;
            buttonTextColor = styles.buttonTextColorDisabled;
          }
          iconTintColor = styles.iconTintColorDisabled;
        } else if (isPressed) {
          buttonColorModifier = styles.buttonPress;
          buttonTextColor = styles.buttonTextColorPressed;
          iconTintColor = styles.iconTintColorPressed;
          buttonShadowModifier = styles.buttonDropshadowPressed;
        } else if (isHovering) {
          if (isPrimaryVariant) {
            buttonColorModifier = styles.primaryButtonHover;
            buttonTextColor = styles.primaryButtonTextColorHovering;
          } else {
            buttonColorModifier = styles.buttonHover;
            buttonTextColor = styles.buttonTextColorHovering;
          }
          iconTintColor = styles.iconTintColorHovering;
        }

        return (
          <View
            style={
              buttonColorModifier
                ? [buttonStyle, buttonColorModifier, buttonShadowModifier]
                : [buttonStyle, buttonShadowModifier]
            }
          >
            <>
              {icon && icon !== '' ? (
                <Image
                  style={
                    customIconStyle
                      ? ([styles.iconBase, iconTintColor, customIconStyle] as ImageStyle)
                      : ([styles.iconBase, iconTintColor] as ImageStyle)
                  }
                  source={icon}
                />
              ) : null}
              <Text
                accessibilityLiveRegion={accessibilityLiveRegion || 'none'}
                style={[commonStyles.buttonText, buttonTextColor, styles.manageButtonContainer]}
              >
                {buttonName}
              </Text>
            </>
          </View>
        );
      }}
    </CommonPressable>
  );
};
