import { IMetadata, IQoSTelemetryData } from '../models/CommonContext';
import { NativeDataHandler } from '../../../common/dataHandler/NativeDataHandler';
import { IDataHandlerSendOptions } from '../../../common/dataHandler/IDataHandler';
import { StringIDWin } from '../../resources/resources';

export interface IStringResponse {
  id: StringIDWin;
  str: string;
}

export class CommonContextDataModel {
  private _dataHandler: NativeDataHandler;

  public constructor() {
    this._dataHandler = new NativeDataHandler();
  }

  /**
   * Get all metadata values.
   */
  public async fetchAllMetadata(): Promise<IMetadata> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/RNCommon/metadata',
      requestType: 'GET'
    };
    return this._dataHandler.send(options);
  }

  /**
   * Get all resource string values for settings.
   * They are localized strings to be displayed in the U
   */
  public async fetchResourceStrings(stringsToLoad: StringIDWin[]): Promise<IStringResponse[]> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/RNCommon/strings',
      requestType: 'GET',
      postData: stringsToLoad
    };
    return this._dataHandler.send(options);
  }

  /**
   * Open new window and/or web browser handleAction()
   */
  public async handleAction(action: string, appKey: string): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/RNCommon/handleAction',
      requestType: 'POST',
      postData: {
        actionParameter: action,
        viewName: appKey
      }
    };
    return this._dataHandler.send(options);
  }

  /**
   * Log information from the view through the native logging API.
   */
  public async log(message: string, appKey: string): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/RNCommon/log',
      requestType: 'POST',
      postData: {
        message: message,
        viewName: appKey
      }
    };
    return this._dataHandler.send(options);
  }

  /**
   * Record a QoS Telemetry point through the native logging API.
   */
  public async recordQoSTelemetry(data: IQoSTelemetryData, appKey: string): Promise<void> {
    const options: IDataHandlerSendOptions = {
      url: 'http://localhost:9001/sync/RNCommon/recordQoSTelemetry',
      requestType: 'POST',
      postData: {
        viewName: appKey,
        ...data
      }
    };
    return this._dataHandler.send(options);
  }
}
