import * as React from 'react';
import { Pressable } from 'react-native';

/**
 * This component should only be used if CommonPressable doesn't work for your situation.
 * Adds hover support as a wrapper component.
 */
export function Hover(props: { children: (isHovering: boolean) => React.ReactNode }): JSX.Element {
  const [isHovering, setIsHovering] = React.useState(false);

  const onHoverIn = React.useCallback(() => {
    setIsHovering(true);
  }, []);

  const onHoverOut = React.useCallback(() => {
    setIsHovering(false);
  }, []);

  return (
    <Pressable
      {...{
        onHoverIn: onHoverIn,
        onHoverOut: onHoverOut,
        focusable: false
      }}
    >
      {props.children(isHovering)}
    </Pressable>
  );
}
