import React from 'react';
import { View, Text, Button } from 'react-native';
import { DebugViewMessageHandler } from './DebugViewMessageHandler';
import { CommonContext } from '../common/loader/models/CommonContext';
import { NativeDataHandler } from '../common/dataHandler/NativeDataHandler';

interface IHelloWorldResponse {
  statusCode: number;
  message: string;
}

export const DebugView: () => JSX.Element = () => {
  const dataHandler = React.useMemo(() => new NativeDataHandler(), []);

  const commonContext = React.useContext(CommonContext);

  const [response, setResponse] = React.useState<string>();

  const fetchHelloWorld = async (): Promise<void> => {
    try {
      const helloWorldResponse: IHelloWorldResponse = (await dataHandler.send({
        url: 'http://localhost:9001/sync/hello/worldjson',
        requestType: 'GET'
      })) as IHelloWorldResponse;
      setResponse(helloWorldResponse.message);
    } catch (error) {
      console.error('Failed to fetch from sync/hello/worldjson: ' + error);
      setResponse(JSON.stringify(error));
    }
  };

  // No data fetching needed on initialization so immediately signal that we are ready
  React.useEffect(() => {
    commonContext.onChildDataLoaded(false);
  }, []);

  return (
    <View>
      <DebugViewMessageHandler dataHandler={dataHandler} />
      <Text>Click the button to send a request to the C++ Router.</Text>
      <Button title='GET sync/hello/world' color='#f194ff' onPress={fetchHelloWorld} />
      <Text>Response: {response}</Text>
    </View>
  );
};
