import React from 'react';

/**
 * Declare type for appContextData properties
 */
export type Settings = ISettings | null;
export type ResourceStrings = IResourceStrings | null;
export type MountedFolderState = IMountedFolderState | null;
export type Auth = IAuth | null;

/**
 * IAppContextData is used to store resourceString, settings ,metadata, and auth value.
 * IAppContextData combines all hooks from Setting Context, Metadata Context, and Auth Context to update data model and components
 * As a result, SettingsView only needs AppContext Provider for components to consume and update.
 *
 * undefined means data are fetching/loading, null means the fetch functions failed.
 * the implementation is shown inside the SettingsView component's conditional statements
 */

export interface IAppContextData {
  resourceStrings: ResourceStrings;
  settings: Settings;
  mountedFolderState: MountedFolderState;
  auth: Auth;
  failedFetch: boolean;
  onToggleChange: (key: string, value: boolean) => void;
  onNetworkChange: (value: INetworkState) => void;
  onDropdownChange: (key: string, value: string) => void;
  onCatfoodChange: (value: string) => void;
  onStopSyncingMountedFolder: (scopeId: string) => void;
  onStartSyncingDefaultMountedFolder: () => void;
  onChooseFolders: (scopeId: string, libraryType: number) => void;
  onUpdateMountedFolderState: (updatedMountedFolderState: IMountedFolderState) => void;
  beginGetMountedFolders: () => void;
}

/**
 * Every callback function can be the same, simple, empty function to initialize the AppContext.
 * This is possible because none of the parameters for these callbacks are actually used in the stub implementation.
 */
const defaultContextCallback = (): void => {};

export const defaultAppContextData: IAppContextData = {
  resourceStrings: null,
  settings: null,
  mountedFolderState: null,
  auth: null,
  failedFetch: false,
  onToggleChange: defaultContextCallback,
  onNetworkChange: defaultContextCallback,
  onDropdownChange: defaultContextCallback,
  onCatfoodChange: defaultContextCallback,
  onStopSyncingMountedFolder: defaultContextCallback,
  onStartSyncingDefaultMountedFolder: defaultContextCallback,
  onChooseFolders: defaultContextCallback,
  onUpdateMountedFolderState: defaultContextCallback,
  beginGetMountedFolders: defaultContextCallback
};

export const AppContext: React.Context<IAppContextData> =
  React.createContext<IAppContextData>(defaultAppContextData);

export interface IResourceStrings {
  [key: string]: string;
}

/**  SettingContext data returned from data model FetchAllSettings()
 * The way interfaces are declared below is to ensure the contexts have the same data format as data model's.
 */

export interface IToggleState {
  checked: boolean;
  disabled: boolean;
}

export interface IFasterRingState extends IToggleState {
  showRingName: boolean;
  ringName: string;
  isPolicySet: boolean;
}

export interface INetworkState {
  limitDownload: boolean;
  limitDownloadDisabled: boolean;
  limitUpload: boolean;
  limitUploadDisabled: boolean;
  uploadRateAuto: boolean;
  uploadRateAutoDisabled: boolean;
  uploadLimit: number;
  downloadLimit: number;
}

export enum VaultLockTimeout {
  TwentyMinutes = 'TwentyMinutes',
  OneHour = 'OneHour',
  TwoHours = 'TwoHours',
  FourHours = 'FourHours'
}

export interface IButtonState {
  disabled: boolean;
  actionParameter: string;
}

export interface ILinkState {
  actionParameter: string;
}

export interface IDropDownState {
  selection: string;
}

export interface IMountedFolderState {
  /**
   * Denotes the primary mounted folder's scope ID.
   * i.e. the user's ODB folder
   */
  primaryScopeId: string;

  /**
   * Contains a special action parameter string used to unlink the user's account.
   */
  unlinkFolderActionParameter: string;

  /**
   * Contains a list of all mounted folder information needed to render the list of
   * Mounted Folder UI elements.
   */
  mountedFolderList: IMountedFolder[];
}

/**
 * Strings associated with each mounted folder UI item are stored in Settings instead of ResourceStrings,
 * since they each rely on information specific to their respective mounted folder.
 */
export interface IMountedFolder {
  scopeId: string;
  libraryType: number;
  folderName: string;
  folderDescription: string;
  stopSyncConfirmHeader: string;
  stopSyncConfirmBody: string;
  unlinkConfirmBody: string;
}

export type ISettingState =
  | IToggleState
  | INetworkState
  | IButtonState
  | ILinkState
  | IDropDownState
  | IFasterRingState;

export interface ISettings {
  [key: string]: ISettingState;
}

// Type guards for ISettingState's inner types
export function isIToggleState(value: ISettingState): value is IToggleState {
  return value ? 'checked' in value : false;
}

export function isINetworkState(value: ISettingState): value is INetworkState {
  return value ? 'uploadLimit' in value : false;
}

export function isIDropDownState(value: ISettingState): value is IDropDownState {
  return value ? 'selection' in value : false;
}

export interface IAuth {
  authToken: string;
  profilePicture: string;
}
