import React from 'react';
import { View, Text, ActivityIndicator, Linking, ViewStyle, TextStyle } from 'react-native';
import { AppContext, ILinkState, INetworkState, IToggleState } from '../models/AppContext';
import { useSettingsViewStyles } from '../SettingsView';
import { CommonButton } from '../../common/components/CommonButton';
import { GenericSettingWrapper } from '../components/GenericSettingWrapper';
import { ToggleAdapter } from '../components/ToggleAdapter';
import { NetworkSetting } from '../components/NetworkSetting';
import { settingIDs } from '../models/SettingIDs';
import { Link } from '../../common/components/Link';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { Footer } from '../components/Footer';
import { useSettingsStyles } from '../themes/settingsStyles';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';
import { Dialog, dialogDismissHandler } from '../../common/components/Dialog';
import { DialogButtons } from '../../kfmMoveView/models/AppContext';
import { useCommonStyles } from '../../common/themes/ICommonTheme';

// additional styling for network settings
interface ISyncViewStyles {
  container: ViewStyle;
  network: ViewStyle;
  networkContainer: ViewStyle;
  sameLine: ViewStyle;
  linkMargin: TextStyle;
  learnMoreLinkMargin: ViewStyle;
  linkSubHeaderMargin: ViewStyle;
  expanderTextContainer: ViewStyle;
  expanderComponentContainer: ViewStyle;
  expanderContentContainer: ViewStyle;
  expanderOuterContainer: ViewStyle;
}

// Sync page styling specifications
const useSyncViewStyles: ThemedStylesHook<ISyncViewStyles> = createThemedStylesHook<ITheme, ISyncViewStyles>(
  themeManager,
  (theme: ITheme) => {
    return {
      container: {
        paddingBottom: 40
      },
      headerText: {
        color: theme.bodyTextColor,
        fontWeight: '500',
        fontSize: 28,
        lineHeight: 36
      },
      subheaderText: {
        marginBottom: 12,
        color: theme.bodyTextColor,
        fontSize: 14,
        fontWeight: '500',
        lineHeight: 20
      },
      network: {
        marginBottom: 7
      },
      networkContainer: {
        marginBottom: 43
      },
      sameLine: {
        flexDirection: 'row'
      },
      linkMargin: {
        marginRight: 15
      },
      learnMoreLinkMargin: {
        marginTop: -6
      },
      linkSubHeaderMargin: {
        marginBottom: 6
      },
      expanderTextContainer: {
        flex: 1,
        minHeight: 20,
        marginLeft: 5,
        maxWidth: 392
      },
      expanderComponentContainer: {
        display: 'flex',
        justifyContent: 'space-between',
        paddingRight: 12
      },
      expanderContentContainer: {
        display: 'flex',
        minHeight: 62,
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingTop: 13,
        paddingBottom: 13
      },
      expanderOuterContainer: {
        position: 'relative',
        flex: 1
      }
    };
  }
);

// SyncView display
export function SyncView(): JSX.Element {
  const styles = useSyncViewStyles();
  const settingsViewStyles = useSettingsViewStyles();
  const commonContext = React.useContext(CommonContext);
  const { metadata, useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);
  const settingsStyles = useSettingsStyles();
  const commonStyles = useCommonStyles();
  // toggle Advanced Settings display
  const [showAdvanced, setShowAdvanced] = React.useState(false);

  // toggle dialog visibility state
  const [isDialogOpen, setIsDialogOpen] = React.useState<boolean>(false);
  const [isDialogUserClosed, setIsDialogUserClosed] = React.useState<boolean>(false);
  /**
   * given the selected radio button, which will be retrieved
   * at the component level, return the corresponding boolean
   * to update the data layer
   * @param index number of radio button
   * @returns corresponding boolean
   */
  const isAutoRateLimit = (index: number): boolean => {
    return index === 0;
  };

  /**
   * given whether the rate is automatically determined, as fetched from the Data Model, return
   * the corresponding radio button to display correctly
   * @param autoRate boolean
   * @returns number of corresponding radio button
   */
  const getSelectedNetworkItem = (autoRate: boolean): number => {
    return autoRate ? 0 : 1;
  };

  // check for null or undefined ResourceStrings and Settings and Metadata
  if (appContext.resourceStrings && appContext.settings && metadata) {
    let savePhotosSetting = null;
    let saveScreenshotsSetting = null;
    let advancedSettingsSection = null;
    if (metadata.account.isSignedIn) {
      const networkState = appContext.settings!.network as INetworkState;
      const uploadSelectedItem = getSelectedNetworkItem(networkState.uploadRateAuto);
      let advancedSettings = null;
      if (showAdvanced) {
        const fondToggleState = appContext.settings![settingIDs.filesOnDemandId] as IToggleState;
        const fondAlertToggleHandler = (): void => {
          setIsDialogUserClosed(false);
          setIsDialogOpen(true);
        };

        const userCloseDialog = (): void => {
          setIsDialogUserClosed(true);
          setIsDialogOpen(false);
        };

        const getFileCollaborationExpanderContent = (isParentOn: boolean): JSX.Element => {
          return (
            <View style={styles.expanderOuterContainer}>
              <View style={styles.expanderContentContainer}>
                <View style={styles.expanderTextContainer}>
                  <View style={settingsStyles.fullWidth}>
                    <Text style={commonStyles.bodyText}>
                      {useString(StringIDWin.IDS_SETTINGS_OFFICE_SYNC_CONFLICTS_NAME)}
                    </Text>
                  </View>
                  <Text style={settingsStyles.caption}>
                    {useString(StringIDWin.IDS_SETTINGS_SYNC_CONFLICT_DESCRIPTION)}
                  </Text>
                </View>
                <View style={styles.expanderComponentContainer}>
                  <ToggleAdapter
                    accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_OFFICE_SYNC_CONFLICTS_NAME)}
                    accessibilityHint={useString(StringIDWin.IDS_SETTINGS_SYNC_CONFLICT_DESCRIPTION)}
                    settingId={settingIDs.syncConflictId}
                    disabled={!isParentOn}
                  />
                </View>
              </View>
            </View>
          );
        };

        advancedSettings = (
          <View>
            {appContext.settings.fileCollaboration ? (
              <GenericSettingWrapper
                hasExpander={appContext.settings?.syncConflict ? true : false}
                name={useString(StringIDWin.IDS_SETTINGS_OFFICE_COLLAB_BOX)}
                description={useString(StringIDWin.IDS_SETTINGS_OFFICE_COLLAB_TEXT)}
                link={(appContext.settings.fileCollabLearnMoreLink as ILinkState).actionParameter}
                expanderContent={getFileCollaborationExpanderContent(
                  (appContext.settings![settingIDs.fileCollaborationId] as IToggleState).checked
                )}
              >
                <ToggleAdapter
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_OFFICE_COLLAB_BOX)}
                  accessibilityHint={useString(StringIDWin.IDS_SETTINGS_OFFICE_COLLAB_TEXT)}
                  settingId={settingIDs.fileCollaborationId}
                />
              </GenericSettingWrapper>
            ) : null}
            {appContext.settings.filesOnDemand ? (
              <GenericSettingWrapper
                name={useString(StringIDWin.IDS_SETTINGS_GENERAL_PLACEHOLDERS_BOX)}
                description={useString(StringIDWin.IDS_SETTINGS_SYNC_PLACEHOLDERS_OPTIN)}
                link={(appContext.settings.fondLearnMoreLink as ILinkState).actionParameter}
              >
                <ToggleAdapter
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_GENERAL_PLACEHOLDERS_BOX)}
                  accessibilityHint={useString(StringIDWin.IDS_SETTINGS_SYNC_PLACEHOLDERS_OPTIN)}
                  customOnToggle={fondToggleState.checked ? fondAlertToggleHandler : undefined}
                  settingId={settingIDs.filesOnDemandId}
                />
              </GenericSettingWrapper>
            ) : null}
            <View style={styles.network}>
              <NetworkSetting
                name={useString(StringIDWin.IDS_SETTINGS_LIMIT_NETWORK_DOWNLOAD_BOX)}
                itemNames={[
                  useString(StringIDWin.IDS_SETTINGS_NETWORK_AUTO_LIMIT_TEXT),
                  useString(StringIDWin.IDS_SETTINGS_NETWORK_CUSTOM_LIMIT_TEXT)
                ]}
                onChangeInput={(index: number) => {
                  networkState.downloadLimit = index;
                  appContext.onNetworkChange(networkState);
                }}
                currentValue={networkState.downloadLimit}
                checked={networkState.limitDownload}
                disabled={networkState.limitDownloadDisabled}
                onToggle={() => {
                  networkState.limitDownload = !networkState.limitDownload;
                  appContext.onNetworkChange(networkState);
                }}
                showAutoOption={false}
                units={useString(StringIDWin.IDS_KILOBYTE_PER_SECOND_ABBREVIATION)}
                radioPanelToggleLabel={useString(StringIDWin.IDS_SETTINGS_LIMIT_NETWORK_DOWNLOAD_BOX)}
                networkSettingTextboxHint={useString(StringIDWin.IDS_SETTINGS_NETWORK_SETTING_TEXTBOX_HINT)}
                radioButtonHint={useString(StringIDWin.IDS_SETTINGS_RADIO_BUTTON_HINT)}
                toggleOnLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_ON)}
                toggleOffLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_OFF)}
                linkText={useString(StringIDWin.IDS_SETTINGS_LEARN_MORE_TEXT)}
                onLinkPress={() =>
                  Linking.openURL(
                    appContext.settings
                      ? (appContext.settings.limitLearnMoreLink as ILinkState).actionParameter
                      : ''
                  )
                }
                showGroupPolicyDescription={networkState.limitDownloadDisabled}
              />
            </View>
            <View style={styles.networkContainer}>
              <View style={styles.network}>
                <NetworkSetting
                  name={useString(StringIDWin.IDS_SETTINGS_LIMIT_NETWORK_UPLOAD_BOX)}
                  itemNames={[
                    useString(StringIDWin.IDS_SETTINGS_NETWORK_AUTO_LIMIT_TEXT),
                    useString(StringIDWin.IDS_SETTINGS_NETWORK_CUSTOM_LIMIT_TEXT)
                  ]}
                  linkText={useString(StringIDWin.IDS_SETTINGS_LEARN_MORE_TEXT)}
                  onClick={(radioIndex: number) => {
                    networkState.uploadRateAuto = isAutoRateLimit(radioIndex);
                    appContext.onNetworkChange(networkState);
                  }}
                  onChangeInput={(index: number) => {
                    networkState.uploadLimit = index;
                    appContext.onNetworkChange(networkState);
                  }}
                  onLinkPress={() =>
                    Linking.openURL(
                      appContext.settings
                        ? (appContext.settings.limitLearnMoreLink as ILinkState).actionParameter
                        : ''
                    )
                  }
                  selectedItem={uploadSelectedItem}
                  radioButtonDisabled={
                    networkState.limitUploadDisabled || networkState.uploadRateAutoDisabled
                  }
                  currentValue={networkState.uploadLimit}
                  checked={networkState.limitUpload}
                  disabled={networkState.limitUploadDisabled}
                  onToggle={() => {
                    networkState.limitUpload = !networkState.limitUpload;
                    networkState.uploadRateAuto = false;
                    appContext.onNetworkChange(networkState);
                  }}
                  showAutoOption={true}
                  units={useString(StringIDWin.IDS_KILOBYTE_PER_SECOND_ABBREVIATION)}
                  radioPanelToggleLabel={useString(StringIDWin.IDS_SETTINGS_LIMIT_NETWORK_UPLOAD_BOX)}
                  networkSettingTextboxHint={useString(StringIDWin.IDS_SETTINGS_NETWORK_SETTING_TEXTBOX_HINT)}
                  radioButtonHint={useString(StringIDWin.IDS_SETTINGS_RADIO_BUTTON_HINT)}
                  toggleOnLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_ON)}
                  toggleOffLabel={useString(StringIDWin.IDS_SETTINGS_TOGGLE_OFF)}
                  showGroupPolicyDescription={
                    networkState.limitUploadDisabled || networkState.uploadRateAutoDisabled
                  }
                />
              </View>
            </View>
            {appContext.settings.pinMountpoints && appContext.settings.unpinMountpoints ? (
              <View>
                <View style={styles.sameLine}>
                  <Text style={[settingsStyles.subheaderText, styles.linkMargin]}>
                    {useString(StringIDWin.IDS_SETTINGS_GENERAL_PLACEHOLDERS_BOX)}
                  </Text>
                  <Link
                    customStyles={[styles.learnMoreLinkMargin]}
                    accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_LEARN_MORE_TEXT)}
                    text={useString(StringIDWin.IDS_SETTINGS_LEARN_MORE_TEXT)}
                    onPress={async () => {
                      await onAction((appContext.settings!.fondLearnMoreLink as ILinkState).actionParameter);
                    }}
                  />
                </View>
                <GenericSettingWrapper name={useString(StringIDWin.IDS_CLOUD_FILES_UNPIN_OPTION_DESCRIPTION)}>
                  <CommonButton
                    buttonName={useString(StringIDWin.IDS_CLOUD_FILES_UNPIN_BUTTON_RN)}
                    onPress={() => {
                      onAction((appContext.settings!.unpinMountpoints as ILinkState).actionParameter);
                    }}
                  />
                </GenericSettingWrapper>
                <GenericSettingWrapper name={appContext.resourceStrings.placeHoldersPinName}>
                  <CommonButton
                    buttonName={useString(StringIDWin.IDS_CLOUD_FILES_PIN_BUTTON)}
                    onPress={() => {
                      onAction((appContext.settings!.pinMountpoints as ILinkState).actionParameter);
                    }}
                  />
                </GenericSettingWrapper>
              </View>
            ) : null}
            <Dialog
              buttonOneText={useString(StringIDWin.IDS_COMMON_OK_BUTTON)}
              buttonOneClick={() => {
                appContext.onToggleChange(settingIDs.filesOnDemandId, !fondToggleState.checked);
                userCloseDialog();
              }}
              primary={DialogButtons.First}
              buttonTwoText={useString(StringIDWin.IDS_COMMON_CANCEL_BUTTON)}
              buttonTwoClick={userCloseDialog}
              header={useString(StringIDWin.IDS_PLACEHOLDERS_OPT_OUT_PRIMARY)}
              body={
                <Text style={commonStyles.bodyText}>
                  {useString(StringIDWin.IDS_PLACEHOLDERS_OPT_OUT_WARNING)}
                </Text>
              }
              isOpen={isDialogOpen}
              horizontalOffset={450}
              onDismiss={() => dialogDismissHandler(setIsDialogOpen, isDialogUserClosed)}
            />
          </View>
        );
      }

      const onAdvancedDisplayToggled = (): void => {
        setShowAdvanced((showAdvanced) => !showAdvanced);
        commonContext.onLog('Advanced Sync Settings display toggled to: ' + !showAdvanced);
      };

      advancedSettingsSection = (
        <View style={settingsStyles.section}>
          <Link
            customStyles={[styles.linkSubHeaderMargin]}
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_ADVANCED_SETTINGS_SECTION)}
            accessibilityRole={'button'}
            accessibilityState={{ expanded: showAdvanced }}
            text={
              useString(StringIDWin.IDS_SETTINGS_SYNC_ADVANCED_SETTINGS_SECTION) +
              '  ' +
              (showAdvanced ? '\u23F6' : '\u23F7') // Up arrow if advanced settings shown, down arrow otherwise
            }
            onPress={onAdvancedDisplayToggled}
            testID={'advancedSettingsLink'}
          />
          {advancedSettings}
        </View>
      );

      if (appContext.settings[settingIDs.savePhotosId]) {
        savePhotosSetting = (
          <GenericSettingWrapper
            name={useString(StringIDWin.IDS_SETTINGS_AUTO_SAVE_PHOTOS_VIDEOS_NEW)}
            description={useString(StringIDWin.IDS_SETTINGS_AUTO_SAVE_IMPORT_CHECKBOX_NEW)}
          >
            <ToggleAdapter
              accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_AUTO_SAVE_PHOTOS_VIDEOS_NEW)}
              accessibilityHint={useString(StringIDWin.IDS_SETTINGS_AUTO_SAVE_IMPORT_CHECKBOX_NEW)}
              settingId={settingIDs.savePhotosId}
            />
          </GenericSettingWrapper>
        );
      }

      if (appContext.settings[settingIDs.saveScreenshotsId]) {
        saveScreenshotsSetting = (
          <GenericSettingWrapper
            name={useString(StringIDWin.IDS_SETTINGS_AUTO_SAVE_SCREENSHOTS_CHECKBOX_NEW)}
            description=''
          >
            <ToggleAdapter
              accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_AUTO_SAVE_SCREENSHOTS_CHECKBOX_NEW)}
              settingId={settingIDs.saveScreenshotsId}
            />
          </GenericSettingWrapper>
        );
      }
    }

    return (
      <View>
        <View style={styles.container}>
          <Text
            testID='syncHeader'
            style={settingsStyles.title}
            accessibilityRole='header'
            accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_GENERAL_SYNC_TAB)}
          >
            {useString(StringIDWin.IDS_SETTINGS_GENERAL_SYNC_TAB)}
          </Text>

          <View style={settingsStyles.section}>
            {appContext.settings.kfmBackupLink ? (
              <GenericSettingWrapper
                name={useString(StringIDWin.IDS_SETTINGS_SYNC_MANAGE_BACKUP)}
                description={useString(StringIDWin.IDS_SETTINGS_SYNC_BACKUP_TEXT)}
                link={(appContext.settings.kfmLearnMoreLink as ILinkState).actionParameter}
              >
                <CommonButton
                  buttonName={useString(StringIDWin.IDS_SETTINGS_BACKUP_MOVE_WINDOW_LAUNCH_BUTTON)}
                  accessibilityHint={useString(StringIDWin.IDS_SETTINGS_SYNC_BACKUP_TEXT)}
                  testID={'kfmBackupButton'}
                  onPress={() => {
                    onAction((appContext.settings!.kfmBackupLink as ILinkState).actionParameter);
                  }}
                />
              </GenericSettingWrapper>
            ) : null}

            {savePhotosSetting}

            {saveScreenshotsSetting}
          </View>

          <View style={settingsStyles.section}>
            <Text
              style={settingsStyles.subheaderText}
              accessibilityRole='header'
              accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_PREFERENCES_SECTION)}
            >
              {useString(StringIDWin.IDS_SETTINGS_SYNC_PREFERENCES_SECTION)}
            </Text>
            <GenericSettingWrapper
              name={useString(StringIDWin.IDS_SETTINGS_SYNC_START_AUTOMATICALLY)}
              description=''
            >
              <ToggleAdapter
                accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_START_AUTOMATICALLY)}
                settingId={settingIDs.openAtLoginId}
              />
            </GenericSettingWrapper>
            {appContext.settings.pauseLowBattery ? (
              <GenericSettingWrapper
                name={useString(StringIDWin.IDS_SETTINGS_SYNC_AUTO_PAUSE_BATTERY)}
                description=''
                showGroupPolicyDescription={
                  (appContext.settings![settingIDs.pauseLowBatteryId] as IToggleState).disabled
                }
              >
                <ToggleAdapter
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_AUTO_PAUSE_BATTERY)}
                  settingId={settingIDs.pauseLowBatteryId}
                />
              </GenericSettingWrapper>
            ) : null}
            {appContext.settings.pauseOnMetered ? (
              <GenericSettingWrapper
                name={useString(StringIDWin.IDS_SETTINGS_SYNC_AUTO_PAUSE_METERED)}
                description=''
                showGroupPolicyDescription={
                  (appContext.settings![settingIDs.pauseOnMeteredId] as IToggleState).disabled
                }
              >
                <ToggleAdapter
                  accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_SYNC_AUTO_PAUSE_METERED)}
                  settingId={settingIDs.pauseOnMeteredId}
                />
              </GenericSettingWrapper>
            ) : null}
          </View>

          {advancedSettingsSection}

          <Footer />
        </View>
      </View>
    );
  } else {
    return (
      <View style={settingsViewStyles.loadingView}>
        <ActivityIndicator size='large' />
      </View>
    );
  }
}
