import { ISystemHighContrastTheme } from '../../common/themes/themes';
import { IKFMTheme } from './IKFMTheme';
import { createCommonHighContrastTheme } from '../../common/themes/CommonHighContrastTheme';

/**
 * High Contrast themes come in from Windows dynamically as a limited color pallete,
 * this needs to be mapped to our ITheme type.
 */

// TO DO
// Update colors in TU for High contrast
export function createHighContrastTheme(systemTheme: ISystemHighContrastTheme): IKFMTheme {
  return {
    ...createCommonHighContrastTheme(systemTheme),
    innerFrameBackgroundColor: systemTheme.WindowColor,
    borderColor: systemTheme.WindowTextColor,
    storageBackgroundColor: systemTheme.WindowColor,
    storageBorderColor: systemTheme.HighlightColor,
    secondaryTextColor: systemTheme.WindowTextColor,
    frameBackgroundColor: systemTheme.WindowColor,
    secondaryButtonColor: systemTheme.ButtonTextColor,
    secondaryButtonText: systemTheme.ButtonTextColor,
    criticalQuotaBackgroundColor: systemTheme.WindowColor,
    bottomFrameBackgroundColor: systemTheme.WindowColor,
    errorTextColor: systemTheme.HighlightTextColor
  };
}
