import React from 'react';
import { View } from 'react-native';
import { IKeyboardEvent } from 'react-native-windows';

/**
 * Generates a function that can be passed to the onKeyDown listener. This will listen for 'Enter'
 * and call the given function with the given parameters.
 *
 * Examples of usage:
 *   const onKeyDown = createKeyDownActivation<[boolean]>(toggleSwitch, [!isEnabled]);
 *   const onKeyDown = createKeyDownActivation<[boolean, string]>(onPress, [!isEnabled, value]);
 *
 * @param onPress Function to be called when the key is pressed
 * @param params Parameters that need to be passed to the onPress function. Can be entered as any
 * number of parameters in an array (see example above). Optional.
 * @returns Function that can be passed to an onKeyDown listener
 */
export function createKeyDownActivation<T extends unknown[]>(
  onPress: ((...params: T) => void) | (() => void),
  keys: string[] = ['Enter', 'Space'],
  params?: T
): (event: IKeyboardEvent) => void {
  return (event: IKeyboardEvent): void => {
    if (keys.includes(event.nativeEvent.code)) {
      if (params) {
        onPress(...params);
      } else {
        onPress();
      }
    }
  };
}

/**
 * Interface used as the return value of useArrowKeys
 */
export interface IArrowKeyHandlers {
  tabRefs: React.RefObject<View>[];
  createHandleArrowKeys: (refIndex: number) => (event: IKeyboardEvent) => void;
}

/**
 * Used to implement arrow key navigation within a set of UI elements.
 *
 * @param numElements Number of elements that need to be navigated between with arrow keys
 * @returns {IArrowKeyHandlers} The refs to be given to each UI element and generator used to
 * create a handler for each element. The ref and generated handler should ultimately be passed to
 * the innerRef and onKeyDown properties of a CommonPressable, respectively.
 */
export function useArrowKeys(numElements: number): IArrowKeyHandlers {
  const tabRefs: React.RefObject<View>[] = [];

  const createHandleArrowKeys = (refIndex: number): ((event: IKeyboardEvent) => void) => {
    return (event: IKeyboardEvent): void => {
      if (event.nativeEvent.code === 'ArrowUp') {
        let prevTab = refIndex - 1;
        if (prevTab < 0) {
          // If we're at the first element, stay there
          prevTab = 0;
        } else {
          tabRefs[prevTab].current?.focus();
        }
      } else if (event.nativeEvent.code === 'ArrowDown') {
        let nextTab = refIndex + 1;
        if (nextTab > tabRefs.length - 1) {
          // If we're at the last element, stay there
          nextTab = tabRefs.length - 1;
        } else {
          tabRefs[nextTab].current?.focus();
        }
      }
    };
  };

  for (let i = 0; i < numElements; i++) {
    const tabRef = React.createRef<View>();
    tabRefs.push(tabRef);
  }

  return {
    tabRefs,
    createHandleArrowKeys
  };
}
