import React from 'react';
import { View, ViewStyle, Image, ImageStyle } from 'react-native';
import { AppContext, ILinkState } from '../models/AppContext';
import { Link } from '../../common/components/Link';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';

interface IFooterStyles {
  row: ViewStyle;
  topRow: ViewStyle;
  bottomRow: ViewStyle;
  icon: ImageStyle;
}

const useFooterStyles: ThemedStylesHook<IFooterStyles> = createThemedStylesHook<ITheme, IFooterStyles>(
  themeManager,
  (theme: ITheme) => {
    return {
      row: {
        flexDirection: 'row',
        alignItems: 'center'
      },
      topRow: {
        marginTop: 25,
        marginBottom: 4
      },
      bottomRow: {
        marginBottom: 20
      },
      icon: {
        width: 14,
        height: 14,
        marginLeft: 12,
        marginTop: 5,
        marginBottom: 3,
        tintColor: theme.bodyTextColor
      }
    };
  }
);

export function Footer(): JSX.Element {
  const commonContext = React.useContext(CommonContext);
  const { useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);
  const styles = useFooterStyles();

  const helpImage = (
    <Image style={styles.icon as ImageStyle} source={require('../assets/images/getHelp_light.png')} />
  );
  const feedbackImage = (
    <Image style={styles.icon as ImageStyle} source={require('../assets/images/giveFeedback_light.png')} />
  );

  return (
    <>
      <View style={[styles.row, styles.topRow]}>
        <Link
          accessibilityLabel={useString(StringIDWin.IDS_SETTINGS_HELP_GET_HELP_WO_TAGS)}
          text={useString(StringIDWin.IDS_SETTINGS_HELP_GET_HELP_WO_TAGS)}
          customContents={helpImage}
          onPress={() => {
            if (appContext.settings) {
              onAction((appContext.settings.getHelpLink as ILinkState).actionParameter);
            }
          }}
        />
      </View>
      <View style={[styles.row, styles.bottomRow]}>
        <Link
          accessibilityLabel={useString(StringIDWin.IDS_GIVE_FEEDBACK_TEXT)}
          text={useString(StringIDWin.IDS_GIVE_FEEDBACK_TEXT)}
          customContents={feedbackImage}
          onPress={() => {
            if (appContext.settings) {
              onAction((appContext.settings.sendFeedbackLink as ILinkState).actionParameter);
            }
          }}
        />
      </View>
    </>
  );
}
