import React, { ReactNode } from 'react';
import { Text, View, ViewStyle, Image, ImageStyle, StyleProp, ScrollView } from 'react-native';
import { useGlobalStyles } from '../themes/globalStyles';
import { IKFMTheme, themeManager } from '../themes/IKFMTheme';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { ThemeName } from '../../common/themes/themeUtils';

// line height for the InfoAreaText, used for custom lineHeight
// will be used for custom lineHeight along with textScale ratio
const LINEHEIGHT = 20;

interface IDialogErrorProps {
  isCritical: boolean;
  text: string;
  links: Array<ReactNode>;
}

interface IDialogErrorViewStyles {
  container: ViewStyle;
  errorContainer: ViewStyle;
  errorBody: ViewStyle;
  errorBackground: ViewStyle;
  infoBackground: ViewStyle;
  icon: StyleProp<ImageStyle>;
  textContainer: ViewStyle;
  linkAlignment: ViewStyle;
}

// Styles for quota section using theme colors from createThemedStylesHook
const useDialogErrorViewStyles: ThemedStylesHook<IDialogErrorViewStyles> = createThemedStylesHook<
  IKFMTheme,
  IDialogErrorViewStyles
>(themeManager, (theme: IKFMTheme) => {
  return {
    container: {
      display: 'flex',
      backgroundColor: theme.innerFrameBackgroundColor,
      flex: 1,
      flexDirection: 'row',
      justifyContent: 'center',
      alignItems: 'center',
      borderTopWidth: 1,
      paddingBottom: 1,
      paddingRight: 24,
      paddingLeft: 32,
      borderColor: theme.storageBorderColor
    },
    errorContainer: {
      display: 'flex',
      minHeight: '60%',
      maxHeight: '88%',
      paddingVertical: 10,
      paddingRight: 10,
      flexDirection: 'row',
      justifyContent: 'space-between',
      borderRadius: 3,
      width: '100%',
      borderWidth: 1,
      borderColor: theme.themeName === ThemeName.HighContrast ? theme.storageBorderColor : 'transparent'
    },
    errorBody: {
      flexDirection: 'row',
      flex: 1
    },
    errorBackground: {
      backgroundColor: theme.criticalQuotaBackgroundColor
    },
    infoBackground: {
      backgroundColor: theme.storageBackgroundColor
    },
    icon: {
      marginHorizontal: 10,
      height: 16,
      width: 16
    },
    textContainer: {
      display: 'flex',
      flexDirection: 'row',
      flex: 1,
      flexWrap: 'wrap'
    },
    linkAlignment: {
      flexDirection: 'row',
      justifyContent: 'space-evenly'
    }
  };
});

export const DialogErrorArea = (props: IDialogErrorProps): JSX.Element => {
  const styles = useDialogErrorViewStyles();
  const globalStyles = useGlobalStyles();
  const { isCritical, text, links } = props;
  const commonContext = React.useContext(CommonContext);
  const newLineHeight = LINEHEIGHT * commonContext.textScale;

  return (
    <View testID='dialogError' style={styles.container}>
      <View style={[styles.errorContainer, isCritical ? styles.errorBackground : styles.infoBackground]}>
        <ScrollView testID='dialogErrorAreaScrollView'>
          <View style={styles.errorBody}>
            <Image
              testID='errorAreaImage'
              style={[styles.icon as ImageStyle, { marginTop: newLineHeight / 2 - 7 }]}
              source={
                isCritical ? require('../assets/images/error.png') : require('../assets/images/info.png')
              }
            />
            <View style={styles.textContainer}>
              <Text testID='errorAreaBody' style={[globalStyles.bodyText, { lineHeight: newLineHeight }]}>
                {text}
              </Text>
              <View style={styles.linkAlignment}>{links.map((link) => link)}</View>
            </View>
          </View>
        </ScrollView>
      </View>
    </View>
  );
};
