import React from 'react';
import { FlatList, Text } from 'react-native';
import {
  AppContext,
  ConfirmDialogResult,
  DialogButtons,
  FolderState,
  FolderType,
  IFolder
} from '../models/AppContext';
import { KFMListItem } from './KFMListItem';
import { Dialog, dialogDismissHandler } from '../../common/components/Dialog';
import { useGlobalStyles } from '../themes/globalStyles';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';

export const KFMFolderList = (): JSX.Element => {
  const globalStyles = useGlobalStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const appContext = React.useContext(AppContext);
  const folders: Array<IFolder> = Object.values(appContext.folders || {});
  const [isDialogOpen, setIsDialogOpen] = React.useState<boolean>(false);
  const [isDialogUserClosed, setIsDialogUserClosed] = React.useState<boolean>(false);

  const handleDialogClosed = (result: ConfirmDialogResult): void => {
    appContext.confirmDialogClosed(result);
    setIsDialogUserClosed(true);
    setIsDialogOpen(false);
  };

  const handleFolderToggle = (folderType: FolderType, folderState: FolderState): void => {
    if (folderState === FolderState.DoneWithStop) {
      appContext.onStopFolderBackup(folderType);
      setIsDialogUserClosed(false);
      setIsDialogOpen(true);
    } else {
      appContext.onToggleFolder(folderType);
    }
  };

  if (!folders) return <></>;

  // We support a dynamic number of folders by adjusting the height of folders
  const folderListHeight = 252;
  const maxFolderHeight = 49;
  const minFolderHeight = 42;
  // Make sure the folders are not too small if there are many folders
  const lowerClampedHeight = Math.max(minFolderHeight, folderListHeight / folders.length);
  // Make sure the folders are not too big if there are few folders
  const folderHeight = Math.min(maxFolderHeight, lowerClampedHeight);
  const renderKFMRow = ({ item }: { item: IFolder }): JSX.Element => {
    return <KFMListItem item={item} height={folderHeight} onToggleFolder={handleFolderToggle} />;
  };

  return (
    <>
      <FlatList
        style={{ marginTop: 3 /* for padding of folder list from Header */ }}
        data={folders}
        renderItem={renderKFMRow}
        testID={'foldersList'}
      />

      {/**When a user tries to deselect a folder that is in a DoneWithStop state,
       * show a confirmation dialog that asks the user to confirm their intent to stop
       * the syncing of that folder.
       */}
      <Dialog
        isOpen={isDialogOpen}
        header={useString(StringIDWin.IDS_MOVE_WINDOW_LOSS_AVERSION_DIALOG_HEADER)}
        body={
          <Text style={globalStyles.bodyText}>
            {useString(StringIDWin.IDS_MOVE_WINDOW_LOSS_AVERSION_DIALOG_BODY)}
          </Text>
        }
        buttonOneClick={() => handleDialogClosed(ConfirmDialogResult.PrimaryButton)}
        buttonTwoClick={() => handleDialogClosed(ConfirmDialogResult.SecondaryButton)}
        buttonOneText={useString(StringIDWin.IDS_MOVE_WINDOW_LOSS_AVERSION_DIALOG_PRIMARY_BUTTON_TEXT)}
        buttonTwoText={useString(StringIDWin.IDS_MOVE_WINDOW_LOSS_AVERSION_DIALOG_SECONDARY_BUTTON_TEXT)}
        primary={DialogButtons.First}
        onDismiss={() => dialogDismissHandler(setIsDialogOpen, isDialogUserClosed)}
      />
    </>
  );
};
