import React from 'react';
import { Image, ImageSourcePropType, Text, TextStyle, View, ViewStyle, ImageStyle } from 'react-native';
import { ToggleSetting } from '../../common/components/ToggleSetting';
import { useGlobalStyles } from '../themes/globalStyles';
import { IKFMTheme, themeManager } from '../themes/IKFMTheme';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { AppContext, DialogStateEnum, FolderState, FolderType, IFolder } from '../models/AppContext';
import { getFolderStateString } from '../models/helpers';
import { CommonContext } from '../../common/loader/models/CommonContext';

interface IKFMItemViewStyles {
  container: ViewStyle;
  infoContainer: ViewStyle;
  nameContainer: ViewStyle;
  iconSize: ImageStyle;
  statusText: TextStyle;
  name: TextStyle;
  folderSize: TextStyle;
  scanningText: TextStyle;
  errorTextStyles: TextStyle;
}

// Create Item styles using createThemedStylesHook which allows us to apply theming
// to component styles.
const useKFMItemViewStyles: ThemedStylesHook<IKFMItemViewStyles> = createThemedStylesHook<
  IKFMTheme,
  IKFMItemViewStyles
>(themeManager, (theme: IKFMTheme) => {
  return {
    container: {
      display: 'flex',
      flex: 1,
      flexDirection: 'row',
      marginVertical: 1,
      paddingLeft: 11,
      borderWidth: 2,
      borderColor: theme.borderColor,
      borderRadius: 3
    },
    nameContainer: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      flex: 0.5
    },
    iconSize: {
      height: 25,
      width: 25
    },
    infoContainer: {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      flex: 1
    },
    statusText: {
      marginLeft: 11,
      flex: 1
    },
    scanningText: {
      flex: 1,
      textAlign: 'center'
    },
    folderSize: {
      textAlign: 'right',
      flex: 0.5
    },
    name: {
      marginLeft: 27
    },
    errorTextStyles: {
      color: theme.errorTextColor
    }
  };
});

/* eslint-disable @typescript-eslint/naming-convention */
const images: { [key: string]: ImageSourcePropType } = {
  '1': require('../assets/images/documents.png'),
  '2': require('../assets/images/pictures.png'),
  '3': require('../assets/images/desktop.png'),
  '6': require('../assets/images/music.png'),
  '8': require('../assets/images/videos.png'),
  '9': require('../assets/images/downloads.png')
};

const toggleTestID: { [key: string]: string } = {
  '1': 'documentsToggle',
  '2': 'picturesToggle',
  '3': 'desktopToggle',
  '6': 'musicToggle',
  '8': 'videosToggle',
  '9': 'downloadsToggle'
};

export const KFMListItem = ({
  item,
  height,
  onToggleFolder
}: {
  item: IFolder;
  height: number;
  onToggleFolder: (folderType: FolderType, folderState: FolderState) => void;
}): JSX.Element => {
  const styles = useKFMItemViewStyles();
  const globalStyles = useGlobalStyles();
  const commonContext = React.useContext(CommonContext);
  const { useString } = commonContext;
  const appContext = React.useContext(AppContext);
  const { dialogState } = appContext;
  const folderState: number = item.folderState;
  /*
    Set toggle to checked if the folder is in any of the three states:
    1. Selected
    2. Done
    3. DoneWithStop
  */
  const isFolderChecked = [FolderState.Selected, FolderState.Done, FolderState.DoneWithStop].includes(
    folderState
  );

  /* Attach a React state to the toggle state to optimistically accept value changes
    The toggle handler sets the toggle state to the new value optimistically, while waiting for the model to compute the next state
    A useEffect hook is also used to update the React state when the folder item prop changes.
   */
  const [isToggleChecked, setIsToggleChecked] = React.useState(isFolderChecked);
  React.useEffect(() => {
    setIsToggleChecked(isFolderChecked);
  }, [isFolderChecked]);
  const toggleSwitch = (val: boolean): void => {
    // Opting out of a synced folder will go through a confirmation dialog
    const isOptingOutSyncedFolder = !val && folderState === FolderState.DoneWithStop;
    if (!isOptingOutSyncedFolder) {
      setIsToggleChecked(val);
    }
    onToggleFolder(item.folderType, item.folderState);
  };

  const localizedFolderState: string = useString(getFolderStateString(item.folderState));
  /**
   * Disable the switch toggle if the state of the folder is:
   * 1. Blocked
   * 2. Done
   * 3. Error or
   * 4. Scanning
   *
   * Or if the dialog state is not ready for user input
   */

  const isToggleDisabled =
    [FolderState.Done, FolderState.Error, FolderState.Scanning].includes(folderState) ||
    dialogState?.kfmCurrentDialogState !== DialogStateEnum.UserInput;

  const errorTextStyles = folderState === FolderState.Error ? styles.errorTextStyles : {};

  if (folderState === FolderState.Blocked) {
    return <></>;
  } else {
    return (
      <View style={{ height }}>
        <View testID='kfmListItem' style={styles.container}>
          <View style={styles.nameContainer}>
            <Image
              testID='folderIcon'
              source={images[item.folderType]}
              style={styles.iconSize as ImageStyle}
            />
            <Text testID='folderName' style={[styles.name, globalStyles.bodyText, errorTextStyles]}>
              {item.folderName}
            </Text>
          </View>
          <View style={styles.infoContainer}>
            {folderState === FolderState.Scanning ? (
              <Text testID='scanningText' style={[styles.scanningText, globalStyles.infoText]}>
                {localizedFolderState}
              </Text>
            ) : (
              <Text testID='folderSize' style={[styles.folderSize, globalStyles.infoText, errorTextStyles]}>
                {item.folderSize}
              </Text>
            )}
            {folderState !== FolderState.Scanning ? (
              <Text testID='folderState' style={[styles.statusText, globalStyles.infoText, errorTextStyles]}>
                {localizedFolderState}
              </Text>
            ) : null}
          </View>
          <View
            style={{
              justifyContent: 'center',
              alignItems: 'flex-end',
              paddingRight: 30
            }}
          >
            <ToggleSetting
              testID={toggleTestID[item.folderType]}
              accessibilityLabel={item.folderName + '. ' + item.folderSize + '. ' + localizedFolderState}
              checked={isToggleChecked}
              onToggle={toggleSwitch}
              disabled={isToggleDisabled}
            />
          </View>
        </View>
      </View>
    );
  }
};
