import React from 'react';
import { View, TextStyle, ViewStyle, ActivityIndicator } from 'react-native';
import { Tab } from './Tab';
import { TabContext } from './TabContextManagement';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { TabLabel } from './RightPane';
import { StorageSetting } from './StorageSetting';
import { AppContext, ILinkState } from '../models/AppContext';
import { useSettingsViewStyles } from '../SettingsView';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';
import { useArrowKeys } from '../../common/components/accessibilityUtils';

// LeftNav styling interface
interface ILeftNavStyles {
  container: ViewStyle;
  tabs: TextStyle;
  storage: ViewStyle;
  storageText: TextStyle;
}

// LeftNav styling specifications
const useLeftNavStyles: ThemedStylesHook<ILeftNavStyles> = createThemedStylesHook<ITheme, ILeftNavStyles>(
  themeManager,
  (theme: ITheme) => {
    return {
      container: {
        flex: 1,
        flexDirection: 'column',
        justifyContent: 'space-between',
        marginLeft: 5
      },
      tabs: {},
      storage: {
        marginLeft: 11,
        marginBottom: 34
      },
      storageText: {
        fontWeight: '500'
      }
    };
  }
);

// LeftNav styling display
export const LeftNav = (): JSX.Element => {
  const styles = useLeftNavStyles();
  const settingsViewStyles = useSettingsViewStyles();

  const state = React.useContext(TabContext);
  const commonContext = React.useContext(CommonContext);
  const { metadata, useString, onAction } = commonContext;
  const appContext = React.useContext(AppContext);

  const tabDisplays = [];

  const { tabRefs, createHandleArrowKeys } = useArrowKeys(Object.keys(TabLabel).length);

  if (appContext.resourceStrings && appContext.settings && metadata) {
    // traverse through TabLabel enum
    let currentRefIndex = 0;
    for (const tab in TabLabel) {
      // filter unwanted properties
      if (typeof tab !== 'string') {
        continue;
      }

      // Do not display the Notifications tab if not signed-in.
      let disabled = false;
      if (!metadata.account.isSignedIn && tab === TabLabel.Notifications) {
        disabled = true;
      }

      // Only display the 'Debug' tab if using a debug build.
      if (!metadata.account.isDebugBuild && tab === TabLabel.Debug) {
        continue;
      }

      let tabIcon = undefined;

      // cast the string as an Enum type
      const tabEnum: TabLabel = TabLabel[tab as keyof typeof TabLabel];

      // update TabContext
      const onPress: () => void = () => {
        if (state.tab !== tabEnum) {
          commonContext.onLog('React Native Settings tab changed: ' + tabEnum);
        }
        state.setTab(tabEnum);
      };

      // check if tab is currently selected to add highlighting
      const isSelected = tabEnum === state.tab;

      // Keep track of localized name for tab, for display purposes
      let localizedTabName = useString(StringIDWin.IDS_SETTINGS_GENERAL_SYNC_TAB);

      // add corresponding icon
      switch (tabEnum) {
        case TabLabel.Sync:
          tabIcon = require('../assets/images/syncIcon.png');
          localizedTabName = useString(StringIDWin.IDS_SETTINGS_GENERAL_SYNC_TAB);
          break;
        case TabLabel.Account:
          tabIcon = require('../assets/images/accountIcon.png');
          localizedTabName = useString(StringIDWin.IDS_SETTINGS_SELECTIVE_SYNC_TAB);
          break;
        case TabLabel.Notifications:
          tabIcon = require('../assets/images/notificationsIcon.png');
          localizedTabName = useString(StringIDWin.IDS_SETTINGS_NOTIFICATION_TAB);
          break;
        case TabLabel.About:
          tabIcon = require('../assets/images/aboutIcon.png');
          localizedTabName = useString(StringIDWin.IDS_SETTINGS_HELP_TAB);
          break;
        case TabLabel.Debug:
          tabIcon = require('../assets/images/aboutIcon.png');
          localizedTabName = useString(StringIDWin.IDS_SETTINGS_DEBUG_TAB);
          break;
      }

      tabDisplays.push(
        <Tab
          key={tab}
          selected={isSelected}
          onPress={onPress}
          label={localizedTabName}
          icon={tabIcon}
          accessibilityHint={useString(StringIDWin.IDS_SETTINGS_TAB_HINT)}
          disabled={disabled}
          innerRef={tabRefs[currentRefIndex]}
          onKeyDown={createHandleArrowKeys(currentRefIndex)}
        />
      );

      currentRefIndex++;
    }

    // There should only ever be one of these 2 in the settings response from the sync client
    // "Get more storage" is for ODC, "Manage storage" is for ODC Premium
    let buttonName = '';
    let actionParameter = '';

    if (metadata.account.isSignedIn) {
      if (appContext.settings.getMoreStorageLink) {
        buttonName = useString(StringIDWin.IDS_SETTINGS_GET_MORE_STORAGE_BUTTON);
        actionParameter = (appContext.settings.getMoreStorageLink as ILinkState).actionParameter;
      } else if (appContext.settings.manageStorageLink) {
        buttonName = useString(StringIDWin.IDS_SETTINGS_MANAGE_STORAGE_BUTTON);
        actionParameter = (appContext.settings.manageStorageLink as ILinkState).actionParameter;
      }
    }

    return (
      <View style={styles.container}>
        <View style={styles.tabs}>{tabDisplays}</View>
        {metadata.account.isODB === false ? (
          <View style={styles.storage}>
            <StorageSetting
              storageUsedInBytes={metadata.quota.storageUsedInBytes}
              storageTotalInBytes={metadata.quota.storageTotalInBytes}
              quotaState={metadata.quota.quotaState}
              storageName={useString(StringIDWin.IDS_SETTINGS_GENERAL_STORAGE_SECTION)}
              buttonName={buttonName}
              quotaUsageLabel={appContext.resourceStrings.quotaUsageLabel}
              quotaBarAccessibilityLabel={appContext.resourceStrings.quotaBarAccessibilityLabel}
              buttonAccessibilityLabel={appContext.resourceStrings.quotaAccessibilityLabel}
              onPress={() => {
                if (actionParameter) {
                  onAction(actionParameter);
                }
              }}
            />
          </View>
        ) : null}
      </View>
    );
  } else {
    return (
      <View style={settingsViewStyles.loadingView}>
        <ActivityIndicator size='large' />
      </View>
    );
  }
};
