import React from 'react';

import { AppRegistry } from 'react-native';
import { CommonLoader } from './src/common/loader/CommonLoader';

// Register all possible React Native view components under a specific view name.
// Each view must have a unique name that corresponds with it.

// To add a new view, please follow these steps:
// 1. Create a sub-folder for the TypeScript code under the 'src' folder called '<name>View'
// 2. Add a base component to this folder called '<Name>View' that exports a React component of the same name.
// 3. Import this component here and register it: AppRegistry.registerComponent('<name>', () => <Name>View);

// To display a native window with this view, make the following call in Native C++:
//      ReactNativeViewProps viewProps = ReactNativeViewProps(
//          L"Example View" /*windowTitle*/,
//          L"<name>" /*viewName*/,
//          400 /*width*/,
//          400 /*height*/,
//          RNViewStyle::StandardWindow /*style*/);
//      GetHost()->GetHostedComponent<IRNViewsManager>()->DisplayRNView(viewProps);
// Note that in the above example snippet the viewName property for ReactNativeViewProps should match what
// is passed into the AppRegistry in this file.

function registerApplicationWithCommonLoader(appKey: string): void {
  const view = (): JSX.Element => {
    return <CommonLoader appKey={appKey} />;
  };

  AppRegistry.registerComponent(appKey, () => view);
}

registerApplicationWithCommonLoader('hiddenRoot');
registerApplicationWithCommonLoader('kfmMove');
registerApplicationWithCommonLoader('settings');
registerApplicationWithCommonLoader('debug');
registerApplicationWithCommonLoader('reportABug');
