import React from 'react';
import { SyncView } from '../views/SyncView';
import { AccountView } from '../views/AccountView';
import { NotificationsView } from '../views/NotificationsView';
import { AboutView } from '../views/AboutView';
import { TabContext } from './TabContextManagement';
import { SettingsDebugView } from '../views/SettingsDebugView';

// Tab label names
export enum TabLabel {
  Sync = 'Sync',
  Account = 'Account',
  Notifications = 'Notifications',
  About = 'About',
  Debug = 'Debug'
}

/**
 * RightPange display
 * renders view corresponding to current Context
 */
export const RightPane = (): JSX.Element => {
  // fetch updated Context
  const { tab } = React.useContext(TabContext);

  // render appropriate Page view according to current Context
  switch (tab) {
    case TabLabel.Sync:
      return <SyncView />;
    case TabLabel.Account:
      return <AccountView />;
    case TabLabel.Notifications:
      return <NotificationsView />;
    case TabLabel.About:
      return <AboutView />;
    case TabLabel.Debug:
      return <SettingsDebugView />;
    default:
      return <SyncView />;
  }
};
