import React from 'react';
import { CommonContext } from '../common/loader/models/CommonContext';
/**
 * React Native for Windows has a bug in multi-window scenarios where closing the first window that was opened before
 * any other open windows results in the other windows freezing, becoming unresponsive. Afterwards, any future React
 * Native views that are opened will render a completely blank-white screen until the process is completely restarted.
 * Something in the RNW global application expects the first view displayed to also be the last view that is destroyed.
 * To resolve this expectation, we will render this HiddenRootView component before any other views are displayed,
 * and destroy it only after all other views have also been destroyed.
 *
 * We will still use the CommonContext in this component, to ensure that it can handle the unmount message properly.
 *
 * @returns null, since it is not meant to actually render anything.
 */
export const HiddenRootView: () => null = () => {
  const commonContext = React.useContext(CommonContext);
  // No data fetching needed on initialization so immediately signal that we are ready.
  React.useEffect(() => {
    commonContext.onChildDataLoaded(false);
  }, []);
  return null;
};
