import React from 'react';

/**
 * Declare type for appContextData properties
 */

export type KFMFolder = IFolder | null;
export type KFMFolders = IKFMFolders | null;
export type UpdatedDialogState = IKFMUpdatedDialogState | null;
export type DialogState = IKFMDialogState | null;
export type SpaceUsedInfo = ISpaceUsedInfo | null;
export type DialogInfoState = IDialogInfoState | null;
export type UserInputDialog = IUserInputDialog | null;
export interface IAppContextData {
  folders: KFMFolders;
  dialogState: DialogState;
  failedToSendOnReady: boolean;
  failedFetch: boolean;
  canSync: boolean;
  canTryAgain: boolean;
  spaceUsedInfo: SpaceUsedInfo;
  dialogInfoState: DialogInfoState;
  userInputDialogState: UserInputDialog;
  onScanInitialized: (dialogState: IKFMDialogState) => void;
  onStateUpdate: (updatedDialogState: IKFMUpdatedDialogState) => void;
  onSetSpaceUsedInfo: (spaceInfo: ISpaceUsedInfo) => void;
  onToggleFolder: (folderType: FolderType) => void;
  onStartMove: () => void;
  onSkipBackup: () => void;
  onTryAgain: () => void;
  sendReady: () => void;
  onStopFolderBackup: (folderType: FolderType) => void;
  confirmDialogClosed: (result: ConfirmDialogResult) => void;
  onDialogStateInfoReceived: (dialogInfoState: IDialogInfoState) => void;
  onUserInputDialogStateReceived: (userInputDialogState: IUserInputDialog) => void;
}

const defaultContextCallback = (): void => {};

export const defaultAppContextData: IAppContextData = {
  folders: {},
  dialogState: null,
  failedToSendOnReady: false,
  spaceUsedInfo: null,
  failedFetch: false,
  canSync: false,
  canTryAgain: false,
  dialogInfoState: null,
  userInputDialogState: null,
  sendReady: defaultContextCallback,
  onScanInitialized: defaultContextCallback,
  onStateUpdate: defaultContextCallback,
  onSetSpaceUsedInfo: defaultContextCallback,
  onSkipBackup: defaultContextCallback,
  onTryAgain: defaultContextCallback,
  onStartMove: defaultContextCallback,
  onToggleFolder: defaultContextCallback,
  onStopFolderBackup: defaultContextCallback,
  confirmDialogClosed: defaultContextCallback,
  onDialogStateInfoReceived: defaultContextCallback,
  onUserInputDialogStateReceived: defaultContextCallback
};

export const AppContext: React.Context<IAppContextData> =
  React.createContext<IAppContextData>(defaultAppContextData);

export interface IKFMFolders {
  [key: string]: IFolder;
}

export interface IKFMDialogState {
  isKFMFirstPageRun: boolean;
  isOpenedThroughSettings: boolean;
  isKFMSizeIneligible: boolean;
  kfmCurrentDialogState: number;
  kfmPageTitle: string;
  kfmPageSubtext: string;
  learnMore: string;
  learnMoreLink: string;
  quotaLinkText: string;
  quotaLink: string;
  folders: IFolder[];
  spaceUsedInfo: ISpaceUsedInfo;
}

export interface IKFMUpdatedDialogState {
  /**
   * dialog state
   */
  kfmCurrentState: number;

  /**
   * Contains a list of all  kfm folders and their sizes
   */
  folders: IFolder[];
}

export interface IFolder {
  isStopBackup: boolean;
  folderMigrationError: number;
  folderSize: string;
  folderState: number;
  folderName: string;
  folderType: FolderType;
}

export interface ISpaceUsedInfo {
  remainingSpaceText: string;
  isSpaceBlocking: boolean;
}

/**
 * Dialog info state monitors if any errors occur
 * If isSevereInfo is true, then an error has occured
 * and we show the error message contained in infoAreaText
 */
export interface IDialogInfoState {
  isSevereInfo: boolean;
  infoAreaText: string;
  linkOne: string;
  linkOneText: string;
  linkTwo: string;
  linkTwoText: string;
}

/** User Input Dialog Strings and Button Text
 *
 * UserInputDialog not to be confused with the overall KFM dialog is
 * a second dialog (an Alert) shown to the user to enable them
 * to make a skip or continue decision.
 */
export interface IUserInputDialog {
  userDialogAccessiblePrimaryText: string;
  userDialogPrimaryText: string;
  userInputDialogButtonOneText: string;
  userInputDialogButtonTwoText: string;
  userInputDialogLinkButtonText: string;
  userInputDialogSecondaryText: string;
}

/**
 * Enum matching the QuotaStateUX C++ enum found in QuotaInformation.h
 */
export enum FolderState {
  Unknown = 0,
  Scanning = 1,
  Unselected = 2,
  Selected = 3,
  InProgress = 4,
  Done = 5,
  DoneWithStop = 6,
  Error = 7,
  Blocked = 8
}

export enum DialogStateEnum {
  Unknown = 0,
  Scanning = 1,
  UserInput = 2,
  UserInputDialog = 3,
  Migrating = 4,
  Finished = 5,
  Blocked = 6
}

export enum FolderType {
  NotSet = 0,
  Documents = 1,
  Pictures = 2,
  Desktop = 3,
  Screenshots = 4,
  CameraRoll = 5,
  Music = 6,
  Playlists = 7,
  Videos = 8,
  Downloads = 9
}

export enum ConfirmDialogResult {
  PrimaryButton = 1,
  SecondaryButton = 2
}

export enum Variants {
  Primary = 'primary',
  Default = 'default'
}

/**
 * Enum to decide which button to style
 * as the primary button on a confirmation dialog
 */
export enum DialogButtons {
  First = 'first',
  Second = 'second'
}
