import React from 'react';
import { Text, View, ViewStyle, Image, ImageStyle, StyleProp, ScrollView } from 'react-native';
import { useGlobalStyles } from '../themes/globalStyles';
import { IKFMTheme, themeManager } from '../themes/IKFMTheme';
import { ThemedStylesHook, createThemedStylesHook } from '../../common/themes/themes';
import { AppContext, DialogStateEnum, Variants } from '../models/AppContext';
import { CommonButton } from '../../common/components/CommonButton';
import { DialogErrorArea } from './DialogError';
import { ThemeName } from '../../common/themes/themeUtils';
import { CommonContext } from '../../common/loader/models/CommonContext';
import { StringIDWin } from '../../common/resources/resources';
import { Link } from '../../common/components/Link';
import { Linking } from 'react-native-windows';

// line height for the InfoAreaText, used for custom lineHeight
// will be used for custom lineHeight along with textScale ratio
const LINEHEIGHT = 20;

interface IStorageProps {
  quotaString: string;
  infoString: string;
  scanningString?: string; //shown when dialog is in scanning state
  isLoading: boolean;
  isBlocking: boolean;
  upsellString: string;
  handleUpsellButton: () => void;
}

interface IQuotaDetailsViewStyles {
  container: ViewStyle;
  infoContainer: ViewStyle;
  quotaContainer: ViewStyle;
  usedStorageContainer: ViewStyle;
  criticalQuota: ViewStyle;
  quota: ViewStyle;
  icon: StyleProp<ImageStyle>;
  textContainer: ViewStyle;
}

// Styles for quota section using theme colors from createThemedStylesHook
const useQuotaDetailsViewStyles: ThemedStylesHook<IQuotaDetailsViewStyles> = createThemedStylesHook<
  IKFMTheme,
  IQuotaDetailsViewStyles
>(themeManager, (theme: IKFMTheme) => {
  return {
    container: {
      display: 'flex',
      backgroundColor: theme.innerFrameBackgroundColor,
      flex: 1,
      flexDirection: 'row',
      justifyContent: 'center',
      alignItems: 'center',
      borderTopWidth: 1,
      paddingBottom: 1,
      paddingRight: 24,
      paddingLeft: 32,
      borderColor: theme.storageBorderColor
    },
    infoContainer: {
      minWidth: 133,
      maxWidth: 200,
      flexDirection: 'row',
      justifyContent: 'center',
      alignItems: 'center'
    },
    quotaContainer: {
      display: 'flex',
      minHeight: '60%',
      maxHeight: '88%',
      paddingVertical: 10,
      paddingRight: 10,
      flexDirection: 'row',
      justifyContent: 'space-between',
      borderRadius: 3,
      width: '100%',
      borderWidth: 1,
      borderColor: theme.themeName === ThemeName.HighContrast ? theme.borderColor : 'transparent'
    },
    usedStorageContainer: {
      flexDirection: 'row',
      flex: 1
    },
    criticalQuota: {
      backgroundColor: theme.criticalQuotaBackgroundColor
    },
    quota: {
      backgroundColor: theme.storageBackgroundColor
    },
    icon: {
      marginHorizontal: 10,
      height: 16,
      width: 16
    },
    textContainer: {
      maxWidth: 480
    }
  };
});

const QuotaUsage = (props: IStorageProps): JSX.Element => {
  const styles = useQuotaDetailsViewStyles();
  const globalStyles = useGlobalStyles();
  const { upsellString, scanningString, isBlocking, isLoading, infoString, quotaString, handleUpsellButton } =
    props;
  const quotaBackground = isBlocking ? styles.criticalQuota : styles.quota;
  const commonContext = React.useContext(CommonContext);
  const isBusiness = commonContext.metadata?.account.isODB;
  const newLineHeight = LINEHEIGHT * commonContext.textScale;
  return (
    <View testID='quotaUsage' style={styles.container}>
      <View style={[styles.quotaContainer, quotaBackground]}>
        <ScrollView testID='quotaUsageScrollView'>
          <View style={styles.usedStorageContainer}>
            <Image
              style={[styles.icon as ImageStyle, { marginTop: newLineHeight / 2 - 7 }]}
              source={
                isBlocking ? require('../assets/images/error.png') : require('../assets/images/info.png')
              }
            />
            <View style={styles.textContainer}>
              {isLoading ? (
                <Text style={[globalStyles.boldBodyText, { lineHeight: newLineHeight }]}>
                  {scanningString}
                </Text>
              ) : (
                <Text style={[globalStyles.boldBodyText, { lineHeight: newLineHeight }]}>{quotaString}</Text>
              )}
              {isBusiness ? (
                <></>
              ) : (
                <Text style={[globalStyles.bodyText, { lineHeight: newLineHeight }]}>{infoString}</Text>
              )}
            </View>
          </View>
        </ScrollView>

        {upsellString !== '' ? (
          <View style={styles.infoContainer}>
            <CommonButton
              variant={isBlocking ? Variants.Primary : Variants.Default}
              buttonName={upsellString || ''}
              onPress={handleUpsellButton}
              testID={'upsellButton'}
            />
          </View>
        ) : null}
      </View>
    </View>
  );
};

export const QuotaDetails = (): JSX.Element => {
  const appContext = React.useContext(AppContext);
  const { dialogInfoState, spaceUsedInfo } = appContext;
  const commonContext = React.useContext(CommonContext);
  const { useString, onAction } = commonContext;

  if (!spaceUsedInfo) return <></>;

  const isLoading = appContext.dialogState?.kfmCurrentDialogState === DialogStateEnum.Scanning;

  // Priority order for the error area vs the quota info area:
  // 1. Always show errors if there is any
  // 2. Show the quota info area if the user will go over quota
  // 3. Show info and warnings
  if (
    dialogInfoState?.infoAreaText &&
    dialogInfoState.infoAreaText.length !== 0 &&
    (dialogInfoState?.isSevereInfo || !spaceUsedInfo.isSpaceBlocking)
  ) {
    const links = [];
    const { linkOne, linkOneText, linkTwo, linkTwoText } = dialogInfoState;
    if (dialogInfoState.linkOne) {
      links.push(
        <Link
          testID='link1'
          key={'link1'}
          accessibilityLabel={linkOneText}
          accessibilityRole='link'
          inlineLink
          onPress={() => onAction(linkOne)}
          text={linkOneText}
          customStyles={[{ paddingRight: 15 }]}
        />
      );
    }

    if (dialogInfoState.linkTwo) {
      const linkStyle: ViewStyle = {};
      linkStyle.marginRight = linkOne ? 10 : 0;
      links.push(
        <Link
          testID='link2'
          key={'link2'}
          customStyles={[linkStyle]}
          accessibilityHint={linkTwoText}
          accessibilityLabel={
            dialogInfoState.isSevereInfo
              ? useString(StringIDWin.IDS_KFM_WINDOW_LEARN_MORE_ABOUT_ISSUE)
              : useString(StringIDWin.IDS_KFM_WINDOW_LEARN_MORE_ABOUT_MESSAGE)
          }
          accessibilityRole='link'
          inlineLink
          onPress={() => Linking.openURL(linkTwo)}
          text={linkTwoText}
        />
      );
    }

    return (
      <DialogErrorArea
        isCritical={dialogInfoState.isSevereInfo}
        text={dialogInfoState.infoAreaText}
        links={links}
      />
    );
  }

  return (
    <QuotaUsage
      upsellString={appContext.dialogState?.quotaLinkText || ''}
      handleUpsellButton={() => {
        if (appContext.dialogState?.quotaLink) {
          onAction(appContext.dialogState.quotaLink);
        }
      }}
      infoString={
        spaceUsedInfo.isSpaceBlocking
          ? useString(StringIDWin.IDS_KFM_WINDOW_EXCEEDED_QUOTA_LIMIT_TEXT)
          : useString(StringIDWin.IDS_KFM_WINDOW_WITHIN_QUOTA_LIMIT_TEXT)
      }
      quotaString={spaceUsedInfo.remainingSpaceText}
      isBlocking={spaceUsedInfo.isSpaceBlocking}
      scanningString={useString(StringIDWin.IDS_KFM_WINDOW_SCANNING_TEXT)}
      isLoading={isLoading}
    />
  );
};

export const exportedForTesting = {
  QuotaUsage
};
