import React from 'react';
import { Text, ViewStyle, View, Image, ImageStyle, ImageSourcePropType } from 'react-native';
import { ITheme, themeManager } from '../themes/ITheme';
import { createThemedStylesHook, ThemedStylesHook } from '../../common/themes/themes';
import { useSettingsStyles } from '../themes/settingsStyles';
import { CommonPressable } from '../../common/components/CommonPressable';
import { IKeyboardEvent } from 'react-native-windows';

/**
 * Defines props of Tab component.
 */
export interface ITabProps {
  /**
   * Whether this tab is currently selected
   */
  selected: boolean;
  /**
   * Handler for when this tab is clicked or activated by keyboard
   */
  onPress(): void;
  /**
   * Visual text label for this tab
   */
  label: string;
  /**
   * Icon to use for this tab
   */
  icon: ImageSourcePropType;
  /**
   * Accessibility hint for this tab
   */
  accessibilityHint: string;
  /**
   * Whether this tab is disabled
   */
  disabled: boolean;
  /**
   * Ref to use for this component (Optional)
   */
  innerRef?: React.RefObject<View>;
  /**
   * Custom handler for key presses on this component (Optional)
   */
  onKeyDown?: (event: IKeyboardEvent) => void;
}

// Tab styling
interface ITabStyles {
  tabBase: ViewStyle;
  normalTab: ViewStyle;
  selectedTab: ViewStyle;
  pressedTab: ViewStyle;
  selectedAccent: ViewStyle;
  iconStyleBase: ImageStyle;
  iconStyleNormal: ImageStyle;
  iconStyleSelected: ImageStyle;
  iconStyleDisabled: ImageStyle;
  iconStylePressed: ImageStyle;
  iconStyleHovered: ImageStyle;
  tabView: ViewStyle;
}

// Tab styling specifications
const useTabStyles: ThemedStylesHook<ITabStyles> = createThemedStylesHook<ITheme, ITabStyles>(
  themeManager,
  (theme: ITheme) => {
    return {
      tabBase: {
        minHeight: 34,
        margin: 3
      },
      normalTab: {
        backgroundColor: theme.innerFrameBackgroundColor
      },
      selectedTab: {
        backgroundColor: theme.selectedTabColor,
        borderRadius: 3
      },
      pressedTab: {
        backgroundColor: theme.pressedTabColor,
        borderRadius: 3
      },
      selectedAccent: {
        backgroundColor: theme.link.linkTextColor,
        width: 3,
        height: 16,
        borderRadius: 1,
        marginRight: 9,
        marginLeft: -11
      },
      iconStyleBase: {
        width: 16,
        height: 16
      },
      iconStyleNormal: {
        tintColor: theme.tabIconColor
      },
      iconStyleSelected: {
        marginLeft: -1,
        tintColor: theme.tabIconSelectedColor
      },
      iconStyleHovered: {
        tintColor: theme.tabIconSelectedColor
      },
      iconStyleDisabled: {
        tintColor: theme.button.buttonTextDisabled
      },
      iconStylePressed: {
        tintColor: theme.pressedTabText
      },
      tabView: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'flex-start',
        flexWrap: 'wrap',
        paddingVertical: 7,
        paddingLeft: 11
      }
    };
  }
);

// Tab display
export const Tab = (props: ITabProps): JSX.Element => {
  const { onPress, label, selected, icon, accessibilityHint, disabled, innerRef, onKeyDown } = props;
  const styles = useTabStyles();
  const settingsStyles = useSettingsStyles();

  return (
    <CommonPressable
      onPress={disabled ? () => {} : onPress}
      accessibilityRole='tab'
      accessibilityLabel={label}
      accessibilityHint={accessibilityHint}
      accessibilityState={{ selected: selected }}
      innerRef={innerRef}
      onKeyDown={onKeyDown}
    >
      {(isPressed, isHovering) => {
        let tabTextStyle = settingsStyles.tabTitleNormal;
        let iconStyle = styles.iconStyleNormal;
        let tabStyle = styles.normalTab;

        if (disabled) {
          tabTextStyle = settingsStyles.disabledTabTitle;
          iconStyle = styles.iconStyleDisabled;
        } else if (selected) {
          tabTextStyle = settingsStyles.selectedTabTitle;
          iconStyle = styles.iconStyleSelected;
          tabStyle = styles.selectedTab;
        } else if (isPressed) {
          tabTextStyle = settingsStyles.pressedTabTitle;
          iconStyle = styles.iconStylePressed;
          tabStyle = styles.pressedTab;
        } else if (isHovering) {
          tabTextStyle = settingsStyles.selectedTabTitle;
          iconStyle = styles.iconStyleHovered;
          tabStyle = styles.selectedTab;
        }

        return (
          <View style={[styles.tabBase, tabStyle]}>
            <View style={styles.tabView}>
              {selected ? <View style={styles.selectedAccent} /> : null}
              <Image style={[styles.iconStyleBase as ImageStyle, iconStyle as ImageStyle]} source={icon} />
              <Text style={[settingsStyles.tabTitleBase, tabTextStyle]}>{label}</Text>
            </View>
          </View>
        );
      }}
    </CommonPressable>
  );
};
